import React,{useRef,useCallback, useEffect, useState, useMemo} from 'react'
import { connect } from 'react-redux';
import { BrowserRouter as Routes, useParams,useNavigate } from 'react-router-dom'
import { opleverrapportOverzicht } from '../../actions/opleverrapport'
import { get_projectById } from '../../actions/project';

// MUI
import AddIcon from '@mui/icons-material/Add';
import Button from '@mui/material/node/Button';
import CreateIcon from '@mui/icons-material/Create';
import MaterialReactTable from 'material-react-table';
import { Box, Modal } from '@mui/material';
import { Link } from "react-router-dom";
// CUSTOMS COMPONENTS


const Opleverrapportoverzicht = (props) => {
  let projectId = useParams()
  const projectID = projectId.id
  const titleName = projectID.name
  const [projectnummer,setProjectnummer] = useState(null)
  const [projectnaam,setProjectnaam] = useState('')
  const [projectstatus,setProjectstatus] = useState(null)
  const [data,setData]= useState([])
  const [noData,setNodata] = useState(false)
  const [loadData,setloadData] = useState(false)
  
  useEffect(() => {
    props.get_projectById(projectID)
    props.opleverrapportOverzicht({'projectID':projectID})
    if(props.context !== undefined)
    {
        setProjectnummer(props.context.projectnummer)
        setProjectnaam(props.context.projectnaam)
        setProjectstatus(props.context.projectstatus)
    }
    
    // props.opleverrapportOverzicht({'projectID': projectID})
  },[props.context.projectnaam,projectID])

  useEffect(() => {
    if(props.opleverrapportData !== undefined && props.opleverrapportData.lengh !== 0)
        {
            // data.current = props.opleverrapportData
            setData(props.opleverrapportData)
            setloadData(true)
            
        }
        setloadData(false)
  },[props.opleverrapportData,data,titleName])

  useEffect(() => {
    if(projectnaam !== ''){
        setNodata(false)
    }else{
        setNodata(true)
    }

},[projectnaam])
  const handleExportData = (rows) => {
    alert('genereer pdf ? excel ? ')
}

  let navigate = useNavigate(); 
  const edit = useCallback((row) => {
    let path = `/opleverrapport/${projectID}/${row.original.bouwnr}`; 
    navigate(path);
    },[projectId,navigate])

  const handlethis = (e) => {
    console.log(e)
  } 
  const columns = useMemo(

    () => [
        // column definitions ....
        { accessorKey: 'isGeprint',
            id:"selectprintbutton",
            header:
              <div className='form-check'>
                <input className='form-check-input' type="checkbox" onChange={(e) => handlethis(e)} id="cardtableCheck"/>
                {/* <label className='form-check-label' htmlFor="cardtableCheck" >Print</label> */}
              </div>,
            size: 100,
            Cell: ({ row }) => (
                  <div className="form-check">
                    <input className="form-check-input" type="checkbox" onChange={()=>handlethis(row)} id="cardtableCheck01"/>
                    <label className='form-check-label' htmlFor="cardtableCheck01" />
                  </div>
              ),

        },
        {
            accessorKey: 'bouwnr',
            header: 'Bwnr'
        },
        {
            accessorKey: 'blok',
            header: 'Blok'
        },
        {
            accessorKey: 'straat',
            header: 'Straat'
        },
        {
            accessorKey: 'postcode',
            header: 'Postcode'
        },
        {
            accessorKey: 'icem',
            header: 'iCEM'
        },
        {
            accessorKey: 'opleverrapportaangemaakt',
            header: 'Aangemaakt'
        },
        {
            accessorKey: 'datumrapportrapportAangemaakt',
            header: 'Datum aangemaakt'
        },
        {
            accessorKey: 'definitief',
            header: 'Definitief'
        },
        {
            id:'action',
            header:'Actie',
            Cell: ({ row }) => {
            
            return [
                <Button
                key={row.original.bouwnr}
                onClick={() => {
                    // do something
                    edit(row)
                }}
                >
                {row.original.opleverrapportaangemaakt === 'Nee' ? <AddIcon /> : <CreateIcon />} 
                </Button>,
                
            ]
            }
        },

        
    ],
    []
    )

  const displayheader = () => {
    return(
        <>
          <div className="card card-animate ">
            <div className='row m-3'>
                <div class="col-lg-3">
                    <label class="form-label">Projectnummer</label>
                </div>
                <div class="col-lg-9">
                    <p type="text" class="form-control form-control-sm">{projectnummer} </p>
                </div>
            </div>
            <div className='row m-3'>
                <div class="col-lg-3">
                    <label class="form-label">Projectnaam</label>
                </div>
                <div class="col-lg-9">
                    <p type="text" class="form-control form-control-sm">{projectnaam} </p>
                </div>
            </div>
            <div className='row m-3'>
                <div class="col-lg-3">
                    <label class="form-label">Projectstatus</label>
                </div>
                <div class="col-lg-9">
                    <p type="text" class="form-control form-control-sm">{projectstatus} </p>
                </div>
            </div>
          </div>
        </>
    )
  }

  return (
    <div className='mpo'>
       {noData !== true ? 
       <>
         <h5 className="mb-3">Algemene projectgegevens</h5>
        <div className='row'>
            {displayheader()}
        </div>

        <MaterialReactTable
            style={{ border: ' 0 2px' }}
            columns={columns}
            data={data}
            enableColumnResizing
            
            muiTableProps = {{
                className: 'table table-hover table-sm table-nowrap mb-0',
                sx: {
                  tableLayout: 'fixed',
                  // borderCollapse: '',

                }
              }}
            muiTableBodyProps={{
                sx: {
                  //stripe the rows, make odd rows a darker color
                  // '& tr:nth-of-type(odd)': {
                  //   backgroundColor: '#f5f5f5',
                  // },
                },
                
              }}
              muiTableBodyCellProps={{
                sx: {
                  // borderRight: '2px solid #e0e0e0', //add a border between columns
                },
              }}
            renderTopToolbarCustomActions={({ table }) => (
                <button
                    color="primary"
                    className="btn btn-success btn-icon waves-effect waves-light"
                    //export all data that is currently in the table (ignore pagination, sorting, filtering, etc.)
                    onClick={() => handleExportData(table.getSelectedRowModel().rows)}
                >
                <i class=" ri-file-excel-2-line"></i>
              </button>
             
            )} 
        />
       </>
        :
        <>
          <p>Er is nog geen project aangemaakt</p>
          Click op <Link to="/projectaanmaken" relative="path">projectnaamaken</Link> 
        </>
        }
    </div>
  )
}
const getProjectItems = state => ({
    context: state.project.dataProjectID,
    opleverrapportData: state.opleverrapport.data
})
export default connect(getProjectItems,{opleverrapportOverzicht,get_projectById})(Opleverrapportoverzicht)
    