import React,{useEffect, useMemo, useRef, useState} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';


const IcemTable = (props) => {

    const [data,setData] = useState(false)
    const [isLoading, setIsLoading] = useState(true);
    const validationsMessages = "colorvalidations"
  
  useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)
  },[props.props.data])
  
  function returnwtwdenieten(x){
      let y
      // console.log(x)
      if(x !== '' && x !== undefined){
          y = x.slice(0,3)
         
      }else{
          y = 0
      }
   
      return y
  }
  
  function returnVermogenWP(x){
      let y
      if(x !== null && x !== undefined){
          y = x.substr(x.length - 1)
  
      }else{
          y=0
      }
      return y
  }
 

    function returnRangenumber(arr) {
      let newArry = [];
      for(let i = arr[0]; i < arr[1]; i++ ) {
          newArry.push(i);
      }
      return newArry;
  }
  const dakhelingOpties = returnRangenumber([30,59])

  var array_icem_type = ['a','i','p','e','f']
  const [icemFout,setIcemFout] = useState(false)

  const updateData = index => e => {

    let newArray = [...props.props.data];
    newArray[index][e.target.name] = e.target.value 

    if( [index][e.target.name] === 'icemType')
    {
      if(array_icem_type.includes(e.target.value))
      {
        setIcemFout(false)
      }else{
        setIcemFout(true)
      }
    }
    
    props.props.setData(newArray)
    
  }
 

  const handleEnergieModule = (e,index) => {
   
    let newArray = [...props.props.data];
    if(e !=='')
    {
      var wtwdebiet_ = returnwtwdenieten(e)
      var vermogenWp_ = returnVermogenWP(e)
      
      newArray[index]['wtwDebiet'] = wtwdebiet_
      newArray[index]['vermogenWp'] = vermogenWp_
      
    }
    newArray[index]['energieModule'] = e
    props.props.setData(newArray)

  }

  const links_right = ['Links','Rechts']
  const aansluitingkanalen_optie = ['Kopsekant','Achterkant','Bovenaansluiting']
  const boiler_optie = ['150','200']
  const merkwtw_optie = ['Renovent','Iets anders']
  const typeWtw_optie = ['2/2 Links','4/0 Links']
  const kwh_meter_optie = ['Nee','Ja']
  const semkast_optie = ['F0020','F0021']
  const sensoringOptie_optie = ['Tondgy','Tongdy display badkamer en slaapkamer','Display en badkamer','Display en badkamer en slaapkamer','Display en badkamer en 2 slaapkamer']
  const typePrestatie_optie = ['Epg','Epv','Standaard']
  const positieWpModule_optie = ['Gedraaid','Haaks']

  const columns = useMemo(
   () => [
      {
        accessorKey: 'bouwnr',
        header: "Bouwnr",
      },
      {
        accessorKey: 'blok',
        header: "Blok",
      },
      {
        accessorKey: 'energieModule',
        header: "Energie module",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ? 
        <input type='text'  maxlength="4" style={{textTransform:"uppercase"}} name="energieModule" onBlur={(e) => handleEnergieModule(e.target.value,row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
        :
        cell.getValue() ? cell.getValue():'' 
      },
      {
        accessorKey: 'icemType',
        header: "Type",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
          <input type='text'  maxlength="1"  name="icemType" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
          // icemFout === true ? <span className='validationsMessages'>type juiste type</span>:null
          :
          cell.getValue() ? cell.getValue():'' 
      },
      {
        accessorKey: 'dakhelling',
        header: "Dakhelling",
        Cell: ({ cell,row }) => 
            props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            // disabled={props.props.bewerken_actif === true ? false:true}
            name="dakhelling"
             >
              <option> - Kies optie - </option>
              {dakhelingOpties.map(item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
      },
      {
        accessorKey: 'links_rechts',
        header: "Links/Rechts",
        Cell: ({ cell,row }) => 

            props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            name="links_rechts"
            >
              <option> - Kies optie - </option>
              {links_right.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
         
       
      },
      {
        accessorKey: 'aansluitingkanalen',
        header: "Aansluitingkanalen",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
          <select
          onBlur={updateData(row.index)}
          defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
          className='form-select form-select-sm'
          name="aansluitingkanalen"
          >
            <option> - Kies optie - </option>
            {aansluitingkanalen_optie.map( item => <option value={item}>{item}</option>)}
          </select>:
          cell.getValue() !== null ? cell.getValue(): ''
      },
      {
        accessorKey: 'boilerInhoud',
        header: "Boiler (L)",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            disabled={props.props.bewerken_actif === true ? false:true}
            name="boilerInhoud"
            >
              <option> - Kies optie - </option>
              {boiler_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
         
      },
      {
        accessorKey: 'merkWtw',
        header: "MerkWtw",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
          <select
          onBlur={updateData(row.index)}
          defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
          className='form-select form-select-sm'
          disabled={props.props.bewerken_actif === true ? false:true}
          name="merkWtw"
          >
            <option> - Kies optie - </option>
            {merkwtw_optie.map( item => <option value={item}>{item}</option>)}
          </select>:
          cell.getValue() !== null ? cell.getValue(): ''
      },
      {
        accessorKey: 'typeWtw',
        header: "TypekWtw",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
          <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            disabled={props.props.bewerken_actif === true ? false:true}
            name="typeWtw"
            >
              <option> - Kies optie - </option>
              {typeWtw_optie.map( item => <option key={item} value={item}>{item}</option>)}
          </select>:
            cell.getValue() !== null ? cell.getValue(): ''
      },
      {
        accessorKey: 'wtwDebiet',
        header: "WTWDebieten",
        Cell: ({ cell}) => 
        cell.getValue() !== null ? cell.getValue(): '' 
      },
      {
        accessorKey: 'vermogenWp',
        header: "VermogenWP",
        Cell: ({ cell }) =>
        cell.getValue() !== null ? cell.getValue() + 'KW': '' 
                
      },
      {
        accessorKey: 'kwh_meter',
        header: "KwH-meter",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            name="kwh_meter"
            >
              <option> - Kies optie - </option>
              {kwh_meter_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
  
      },
      {
        accessorKey: 'semkast',
        header: "Semkast",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            disabled={props.props.bewerken_actif === true ? false:true}
            name="semkast"
            >
              <option> - Kies optie - </option>
              {semkast_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
      },
      {
        accessorKey: 'sensoringOptie',
        header: "Sensoringoptie",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            // disabled={props.props.bewerken_actif === true ? false:true}
            name="sensoringOptie"
            >
              <option> - Kies optie - </option>
              {sensoringOptie_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''

      },
      {
        accessorKey: 'typePrestatie',
        header: "Typeprestatie",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            disabled={props.props.bewerken_actif === true ? false:true}
            name="typePrestatie"
            >
              <option> - Kies optie - </option>
              {typePrestatie_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): ''
        
      },
      {
        accessorKey: 'koeling',
        header: "Koeling",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            // disabled={props.props.bewerken_actif === true ? false:true}
            name="koeling"
            >
              <option> - Kies optie - </option>
              {kwh_meter_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): '' 
      },
      {
        accessorKey: 'positieWpModule',
        header: "Positie WP P/A-module",
        Cell: ({ cell,row }) => 
        props.props.bewerken_actif === true ?
            <select
            onBlur={updateData(row.index)}
            defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
            className='form-select form-select-sm'
            // disabled={props.props.bewerken_actif === true ? false:true}
            name="positieWpModule"
            >
              <option> - Kies optie - </option>
              {positieWpModule_optie.map( item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue(): '' 
      },
    ],[data,props.props.bewerken_actif,updateData]
  )

  return (
      isLoading ? <LinearProgress /> :
      <MaterialReactTable
        
        columns={columns}
        data={data ? data: []}
        enableColumnResizing
        enablePinning
        initialState={{ 
          density: 'compact' ,
          columnPinning: { left: ['bouwnr'] },
      }}
      enableRowVirtualization
      muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
       />
  )
}

export default IcemTable
