import React, { useEffect, useState,useMemo,useRef  } from 'react';
import {getFuncties }from '../../actions/admin'
import { connect } from 'react-redux'
import {Box, Typography} from '@mui/material'
import {DataGrid } from '@mui/x-data-grid'
import { grey } from '@mui/material/colors'
import UserActions from '../User/UserActions';

const Functies = ({getFuncties,functies}) => {
 
    const [pageSize,setpageSize] = useState(5)
    const [rowId, setRowId] = useState(null)
    const [loadthis, setloadThis] = useState(null)
    
    let functieresults = []
      if(functies !== []){
        if(functies !== undefined && functies !== null){
          functieresults = functies
          }
    }
    useEffect(() => {
      getFuncties() 
      if(loadthis === false){
        getFuncties() 
        } 
        return () => { setloadThis(true)}
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const columns = useMemo(() => [
    {field:'functie_name', 
     headerName: 'Functies',
     width:200,
     sortable: true,
     filterable: true,
     pinnable: true
    },
    {field:'medewekerCount', headerName: 'Aantal', width:400,},
    {field:'actions',headerName:'Actions',width:400, type:'actions', renderCell:params=> <UserActions  {...{params,rowId, setRowId}}/>}
],[rowId])
  return (
    <div>
        <Box 
    sx={{
    height: 400,
    width: '100%'
    }}>
    <Typography
    variant='h5'
    component='h5'
    sx={{textAlign:'left', mt:3, mb:3}}
    >
        Funcite Table
    </Typography>
    <div  style={{ position:'absolute', display: 'flex', textAlign:'center', height: 350, width: '80%' }}>
        <DataGrid
        columns={columns}
        rows={functieresults}
        getRowId={row=>row.id}
        rowsPerPageOptions={[5,10,20]}
        pageSize={pageSize}
        onPageSizeChange={(newPageSize) => setpageSize(newPageSize)}
        getRowSpacing={params=>({
            top:params.isFirstVisible ? 0:5,
            bottom:params.isFirstVisible ?0:5
        })}
        sx={{
            [`& .{gridClasses.row}`]: {
                bgcolor: (theme) =>
                theme.palette.mode === 'light' ? grey[200] : grey[900]
            }
        }}
        onCellEditCommit={params=> setRowId(params.id)}
        pinnedRows={{ pinnedColumns: { left: ['projectnr'], right: ['actions'] } }}
        />
        </div>
        
        
        </Box>
      
    </div>
  )
}

const getALLrFunctieitems = state => ({
  functies: state.admin.roles,
})
export default connect(getALLrFunctieitems,{getFuncties})(Functies)
