import React,{Fragment,useEffect, useState} from 'react'
import {checkauthenticated} from '../actions/auth'
import { connect } from 'react-redux'
import { load_user } from '../actions/profile'

const Check = ({children,checkauthenticated,is_locked,load_user}) => {
    // const [auth,setAuth] = useState(null)
    const [unlocked,setUnlocked] = useState(null)
    useEffect(() => {
        checkauthenticated()
        load_user()
        // setAuth(true)
        setUnlocked(is_locked)
    },[is_locked])
  return (
    <Fragment>
        {children}
    </Fragment>
     
  )
}
const mapStateToProps = state => ({
    is_locked: state.profile.is_loggedin
  })
export default connect(mapStateToProps,{checkauthenticated,load_user})(Check)
