import React, {useState,useEffect} from 'react'
import { load_all_projects } from '../actions/project';
import { InputText } from 'primereact/inputtext';
import { connect } from 'react-redux';
import { BrowserRouter as Routes,Route,useLocation, Navigate, useParams, NavLink } from 'react-router-dom';
// import '../assets/css/main.css'

const SearchNavbar = ({load_all_projects,context,path}) => {

  const [value3, setValue3] = useState('');
  const [loadthis, setloadThis] = useState(null)
  const [isActive, setIsActive] = useState(false);


  const handleClick = event => {
    let navigation = document.querySelector('.navigation')
    event.currentTarget.classList.toggle('active');
    navigation.classList.toggle('active')
    setIsActive(current => !current);
    
  }
  let projects = []
      if(context !== [] ){
        if(context !== undefined){
            projects = context
          }
    }
  let displayThis = []
  useEffect(() => {
    load_all_projects() 
    if(loadthis === false){
      load_all_projects() 
    } 
    return () => { setloadThis(true)}
  },[])


  function display(projectnr,id,projectnaam){
    return (
        <li key={id}>
          <NavLink className="navlinkStyle" to={path+id}>{projectnr + " "+ projectnaam}</NavLink><br></br>
        </li>
    )
  }
  let displaySomehting_ = ""
      
      if(projects !==undefined){
        for(let i in projects){
          var projectnaam= projects[i].projectnaam
          var id = projects[i].id
          var projectnr = projects[i].projectnr
          displaySomehting_ =  display(projectnr,id,projectnaam)
          displayThis.push(displaySomehting_)
        }

      }

  const onchange = (e) => {
    setValue3(e.target.value)
  }
  const onSearch = (e) => {
    setValue3(e)
  }
  return (
    <div className='navbarclass'>
          <span className="p-input-icon-right">
              <i onClick={()=>onSearch(value3)} className="pi pi-search" />
              <InputText value={value3} onChange={onchange} placeholder="Search" />
          </span>
          <div className='dropdown'>
            {projects.filter(
              item => {
                const searchTerm = value3.toLowerCase()
                const nameProject =  item.projectnaam.toLowerCase()
  
                const nrPoject = item.projectnr

                return ((searchTerm && nameProject.startsWith(searchTerm)) || (searchTerm && nrPoject.startsWith(searchTerm))) && nameProject !== searchTerm
              }).slice(0,10)
            .map((item)=> <div onClick={() => onSearch(item.projectnr + ' ' + item.projectnaam )} className='dropdown-row' key={item.id}><NavLink  to={'/'+ item.id}>{item.projectnr + " " +item.projectnaam}</NavLink></div>)}
          </div>
          <ul className='list'>
            <div className='body-content'>
              {displayThis}
            </div>
          </ul>
          <div className={isActive ? 'toggle': 'toggle'} onClick={handleClick} >
            <i className="pi pi-angle-right open"></i>
            <i className="pi pi-angle-left close"></i>
          </div>
      </div>

  )
}

const mapStateToProps = state => ({
  context: state.project.data
})

export default connect(mapStateToProps, {load_all_projects})(SearchNavbar)
