import React from 'react'
import SearchNavbar from '../../components/SearchNavbar'
// import 'static/assets/css/productiebon.css'

const Testrapport = () => {
  const path = "/testrapport/mpo/"
  return (
    <>
    <div>Testrapport</div>
        <div className='navigation'>
            <SearchNavbar path={path}/>
        </div>
       <div>
        <p>Testrapport pagina. via de breadcumb kies een project</p>
       </div>
    </>
  )
}

export default Testrapport
