import React,{useState,useEffect,useMemo, useRef, useCallback} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import $ from 'jquery'
const Planning = (props) => {
   
    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    const wknummer = useRef(null)
    useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
        
      } 
      setIsLoading(false)
    },[props.props.data])

    function checkDateString(d){
      var c = ''
      if(typeof(d) === Object){
        c = d
      }else{
        var dateObject = new Date(d);
        // var a = dateObject.toString();
        c=dateObject
      }
      return c
    }

    const returnWeeknr =(x,index) => {
        let y

        if(x !== null && x !== undefined && x !== 0){
          var currentDate = checkDateString(x);
          
          var startDate = new Date(currentDate.getFullYear(), 0, 1);
          var days = Math.floor((currentDate - startDate) /
              (24 * 60 * 60 * 1000));
        
          var weekNumber = Math.ceil(days / 7);
          
          y = "wk" + weekNumber
            
        }else{
            y = " - "
        }
        wknummer.current = ({'index':index,'value':y})
        // setWknummer({'index':index,'value':y})
        return y
        
    }

    const updateData = index => e => {
      let newArray = [...data];
      newArray[index][e.target.name] = e.target.value 
      props.props.setData(newArray)
      
    }
 
    const columns = useMemo(
      () => [
        {
          accessorKey: 'bouwnr',
          header: "Bouwnr",
        },
        {
          accessorKey: 'blok',
          header: "Blok",
        },
        {
          accessorKey: 'bouwrouting',
          header: "Bouwrouting",
        },
        {
          accessorKey: 'leverdatum',
          header: "Leverdatum",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
          <input type='date' style={{textTransform:"uppercase"}} name="leverdatum" onChange={returnWeeknr(cell.getValue(),row.index)} onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
          : cell.getValue() ? cell.getValue():''
        },
        {
          accessorKey: 'leverdatum',
          header: "WK",
          Cell: ({ cell,row }) =>
          returnWeeknr(cell.getValue() ? cell.getValue():0,row.index)
          
        },
      ],[props.props.bewerken_actif, updateData,])

    

  useEffect(() => {
    $(function () {
      // $("#table6").enableCellNavigation();
      // $("#table").DataTable();
    });

    })
          
    return (
      isLoading ? <LinearProgress /> :
        <MaterialReactTable
          columns={columns}
          data={data ? data: []}
          enablePinning
          enableColumnResizing
          initialState={{ 
            density: 'compact' ,
            columnPinning: { left: ['bouwnr'] },
        }}
        enableRowVirtualization
        muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
      />

    )
}

export default Planning