import React ,{useEffect, useState} from "react";
import { Navigate, Outlet } from "react-router-dom";
import { checkauthenticated } from "../actions/auth";
import { connect } from 'react-redux'
import { BrowserRouter as Routes,Route,} from 'react-router-dom';
import Login from "../containers/Login";
import Layout from "../hocs/Layout";

const ProtectedRoute = ({isAuthenticated,is_unclocked}) => {
 const [isunclocked,setIsunclocked] = useState(null)
  useEffect(() => {
    setIsunclocked(is_unclocked)
  },[is_unclocked])
  return (
    isAuthenticated ? is_unclocked === false ? <Navigate to="/lockscreen" /> : <Layout><Outlet /></Layout> : <Navigate to="/login" />
  )
}

let auth = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  is_unclocked: state.profile.is_loggedin
})
export default connect(auth,{})(ProtectedRoute);

