import React, { useEffect, useState,useRef, useCallback } from 'react'
import { useParams } from 'react-router-dom'
import { connect } from 'react-redux';
import { get_projectById } from '../../actions/project';
import { mpoAanmaken,mpoGet } from '../../actions/mpo';
import $ from 'jquery'
// Table componenten

import Algemeentable from '../../components/mpo/Algemeentable';
import IcemTable from '../../components/mpo/IcemTable';
import Icemdebieten from '../../components/mpo/Icemdebieten';
import Energiebundel from '../../components/mpo/Energiebundel';
import Omvormer from '../../components/mpo/Omvormer';
import Planning from '../../components/mpo/Planning';
import Bouwkundig from '../../components/mpo/Bouwkundig';
import ProductieExact from '../../components/mpo/ProductieExact';
import Bewoners from '../../components/mpo/Bewoners';
import { LinearProgress } from '@material-ui/core';
import { Link } from "react-router-dom";
// EXCEL LIBRARY
import XLSX from 'sheetjs-style'


const MpoComponent = (props) => {
    let projectParam = useParams()
    const [projectData,setProjectData] = useState([])
    const [rownumber,setRownumber] = useState([])
    const [bewerken_actif,setbewerkenactif] = useState(false)
    

    const [algemenegegevens_,setAlgemenegegevens] = useState({})
    const [icemComponenten,setIcemComponenten] = useState({})
    const [icemDebieten,setIcemDebieten] = useState({})
    const [omvormer,setOmvormer] = useState({})
    const [planning,setPlanning] = useState({})
    const [bouwkundig,setBouwkundig] = useState({})
    const [productie_exact,setProductie_exact] = useState({})
    const [bewoners,setBewoners] = useState({})

    const [typeIcem,setTypeicem] = useState([])

    // Update states
    useEffect(() => {
        props.mpoGet(projectParam.id)  
    },[projectParam.id])

    useEffect(() => {
        if(props.context !== undefined)
        {
            setProjectData(props.context)

        }

    },[props.context])

    useEffect(() => {
        (function ($) {
  
            $.fn.enableCellNavigation = function () {
              var arrow = {
                left: 37,
                up: 38,
                right: 39,
                down: 40,
              };
          
              this.find("select,input").keydown(function (e) {
                  // shortcut for key other than arrow keys
                //   console.log(e)
                  if (
                    $.inArray(e.which, [arrow.left, arrow.up, arrow.right, arrow.down]) < 0
                  ) {
                    return;
                  }
            
                  var input = e.target;
                  var td = $(e.target).closest("td");
                  var moveTo = null;
            
                  switch (e.which) {
                    case arrow.left: {
                      if (input.selectionStart == 0) {
                        moveTo = td.prev("td:has(input,textarea,select)");
                      }
                      break;
                    }
                    case arrow.right: {
                      if (input.selectionEnd == input.value.length) {
                        moveTo = td.next("td:has(input,textarea,select)");
                      }
                      break;
                    }
            
                    case arrow.up:
                    case arrow.down: {
                      var tr = td.closest("tr");
                      var pos = td[0].cellIndex;
            
                      var moveToRow = null;
                      if (e.which == arrow.down) {
                        moveToRow = tr.next("tr");
                      } else if (e.which == arrow.up) {
                        moveToRow = tr.prev("tr");
                      }
            
                      if (moveToRow.length) {
                        moveTo = $(moveToRow[0].cells[pos]);
                      }
            
                      break;
                    }
                  }
            
                  if (moveTo && moveTo.length) {
                    e.preventDefault();
                    moveTo.find("input,select").each(function (i, input) {
                      input.focus();
                      input.select();
                    });
                  }
                });
            };
          
          
          // eslint-disable-next-line no-undef
          })(jQuery);
          window.$(".MuiTable-root").enableCellNavigation()
      },[projectData.projectnummer])
    
    const projectlengh = (projectnaam) =>{
        if(projectnaam !== undefined && projectnaam !== null && projectnaam !== ''){
            var matches = projectnaam.match(/(\d+)/);
            var len = matches[0]
        }
        return len
    }
    useEffect(() => {
        setRownumber(projectlengh(projectData.projectnaam))
    },[projectData.projectnaam])

   
    useEffect(() => {
        
        if(props.mpodata !== undefined )
        {

            setAlgemenegegevens(props.mpodata.algemeene !== undefined ? props.mpodata.algemeene: [])
            setIcemComponenten(props.mpodata.icemComponenten !== undefined ? props.mpodata.icemComponenten: [])
            setIcemDebieten(props.mpodata.icemDebiet !== undefined ? props.mpodata.icemDebiet: [])
            setOmvormer(props.mpodata.omvormer !== undefined ? props.mpodata.omvormer: [])
            setPlanning(props.mpodata.planning !== undefined ? props.mpodata.planning: [])
            setBouwkundig(props.mpodata.bouwkundig !== undefined ? props.mpodata.bouwkundig: [])
            setProductie_exact(props.mpodata.productieExact !== undefined ? props.mpodata.productieExact: [])
            setBewoners(props.mpodata.bewoner !== undefined ? props.mpodata.bewoner: [])
           
        }  
        
    },[props.mpodata,projectParam.id])
    
    // console.log(algemenegegevens_)
    const returnNumberOfIcemType = (e,x) => {
       
        let xArray = []
        for(let i in e){
            
           if(e[i] !== null && e[i] === x){
            xArray.push(e[i])
           }
            
        }
        
       return xArray.length
      } 

    let aNumber = parseInt(returnNumberOfIcemType(typeIcem,"a"))
    let eNumber = parseInt(returnNumberOfIcemType(typeIcem,"e"))
    let fNumber = parseInt(returnNumberOfIcemType(typeIcem,"f"))
    let iNumber = parseInt(returnNumberOfIcemType(typeIcem,"i"))
    let pNumber = parseInt(returnNumberOfIcemType(typeIcem,"p"))
    let sum = aNumber + eNumber + fNumber + iNumber + pNumber
    if(isNaN(sum)){
        sum = 0
    }
    

   
    const styles = {
        row: {
            width: '100%'
        },
        label:{
            color: '#3498db'
        },
        input:{
            width:'17%',
        },
        input2:{
            width:'15%',
        }

    }
    const dateFormdat = (d) => {
        if(typeof(d) !== 'string'){
            if(d !== undefined && d !== null){
        
                let ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(d);
                let mo = new Intl.DateTimeFormat('en', { month: 'short' }).format(d);
                let da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(d);
                return `${da}-${mo}-${ye}`
            }else{
                return d
            }
        }else{
            return d
        }
        
        
    }

    
    useEffect(() => {
        var array = []
        if (Object.keys(icemComponenten).length !== 0)
        {
            icemComponenten.map(item => array.push(item.icemType))
            setTypeicem(array)
        }
        
    },[icemComponenten])

    const dataJson = {
        'projectId':projectParam.id,
        'algemeene': algemenegegevens_,
        'icemComponenten': icemComponenten,
        'icemDebiet': icemDebieten,
        'omvormer':omvormer,
        'planning':planning,
        'bouwkundig':bouwkundig,
        'productie_exact':productie_exact,
        'bewoners':bewoners
    }
           


    // Header 
    const displayHeader = () => {
        return(
            <>
                <div className="row">
                        <div className="col-xxl-6">
                            <h5 className="mb-3">Algemene projectgegevens</h5>
                            <div className="card card-animate">
                                <div className="card-body">
                                    <h6 className="mb-1 fs-14"> Projectnummer: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectnummer}</p>
                                    <h6 className="mb-1 fs-14"> Klantnaam: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.Klantnaam}</p>
                                    <h6 className="mb-1 fs-14"> Projectnaam: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectnaam}</p>
                                    <h6 className="mb-1 fs-14"> Projectstatus: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectstatus}</p>
                                    <div className="icemsamevating">
                                        <p class="text-muted">iCEM samenvatting</p>
                                    </div>
                                    <div>
                                        <label style={styles.label} htmlFor='readonlyInput' className='form-label'>Totaal aantal iCEM: </label>
                                        <input style={styles.input} type="text"  className='form-control' id='readonlyInput' value={sum} readOnly />
                                    </div>
                                    <div className='row'>
                                        <div className='col-sm'>
                                            <label style={styles.label} htmlFor='readonlyInput1' className='form-label'>-i: </label>
                                            <input type="text" className='form-control' id='readonlyInput1' value={iNumber} readOnly />
                                        </div>
                                        <div className='col-sm'>
                                            <label style={styles.label} htmlFor='readonlyInput2' className='form-label'>-p: </label>
                                            <input type="text" className='form-control' id='readonlyInput2' value={pNumber} readOnly />
                                        </div>
                                        <div className='col-sm'>
                                            <label style={styles.label} htmlFor='readonlyInput2' className='form-label'>-e: </label>
                                            <input type="text" className='form-control' id='readonlyInput2' value={eNumber} readOnly />
                                        </div>
                                        <div className='col-sm'>
                                            <label style={styles.label} htmlFor='readonlyInput2' className='form-label'>-f: </label>
                                            <input type="text" className='form-control' id='readonlyInput2' value={fNumber} readOnly />
                                        </div>
                                        <div className='col-sm'>
                                            <label style={styles.label} htmlFor='readonlyInput2' className='form-label'>-a: </label>
                                            <input type="text" className='form-control' id='readonlyInput2' value={aNumber} readOnly />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-xxl-6">
                            <h5 class="mb-3">Contactpersonen aanemer</h5>
                            <div className="card card-animate">
                                <div className="card-body">
                                    <div className='row'>
                                        <div className='col'>
                                            <h6 className="mb-1 fs-14"> Werkvoorbereider aanemer: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.werkVoorbereiderAanemer !== undefined && projectData.werkVoorbereiderAanemer !== null ? projectData.werkVoorbereiderAanemer.name: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Projectleider aanemer: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectleiderAanemer !== undefined && projectData.projectleiderAanemer !== null  ? projectData.projectleiderAanemer.name: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Uitvoerder aanemer: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.uitvoerderAanemer !== undefined && projectData.uitvoerderAanemer !== null  ? projectData.uitvoerderAanemer.name: 'Geen'}</p>
                                        </div>
                                        <div className='col'>
                                            <h6 className="mb-1 fs-14"> Phone: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.werkVoorbereiderAanemer !== undefined && projectData.werkVoorbereiderAanemer !== null ?  + projectData.werkVoorbereiderAanemer.phone: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Phone: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectleiderAanemer !== undefined && projectData.projectleiderAanemer !== null ?  + projectData.projectleiderAanemer.phone: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Phone: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.uitvoerderAanemer !== undefined && projectData.uitvoerderAanemer !== null ?  + projectData.uitvoerderAanemer.phone: 'Geen'}</p>
                                        </div>
                                    </div>
                                
                                </div>
                            </div>
                            <h5 class="mb-3">Contactpersonen Factoryzero</h5>
                            <div className="card card-animate">
                                <div className="card-body">
                                    <div className='row'>
                                        <div className='col'>
                                            <h6 className="mb-1 fs-14"> Werkvoorbereider Fz: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.werkVoorbereiderFZ !== undefined ? projectData.werkVoorbereiderFZ.name: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Projectleider Fz: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectleiderFZ !== undefined ? projectData.projectleiderFZ.name: 'Geen'}</p>
                                           
                                        </div>
                                        <div className='col'>
                                            <h6 className="mb-1 fs-14"> Phone: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.werkVoorbereiderFZ !== undefined ? + projectData.werkVoorbereiderFZ.phone: 'Geen'}</p>
                                            <h6 className="mb-1 fs-14"> Phone: &nbsp; </h6><p class="mb-0" style={styles.label}>{projectData.projectleiderFZ !== undefined ? + projectData.projectleiderFZ.phone: 'Geen'}</p>
                                        </div>
                                    </div>
                                
                                </div>
                            </div>
                            
                        </div>
                </div>
            </>
        )
    }
    //Body
    const bewerKenMpoForm = () => {
        setbewerkenactif(true)
    }
    const cancelbewerKenMpoForm = () => {
        setbewerkenactif(false)
        }
    const submitMpoForm = () => {
        const form = dataJson
        // console.log(form)
        if(form){
            if(props.mpoAanmaken(form,projectParam.id)){
                
                setbewerkenactif(false)
            }
           
        }
        setbewerkenactif(false)
    }
    const exportExcelFunctie = (data,filename) => {
       
        var algemeene = XLSX.utils.json_to_sheet(data.algemeene);
        var icemgegevens =  XLSX.utils.json_to_sheet(data.icemComponenten);
        var icemDebiet =  XLSX.utils.json_to_sheet(data.icemDebiet);
        var omvormer =  XLSX.utils.json_to_sheet(data.omvormer);
        var planning =  XLSX.utils.json_to_sheet(data.planning);
        var bouwkundig =  XLSX.utils.json_to_sheet(data.bouwkundig);
        var productie_exact =  XLSX.utils.json_to_sheet(data.productie_exact);
        var bewoners =  XLSX.utils.json_to_sheet(data.bewoners);

        var wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, algemeene, `Algemeene gegevens`);
        XLSX.utils.book_append_sheet(wb, icemgegevens, `Icem gegevens`);
        XLSX.utils.book_append_sheet(wb, icemDebiet, `Icem debiet gegevens`);
        XLSX.utils.book_append_sheet(wb, omvormer,`Omvormer gegevens`);
        XLSX.utils.book_append_sheet(wb, planning, `Planning gegevens `);
        XLSX.utils.book_append_sheet(wb, bouwkundig, `Bouwkundig gegevens`);
        XLSX.utils.book_append_sheet(wb, productie_exact, `Productie Exact gegevens`);
        XLSX.utils.book_append_sheet(wb, bewoners, `Bewoner gegevens`);
         /* generate an XLSX file */
        XLSX.writeFile(wb, filename+".xlsx");
             
    }
    const excelExport = async () => {
        const excelform = dataJson
        const fileName = " MPO " + projectData.projectnummer
        exportExcelFunctie(excelform,fileName)
    }
   
    
    const displayButton = () => {
        return (
        <>
            {
            bewerken_actif === true ? 
            <>
                <button type="button" onClick={cancelbewerKenMpoForm} className="btn btn-danger btn-icon waves-effect btn-sm">x</button>&nbsp;&nbsp;
                <button type="button" onClick={submitMpoForm} className="btn btn-primary btn-icon waves-effect btn-sm"><i class="lab las la-save"></i></button>
            </>
            :  
            <>
                <button type="button" onClick={bewerKenMpoForm} class="btn btn-info btn-icon waves-effect btn-sm"><i class="mdi mdi-lead-pencil"></i></button>&nbsp;&nbsp;      
                <button type="button" onClick={excelExport} className="btn btn-success btn-icon waves-effect btn-sm"><i class=" ri-file-excel-2-line"></i></button>
            </>
            
            }
            
        
        </>)
    }

    const algemeneTable = useCallback(() => {
        return(<>
                <Algemeentable props={{
                    'data': algemenegegevens_ !== undefined?algemenegegevens_:[],
                    'setData': setAlgemenegegevens,
                    'bewerken_actif' : bewerken_actif,
                    'projectnummer':projectData.projectnummer
                        
            }} /> 
        
        </>)

    },[algemenegegevens_,projectData.projectnummer,bewerken_actif,setAlgemenegegevens])
    const icemgegevensTable = useCallback(() => {
        return(
            <IcemTable  props={{
            'bewerken_actif' : bewerken_actif,
            'data': icemComponenten !== undefined?icemComponenten:[],
            'setData': setIcemComponenten,
            }}/>

        )
    },[bewerken_actif,icemComponenten,setIcemComponenten])
    const icemdebietgegevensTable = useCallback(() => {
        return(
            <Icemdebieten props={{
                'bewerken_actif' : bewerken_actif,
                'data':icemDebieten !== undefined?icemDebieten:[],
                'setData': setIcemDebieten,

           }}
           />
        )
    },[bewerken_actif,icemDebieten,setIcemDebieten])
    const energiebundelgegevensTable = useCallback(() => {
        return(
            <Energiebundel props={{
                // 'row': rownumber,
                'bewerken_actif' : bewerken_actif,
                // 'data': props.mpodata !== undefined?props.mpodata.en:[],
                // 'setData': setIcemDebieten,
           }}
           />
        )
    },[])

    const omvormergegevensTable = useCallback(() => {
        return(
            <Omvormer props={{
                                           
                'bewerken_actif' : bewerken_actif,
                'data':omvormer !== undefined?omvormer:[],
                'setData': setOmvormer,
           }}
           />
        )
    },[bewerken_actif,omvormer,setOmvormer])

    const planninggegevensTable = useCallback(() => {
        return(
            <Planning props={{

                'bewerken_actif' : bewerken_actif,
                'data':planning !== undefined?planning:[],
                'setData': setPlanning,
           }}
           />
        )
    },[bewerken_actif,planning,setPlanning])

    const bouwkundiggegevensTable = useCallback(() => {
        return(
            <Bouwkundig props={{

                'bewerken_actif' : bewerken_actif,
                'data':bouwkundig !== undefined?bouwkundig:[],
                'setData':setBouwkundig
           }}
           />
        )
    },[bewerken_actif,bouwkundig,setBouwkundig])

    const productieExactgegevensTable = useCallback(() => {
        return(
            <ProductieExact props={{                                  
                'bewerken_actif' : bewerken_actif,
                'data':productie_exact !== undefined?productie_exact:[],
                'setData':setProductie_exact
           }}
           />
        )
    },[bewerken_actif,productie_exact,setProductie_exact])

    const bewonergegevensTable = useCallback(() => {
        return(
            <Bewoners props={{
                'projectData':projectData.projectnummer,
                'bewerken_actif' : bewerken_actif,
                'data':bewoners !== undefined?bewoners:[],
                'setData':setBewoners
                
           }}
           />
        )

    },[bewerken_actif,bewoners,setBewoners])
    const displayBody = () => {
        return(
            <>
            <div class="row">
                <div class="card-body">
                    {/* <!-- Small Button --> */}
                        {displayButton()}
                        {/* <!-- Nav tabs --> */}
                        <ul class="nav nav-tabs nav-tabs-custom nav-success nav-justified mb-3" role="tablist">
                            <li class="nav-item">
                                <a class="nav-link active" data-bs-toggle="tab" href="#algemeen" role="tab">
                                    Algemeen
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#profile1" role="tab">
                                    Icem-componenten
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#messages1" role="tab">
                                    Icem-debiet
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#settings1" role="tab">
                                    Energiebundel
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#omvormer" role="tab">
                                    Omvormer
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#planning" role="tab">
                                    Planning
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#bouwkundig" role="tab">
                                   Bouwkundig
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#productieexact" role="tab">
                                    Productie-exact
                                </a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-bs-toggle="tab" href="#bewoners" role="tab">
                                    Bewoners
                                </a>
                            </li>
                        </ul>

                        {/* <!-- Tab panes --> */}
                        <div class="tab-content text-muted">
                            <div class="tab-pane active" id="algemeen" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2"> 
                                    {algemeneTable()}
                                </div>
                            </div>
                            <div class="tab-pane" id="profile1" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2">
                                    {icemgegevensTable()}
                                </div>
                                
                            </div>
                            <div class="tab-pane" id="messages1" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2">
                                    {icemdebietgegevensTable()}
                                </div>
                            </div>
                            <div class="tab-pane" id="settings1" role="tabpanel">
                 
                                    <div class="flex-shrink-0">
                                        <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                    </div>
                                    <div class="flex-grow-1 ms-2">
                                       {energiebundelgegevensTable()}
                                    </div>
                      
                            </div>
                            <div class="tab-pane" id="omvormer" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2">
                                    {omvormergegevensTable()}
                                </div>
                            </div>
                            <div class="tab-pane" id="planning" role="tabpanel">
                                    <div class="flex-shrink-0">
                                        <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                    </div>
                                    <div class="flex-grow-1 ms-2">
                                       {planninggegevensTable()}
                                    </div>
                            </div>
                            <div class="tab-pane" id="bouwkundig" role="tabpanel">

                                    <div class="flex-shrink-0">
                                        <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                    </div>
                                    <div class="flex-grow-1 ms-2">
                                       {bouwkundiggegevensTable()}
                                    </div>
                            </div>
                            <div class="tab-pane" id="productieexact" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2">
                                    {productieExactgegevensTable()}
                                </div>
                            </div>
                            <div class="tab-pane" id="bewoners" role="tabpanel">
                                <div class="flex-shrink-0">
                                    <i class="ri-checkbox-multiple-blank-fill text-success"></i>
                                </div>
                                <div class="flex-grow-1 ms-2">
                                    {bewonergegevensTable()}
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* <!-- end card-body --> */}
                </div>
                {/* <!-- end card --> */}
            </>
        )
        
    }

  return (

    <>
        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
            <h4 class="mb-sm-0">Mpo {projectData.projectnummer}</h4>
        </div>

        {/* Header panel */}
        {projectData.projectnaam === "" ? <>
            <p>Er is nog geen project aangemaakt</p>
            Click op <Link to="/projectaanmaken" relative="path">projectnaamaken</Link> 
            </> : 
        <>
            {displayHeader()}
            {displayBody()}
        </>
        
        
        }
        {}
        {/* Table body */}
        {}   
    </>
  )
}

const getProjectItems = state => ({
    context: state.project.dataProjectID,
    mpodata: state.mpo.dataSet 
})
export default connect(getProjectItems,{get_projectById,mpoGet,mpoAanmaken})(MpoComponent)
