import axios from "axios";
import Cookies from 'js-cookie'
import {
    AANMAKEN_OPLEVERRAPPORT_SUCCESS,
    AANMAKEN_OPLEVERRAPPORT_FAIL,
    LOAD_OPLEVERRAPPORT_SUCCESS,
    LOAD_OPLEVERRAPPORT_FAIL,
    LOAD_OPLEVERRAPPORTOVERZICHT_SUCCESS,
    LOAD_OPLEVERRAPPORTOVERZICHT_FAIL
} from './types'

axios.defaults.withCredentials = true

export const opleverrapportAanmaken = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = JSON.stringify(formData)
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/opleverrapport/create-opleverrapport`,body,config)
        
        if(res.data.success){
            dispatch({
                type: AANMAKEN_OPLEVERRAPPORT_SUCCESS,
                payload: res.data.success
            })
            var formdata = {
                'site_id': formData.site_id,
                'project_id': formData.project_id
            }
            dispatch(opleverrapportgetbyId(formdata))
            // dispatch(load_medewerkers())
            // dispatch(load_project())
            // dispatch(mpoGet(id))
        }else{
            dispatch({AANMAKEN_OPLEVERRAPPORT_FAIL,
                payload: res.data.error
            })
            // dispatch(load_medewerkers)
        }
    }
    catch (err) {
        dispatch({
            type: AANMAKEN_OPLEVERRAPPORT_FAIL
        });
        // dispatch(load_medewerkers)
    }
}
export const opleverrapportOverzicht = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = formData
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/opleverrapport/get-opleverrapportoverzicht`,body,config)
        if(res.data){
            dispatch({
                type: LOAD_OPLEVERRAPPORTOVERZICHT_SUCCESS,
                payload: res.data
            })
        }else{
            dispatch({LOAD_OPLEVERRAPPORTOVERZICHT_FAIL,
                payload: res.data.error
            })
        }
    }
    catch (err) {
        dispatch({
            type: LOAD_OPLEVERRAPPORTOVERZICHT_FAIL
        });
    }
}
