import React,{useRef,useCallback, useEffect, useState, useMemo} from 'react'
import { BrowserRouter as Routes, useParams } from 'react-router-dom';
import { get_projectById } from '../../actions/project';
import { connect } from 'react-redux';
import { mpoGet } from '../../actions/mpo';
import {  testrapportOverzicht } from '../../actions/testrapport';
import MaterialReactTable from 'material-react-table';
import PrintIcon from '@mui/icons-material/Print';
import AddIcon from '@mui/icons-material/Add';
import Button from '@mui/material/node/Button';
import CreateIcon from '@mui/icons-material/Create';
import { Box, Modal } from '@mui/material';
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
// // CUSTOMS COMPONENTS
import SearchNavbar from '../../components/SearchNavbar'

const Testrapportmpo = (props) => {
    let projectId = useParams()
    const projectID = projectId.id
    const titleName = projectID.name
    const [projectnummer,setProjectnummer] = useState('')
    const [projectnaam, setProjectnaam] = useState('')
    const [projectstatus, setProjectstatus] = useState('')
    const [data,setData] = useState([])
    const [noData,setNodata] = useState(false)
    const state = useRef([])
    const [klantnaam,setKlantnaam] = useState('')
    const [plaats, setplaats] = useState('') 

    useEffect(() => {
        props.get_projectById(projectID)
        props.testrapportOverzicht({'projectID' : projectID})
        // props.mpoGet(projectID)
        if(props.context !== undefined)
        {
            // setRow(projectlengh(context.projectnaam))
            setProjectnaam(props.context.projectnaam)
            setKlantnaam(props.context.Klantnaam)
            setProjectnummer(props.context.projectnummer)
            setProjectstatus(props.context.projectstatus)
        }
        
        
       
    },[projectId,props.context.projectnaam,projectnummer])
    
    useEffect(() => {
        if(props.testrapportData !== undefined && props.testrapportData.lengh !== 0)
        {
            // data.current = props.testrapportData
            setData(props.testrapportData)
            

        }else{
           
        }
    },[projectId,props.testrapportData,data,titleName])

    useEffect(() => {
        if(projectnaam !== ''){
            setNodata(false)
        }else{
            setNodata(true)
        }

    },[projectnaam])
    const displayheader = () => {
        return(
            <>
                <div className="card card-animate ">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label class="form-label">Projectnummer</label>
                        </div>
                        <div class="col-lg-9">
                            <p type="text" class="form-control form-control-sm">{projectnummer} </p>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label class="form-label">Projectnaam</label>
                        </div>
                        <div class="col-lg-9">
                            <p type="text" class="form-control form-control-sm">{projectnaam} </p>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label class="form-label">Projectstatus</label>
                        </div>
                        <div class="col-lg-9">
                            <p type="text" class="form-control form-control-sm">{projectstatus} </p>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    let navigate = useNavigate(); 
    const edit = useCallback((row) => {
        let path = `/testrapport/${projectID}/${row.original.bouwnr}`; 
        navigate(path);
        },[projectId,navigate])
      const columns = useMemo(
    
    () => [
        // column definitions ....
        {
            accessorKey: 'bouwnr',
            header: 'Bwnr'
        },
        {
            accessorKey: 'blok',
            header: 'Blok'
        },
        {
            accessorKey: 'straat',
            header: 'Straat'
        },
        {
            accessorKey: 'postcode',
            header: 'Postcode'
        },
        {
            accessorKey: 'icem',
            header: 'iCEM'
        },
        {
            accessorKey: 'testrapportaangemaakt',
            header: 'Aangemaakt'
        },
        {
            accessorKey: 'datumtestrapportAangemaakt',
            header: 'Aangemaakt'
        },
        {
            accessorKey: 'definitief',
            header: 'Definitief'
        },
        {
          id:'action',
          header:'Actie',
          Cell: ({ row }) => {
            
            return [
              <Button
                key={row.original.bouwnr}
                onClick={() => {
                  // do something
                  edit(row)
                }}
              >
               {row.original.testrapportaangemaakt === 'Nee' ? <AddIcon /> : <CreateIcon />} 
              </Button>,
              
            ]
          }
        },

        
    ],
    []
  )
    const handleExportData = (rows) => {
        alert('genereer pdf ? excel ? ')
    }

   return (
    <div className='mpo'>
        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
            <h4 class="mb-sm-0">Testrapport {projectnummer}</h4>
        </div>
        {noData !== true ? 
        <>
            <h5 className="mb-3">Algemene projectgegevens</h5>
            <div className='row'>
                {displayheader()}
            </div>
            <MaterialReactTable
                columns={columns}
                data={data}
                enableColumnResizing
                enableRowSelection={true}
                renderTopToolbarCustomActions={({ table }) => (
                    <button
                        color="primary"
                        className="btn btn-success btn-icon waves-effect waves-light"
                        //export all data that is currently in the table (ignore pagination, sorting, filtering, etc.)
                        onClick={() => handleExportData(table.getSelectedRowModel().rows)}
                    >
                    <i class=" ri-file-excel-2-line"></i>
                </button>
                   
                )} 
            />
  
        </>  
        :
        <>
        <p>Er is nog geen project aangemaakt</p>
        Click op <Link to="/projectaanmaken" relative="path">project aanmaken</Link> 
        </>
        }
    </div>

   )
 }
const getProjectItems = state => ({
    context: state.project.dataProjectID,
    testrapportData: state.testrapport.data
})
export default connect(getProjectItems,{get_projectById,testrapportOverzicht})(Testrapportmpo)
