import React from 'react'
import { useParams } from 'react-router-dom'
import Testrapportmpo from '../containers/Productie/Testrapportmpo.js'
import Productiebonmpo from '../containers/Productie/Productiebonmpo.js'
import Opleverrapportoverzicht from '../containers/Productie/Opleverrapportoverzicht'
import ProductiestatusicemOverzicht from '../containers/Productie/ProductiestatusicemOverzicht'
import MpoComponent from '../containers/Projecten/MpoComponent'

const DynamicComponents = () => {

    const { name } = useParams()
    // console.log(name)
    
    switch (name) {
        case 'mpo':
            return  <MpoComponent />
        case 'productiebon':
            return  <Productiebonmpo />
        case 'testrapport':
            return  <Testrapportmpo />
        case 'opleverrapport':
            return <Opleverrapportoverzicht />
        case 'productiestatusicem':
            return <ProductiestatusicemOverzicht />
        default:
            return null
    }

}

export default DynamicComponents
