import React, { useEffect, useState,useMemo  } from 'react';
import {getRoles }from '../../actions/admin'
import { connect } from 'react-redux'
import {Box, Typography} from '@mui/material'
import {
    DataGrid,
    GridToolbarContainer,
    GridToolbarFilterButton,
    GridToolbarExport,
    GridToolbarDensitySelector} from '@mui/x-data-grid'
import { grey } from '@mui/material/colors'
import UserActions from '../User/UserActions';

const CustomToolbar = ({ setFilterButtonEl }) => (
    <GridToolbarContainer>
      <GridToolbarDensitySelector />
      <GridToolbarExport />
      <GridToolbarFilterButton ref={setFilterButtonEl} />
    </GridToolbarContainer>
  );

const Roles = ({getRoles,roles}) => {
    // const [state, setState] = useState([])
  
    const [pageSize,setpageSize] = useState(10)
    const [rowId, setRowId] = useState(null)
    const [loadthis, setloadThis] = useState(null)
    let rolesresults = []
      if(roles !== []){
        if(roles !== undefined && roles !== null){
            rolesresults = roles
          }
    }

    useEffect(() => {
        getRoles() 
        if(loadthis === false){
            getRoles() 
          } 
          return () => { setloadThis(true)}
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    
    const columns = useMemo(() => [
        {field:'role_name', 
         headerName: 'Rollen',
         width:400,
         sortable: true,
         filterable: true,
         pinnable: true,
         editable:true
        },
        {field:'medewekerCount', headerName: 'Aantal', width:400,},
        {field:'actions',headerName:'Actions',width:200, type:'actions', renderCell:params=> <UserActions  {...{params,rowId, setRowId}}/>}
    ],[rowId])
    const [filterButtonEl, setFilterButtonEl] = React.useState(null);
  return (
    <div>
        <Box 
    sx={{
    height: 800,
    width: '100%'
    }}>
    <Typography
    variant='h5'
    component='h5'
    sx={{textAlign:'left', mt:3, mb:3}}
    >
        Roles Table
    </Typography>
    <div  style={{ position:'absolute', display: 'flex', textAlign:'center', height: 350, width: '80%' }}>
        <DataGrid
        columns={columns}
        components={{Toolbar: CustomToolbar,}}
        componentsProps={{
            panel: {
              anchorEl: filterButtonEl,
            },
            toolbar: {
              setFilterButtonEl,
              showQuickFilter: true,
              quickFilterProps: { debounceMs: 500 },
              
            },
            GridToolbar: {
                printOptions:{
                  pageStyle: '.MuiDataGrid-root .MuiDataGrid-main { color: rgba(0, 0, 0, 0.87); }',
                }
              }
          }}
        rows={rolesresults}
        loading={rolesresults.length === 0}
        getRowId={row=>row.id}
        rowsPerPageOptions={[10,20]}
        pageSize={pageSize}
        onPageSizeChange={(newPageSize) => setpageSize(newPageSize)}
        checkboxSelection
        disableSelectionOnClick
        getRowSpacing={params=>({
            top:params.isFirstVisible ? 0:5,
            bottom:params.isFirstVisible ?0:5
        })}
        sx={{
            '@media print': {
                '.MuiDataGrid-main': { color: 'rgba(0, 0, 0, 0.87)' },},
            [`& .{gridClasses.row}`]: {
                bgcolor: (theme) =>
                theme.palette.mode === 'light' ? grey[200] : grey[1200]
            }
        }}
        onCellEditCommit={params=> setRowId(params.id)}
        pinnedRows={{ pinnedColumns: { left: ['projectnr'], right: ['actions'] } }}
        />
        </div>
        
        
        </Box>
    </div>
  )
}

const getALLrolesitems = state => ({
    roles: state.admin.roles,
})
export default connect(getALLrolesitems,{getRoles})(Roles)
