// import axios from 'axios'
import axios from  'axios'
import Cookies from 'js-cookie'
import { useNavigate,Link, Navigate } from 'react-router-dom'
import { load_user } from './profile'

import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    MESSAGE_SUCCESS,
    MESSAGE_ERROR,
    PASSWORD_CHANGE_SUCCESS,
    PASSWORD_CHANGE_FAIL,
    LOCKSCREEN_FAIL,
    LOCKSCREEN_SUCCESS,
    UNLOCKSCREEN_FAIL,
    UNLOCKSCREEN_SUCCESS,
    LOAD_PERMISSIONS_USER_SUCCESS,
    LOAD_PERMISSIONS_USER_FAIL

} from './types'

axios.defaults.withCredentials = true
// Home 

export const home = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
            // 'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/home`,config);
        
        if (res.data.error){
            dispatch({
                type: MESSAGE_ERROR,
                payload : res.data.error
            })
        }
        else{
            dispatch({
                type: MESSAGE_SUCCESS,
                payload : res.data
            })

        }

    }catch(err){
        dispatch({
            type: AUTHENTICATED_FAIL,
        });
    }
}


// AUTHENTIFICATION
export const checkauthenticated = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'XSRF-TOKEN': Cookies.get('csrftoken'),
        }
    };
    const  body = JSON.stringify({
        'withCredentials': true
    });
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/authenticated`,body,config);
        
        if (res.data.error || res.data.isAuthenticated === 'error'){
            dispatch({
                type: AUTHENTICATED_FAIL,
                payload : false
            })
        }
        else if(res.data.isAuthenticated === 'success'){
            dispatch({
                type: AUTHENTICATED_SUCCESS,
                payload : true
            })
            dispatch(load_permissions_user())
        }
        else{
            dispatch({
                type: AUTHENTICATED_FAIL,
                payload : false
            })

        }

    }catch(err){
        dispatch({
            type: AUTHENTICATED_FAIL,
            payload: err
        });
    }
}

export const load_permissions_user = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'XSRF-TOKEN': Cookies.get('csrftoken'),
        }
    };

    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/loadpermissions`,{withCredentials:true},config);
        if(res.data.error){
            dispatch ({
                type: LOAD_PERMISSIONS_USER_FAIL,
                err: res.data.error
            });
        }
        else{
            dispatch ({
                type: LOAD_PERMISSIONS_USER_SUCCESS,
                payload: res.data
            })
            // dispatch(load_all_projects())
        }
        
    }catch (err){

        dispatch ({
            type: LOAD_PERMISSIONS_USER_FAIL,
            payload: err
        });
    }
}
// LOGIN REQUEST
export const login = (formData) => async dispatch => {
    
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const  body = formData

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/login`,body,config);
        if (res.data.success) {
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data
            });
            dispatch(load_user())
            dispatch(load_permissions_user())
            // dispatch(load_medewerkers())
            // dispatch(load_all_projects())
        }else {
            dispatch({
                type: LOGIN_FAIL,
                payload: res.data.error
            })
           dispatch(load_user())
        //    dispatch(load_medewerkers())
        }
    }catch(err){
        dispatch({
            type: LOGIN_FAIL
        });
    }
}

// REGISTER
export const register = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };
    const  body = formData;

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/register`,body,config);
        if (res.data.error) {
            dispatch({
                type: REGISTER_FAIL
            });
        }else{
            dispatch({
                type: REGISTER_SUCCESS
            })
        }
    }catch(err){
        dispatch({
            type: REGISTER_FAIL
        });
    }
}

// PASSWORD RESET
export const newPassword = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };
    const  body = formData;

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/users/user-resetPassword`,body,config);
        if (res.data.success) {
            console.log(res.data.success)
            dispatch({
                type: PASSWORD_CHANGE_SUCCESS,
                payload: res.data.success
            });
        }else{
            dispatch({
                type: PASSWORD_CHANGE_FAIL,
                payload: res.data.error
            })
        }
    }catch(err){
        dispatch({
            type: PASSWORD_CHANGE_FAIL
        });
    }
}


// LOGOUT
export const logout = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };
    const  body = JSON.stringify({
        'withCredentials': true
    });

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/logout`,body,config);
        if (res.data.success) {
            dispatch({
                type: LOGOUT_SUCCESS,
                isAuthenticated: false,
                payload: res.data
            });
       
        }else{
            dispatch({
                type: LOGOUT_FAIL,
            })
        }
    }catch(err){
        dispatch({
            type: LOGOUT_FAIL
        });
    }
}

export const screenLock = (formdata) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };
    const  body = formdata
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/screenlock`,body,config);
        if (res.data.success) {
            dispatch({
                type: LOCKSCREEN_SUCCESS,
                // isAuthenticated: false,
                payload: res.data
            });
            dispatch(load_user())
        }else{
            dispatch({
                type: LOCKSCREEN_FAIL,
            })
        }
    }catch(err){
        dispatch({
            type: LOCKSCREEN_FAIL
        });
    }

}

