import React, { useEffect, useState,useRef, useCallback} from 'react'
import { connect } from 'react-redux'
import Sidebar from './js/Navbar/Sidebar'
import Topbar from './js/Navbar/Topbar'
import { useNavigate, useLocation, useHistory} from 'react-router-dom';

import { loadallprojectsList } from '../actions/project'

const Layout = (props) => {
    const copyTooltipRef = useRef();
    const location = useLocation();
    const[functiE, setfunctiE] = useState('');
    const [user,setUser] = useState([])
    const [projects,setProjects] = useState([])
    const [permissions,setPermissions] = useState([])
      useEffect(() => {
        setfunctiE(props.functie)
        setUser(props.user)
        setProjects(props.projects)
        setPermissions(props.permissions)
        copyTooltipRef && copyTooltipRef.current && copyTooltipRef.current.updateTargetEvents();
      }, [location,props]);

      const [unlocked,setUnlocked] = useState(null)
      
    //   useEffect(() => {
    //     setUnlocked(props.is_locked)
    // },[])
    const navigate = useNavigate();

      const [menuState,setMenustate] = useState(null)
      const profileData = {
        'achternaam': user.achternaam,
        'voornaam': user.voornaam,
        'functie': functiE,
      }
      
      const getCurrentYear = () => { 
        var year = new Date().getFullYear()
        return year
      }
      const topbarNav = useCallback(() => <Topbar profileData={profileData}  setMenustate={setMenustate}/>,[profileData,setMenustate])
      const sidebarNav = useCallback(() => <Sidebar projects={projects} menuState={menuState} />,[projects,menuState])
      return (
        <div id="layout-wrapper">
        {topbarNav()}
        {permissions && permissions.length !==0 ? sidebarNav():null}
        <div class="main-content">
          <div class="page-content">
            <div class="container-fluid">
              {props.children}
            </div>
          </div>
        </div>
        <footer class="footer">
              <div class="container-fluid">
                  <div class="row">
                      <div class="col-sm-6">
                      {getCurrentYear()} © Factory zero.
                          {/* <script>{document.write(new Date().getFullYear())}</script> © Factory zero. */}
                      </div>
                      <div class="col-sm-6">
                          <div class="text-sm-end d-none d-sm-block">
                          {getCurrentYear()} © Factory zero.
                          </div>
                      </div>
                  </div>
              </div>
        </footer>
      </div>
      )
  }
const mapStateToProps = state => ({ 
  isAuthenticated: state.auth.isAuthenticated,
  functie: state.profile.functie,
  user: state.profile.medewerker,
  is_locked: state.profile.is_loggedin,
  projects: state.project.data,
  permissions: state.auth.permissions
})
export default connect(mapStateToProps, {})(Layout)
