import React, {useState} from 'react'
import PropTypes from "prop-types";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Navigate,Link } from 'react-router-dom'
import { connect } from 'react-redux'
import {login } from '../actions/auth'

// import mylogo from '%PUBLIC_URL%/assets/img/logo.png'
import CSRFToken from '../components/CSRFToken';




const Login = ({ login,isAuthenticated,isloggedin }) => {
  // functon om data's te initialiseren
  const {addtoast} = toast("")
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  })

  
  const { email,password} = formData;
  const [loading,setLoading] = useState(false)
  const onChange = e => setFormData({...formData, [e.target.name]: e.target.value}) // we use ...formData to not touch other data's
 
  const onSubmit = e => {
    e.preventDefault();
    setLoading(true)
    if(login(JSON.stringify(formData))){
        setLoading(false)
    }
  }

  if(isAuthenticated === true && isloggedin === true){
    
    {
        return <Navigate to="/" />
    }
    
  }
   
  return (
    <>
      <div class="auth-page-wrapper pt-5">
      {/* <!-- auth page bg --> */}
        <div class="auth-one-bg-position auth-one-bg" id="auth-particles">
            <div class="bg-overlay"></div>

            <div class="shape">
                <svg xmlns="http://www.w3.org/2000/svg" version="1.1" xlink="http://www.w3.org/1999/xlink" viewBox="0 0 1440 120">
                    <path d="M 0,36 C 144,53.6 432,123.2 720,124 C 1008,124.8 1296,56.8 1440,40L1440 140L0 140z"></path>
                </svg>
            </div>
        </div>
        {/* <!-- auth page content --> */}
        <div class="auth-page-content">
            <div class="container">
                <div class="row">
                    <div class="col-lg-12">
                        <div class="text-center mt-sm-5 mb-4 text-white-50">
                            <div>
                                <a href="index.html" class="d-inline-block auth-logo">
                                    <img src={process.env.PUBLIC_URL + "/assets/logo_transparent2.png"} alt="" height="100" />
                                </a>
                            </div>
                            {/* <p class="mt-3 fs-15 fw-medium">Premium Admin & Dashboard Template</p> */}
                        </div>
                    </div>
                </div>
                {/* <!-- end row --> */}

                <div class="row justify-content-center">
                    <div class="col-md-8 col-lg-6 col-xl-5">
                        <div class="card mt-4">

                            <div class="card-body p-4">
                                <div class="text-center mt-2">
                                    <h5 class="text-primary">Welkom Terug !</h5>
                                    <p class="text-muted">Log in om door te gaan ...</p>
                                </div>
                                <div class="p-2 mt-4">
                                    <form >
                                       
                                        <CSRFToken />
                                        <div class="mb-3">
                                            <label for="username" class="form-label">E-mail</label>
                                            <input type="email" aria-labelledby="E-mail" name="email" onChange = {e => onChange(e)}  value={email} placeholder="Voer e-mail in"  class="form-control" id="username" />
                                        </div>

                                        <div class="mb-3">
                                            {/* <div class="float-end">
                                                <a href="auth-pass-reset-basic.html" class="text-muted">Forgot password?</a>
                                            </div> */}
                                            <label class="form-label" for="password-input">Wachtwoord</label>
                                            <div class="position-relative auth-pass-inputgroup mb-3">
                                                <input type="password" name="password" value={password} onChange = {e => onChange(e)}  class="form-control pe-5 password-input" placeholder="Voer de wachtwoord in" id="password-input" />
                                                <button class="btn btn-link position-absolute end-0 top-0 text-decoration-none text-muted password-addon" type="button" id="password-addon"><i class="ri-eye-fill align-middle"></i></button>
                                            </div>
                                        </div>

                                        <div class="form-check">
                                            <input class="form-check-input" type="checkbox" value="" id="auth-remember-check" />
                                            <label class="form-check-label" for="auth-remember-check">Remember me</label>
                                        </div>

                                        <div class="mt-4">
                                            <button class="btn btn-success w-100" onClick={(e) => onSubmit(e)} type="submit">Login</button>
                                        </div>

                                        {/* <div class="mt-4 text-center">
                                            <div class="signin-other-title">
                                                <h5 class="fs-13 mb-4 title">Sign In with</h5>
                                            </div>
                                            <div>
                                                <button type="button" class="btn btn-primary btn-icon waves-effect waves-light"><i class="ri-facebook-fill fs-16"></i></button>
                                                <button type="button" class="btn btn-danger btn-icon waves-effect waves-light"><i class="ri-google-fill fs-16"></i></button>
                                                <button type="button" class="btn btn-dark btn-icon waves-effect waves-light"><i class="ri-github-fill fs-16"></i></button>
                                                <button type="button" class="btn btn-info btn-icon waves-effect waves-light"><i class="ri-twitter-fill fs-16"></i></button>
                                            </div>
                                        </div> */}
                                    </form>
                                </div>
                            </div>
                            {/* <!-- end card body --> */}
                        </div>
                        {/* <!-- end card --> */}

                        {/* <div class="mt-4 text-center">
                            <p class="mb-0">Don't have an account ? <a href="auth-signup-basic.html" class="fw-semibold text-primary text-decoration-underline"> Signup </a> </p>
                        </div> */}

                    </div>
                </div>
                {/* <!-- end row --> */}
            </div>
            {/* <!-- end container --> */}
        </div>
        
      </div>
    </>
   
  )
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  isloggedin: state.profile.is_loggedin
})
export default connect(mapStateToProps, { login })(Login)
