import React,{useEffect, useState} from 'react'
import { connect } from 'react-redux'
import {logout, unlock_screen} from '../../actions/auth'
import { useNavigate,Link, Navigate } from 'react-router-dom'


const Userlockscreen = (props) => {
    const navigate = useNavigate()
    const [formdata,setFormdata] = useState({
        email: '',
        password: ''
    })
    const [error,setError] = useState(false)
    // const [loggoutUser,setLoggoutUser] = useState(false)
    
    const onChangePassword = e => setFormdata({...formdata,[e.target.name]: e.target.value})

    const logoutUser =  () => {
       
        props.logout()   
       
    }
    if(props.isAuthenticated === false)
    {
        return <Navigate to="/login" />
    }
    // else if (props.isAuthenticated === true)
    // {
    //     return <Navigate to="/" />
    // }
    // if(loggoutUser)
    // {
    //     navigate('/login')
    // }
    const submitForm = (e) => {
        e.preventDefault()
        formdata.email = props.email
        if(formdata.password.length > 8)
        {
            if(props.unlock_screen(JSON.stringify(formdata))){
               
                setError(false)
                window.location.reload(true);
               
            }else{
                setError(true)
            }

        }else{
            setError(true)
        }
        
    }
  
  return (
    <>
    {props.is_unclocked === null ? 
    <Navigate to="/login" />
    :
    <div class="auth-page-wrapper pt-5">
    {/* <!-- auth page bg --> */}
    <div class="auth-one-bg-position auth-one-bg" id="auth-particles">
        <div class="bg-overlay"></div>
        <div class="shape">
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" xlink="http://www.w3.org/1999/xlink" viewBox="0 0 1440 120">
                <path d="M 0,36 C 144,53.6 432,123.2 720,124 C 1008,124.8 1296,56.8 1440,40L1440 140L0 140z"></path>
            </svg>
        </div>
    </div>
    <div class="auth-page-content">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <div class="text-center mt-sm-5 mb-4 text-white-50">
                        <div>
                            <a href="#" class="d-inline-block auth-logo">
                                <img src={process.env.PUBLIC_URL + "/assets/logo_transparent2.png"} alt="" height="100" />
                            </a>
                        </div>
                        {/* <p class="mt-3 fs-15 fw-medium">Premium Admin & Dashboard Template</p> */}
                    </div>
                </div>
            </div>
            {/* <!-- end row --> */}

            <div class="row justify-content-center">
                <div class="col-md-8 col-lg-6 col-xl-5">
                    <div class="card mt-4">

                        <div class="card-body p-4">
                            <div class="text-center mt-2">
                                <h5 class="text-primary">Vergrendelscherm</h5>
                                <p class="text-muted">Voer uw wachtwoord in om het scherm te ontgrendelen!</p>
                            </div>
                            <div class="user-thumb text-center">
                                <img src={process.env.PUBLIC_URL + "/assets/images/users/avatar-1.jpg"} class="rounded-circle img-thumbnail avatar-lg" alt="thumbnail" />
                                <h5 class="font-size-15 mt-3">{ props.user !== '' ? props.user.voornaam + ' '+ props.user.achternaam : 'User onbekend' }</h5>
                            </div>
                            <div class="p-2 mt-4">
                                <form action='#' novalidate>
                                    <div class="mb-3">
                                        <label class="form-label" for="userpassword">Wachtwoord</label>
                                        <input type="password" name="password" value={formdata.password} onChange={(e) => onChangePassword(e)} class="form-control" id="userpassword" placeholder="Voer wachtwoord in" required />
                                        {error === true ? 'Password incorrect': null}
                                    </div>
                                    <div class="mb-2 mt-4">
                                        <button class="btn btn-success w-100" onClick={(e) => submitForm(e)} type="submit">Ontgrendelen</button>
                                    </div>
                                </form>
                                {/* <!-- end form --> */}

                            </div>
                        </div>
                        {/* <!-- end card body --> */}
                    </div>
                    {/* <!-- end card --> */}

                    <div class="mt-4 text-center">
                        <p class="mb-0">Not you ? return <a  onClick={() => logoutUser()} class="fw-semibold text-primary text-decoration-underline"> Signin </a> </p>
                    </div>

                </div>
            </div>
            {/* <!-- end row --> */}
        </div>
        {/* <!-- end container --> */}
    </div>
</div>  
    }
    </>
   
    
  )
}

const getData = state => ({
    user: state.profile.medewerker,
    email: state.profile.email,
    is_unclocked: state.profile.is_loggedin,
    isAuthenticated: state.auth.isAuthenticated
   
})
export default connect(getData,{unlock_screen,logout})(Userlockscreen)