import React from 'react'
import { Card } from 'primereact/card';
import { connect } from 'react-redux'
import PasswordReset from '../../components/PasswordReset';


const Profile = (props) => {
  const profile = props.medewerker
 const card_body_profile = (title,name) => {
  return(
        <div className="card-body">
          <div className="row">
            <div className="col-sm-3">
              <h6 className="mb-0">{title}</h6>
            </div>
            <div className="col-sm-9 text-secondary">
              {name}
            </div>
          </div>
        </div>
    )
  }
  return (
    <div className='container'>
      <div className='row'>
        <div className='col'>
          <div className="card mb-3">
            <h2>Algemeen</h2><hr></hr>
            {card_body_profile('Voornaam:',profile.voornaam)}
            {card_body_profile('Voorletter:',profile.voorletter)}
            {card_body_profile('Tussenvoegsel:',profile.tussenvoegsel)}
            {card_body_profile('Achternaam:',profile.achternaam)}
            {card_body_profile('Geslacht:',profile.geslacht)}
            {card_body_profile('Role:',props.role_name)}
            {card_body_profile('Functie:',props.functie)}
          </div>
          </div>
        <div className='col'>
          <Card>
          <h2>Contact gegevens </h2><hr></hr>
            {card_body_profile('Geboortdatum:',profile.geboortdatum)}
            {card_body_profile('Telefoon:',profile.phone_no)}
            {card_body_profile('Email:',props.email)}
            <h2 >Wachtwoord aanpassen</h2><hr></hr>
          </Card>
          <Card>
            <PasswordReset />
          </Card>
        </div>
      </div>
     
        {/* <div className='text-center '>
         
         
        </div> */}
    </div>
  )
}
const getPorfileitems = state => ({
  email: state.profile.email,
  medewerker: state.profile.medewerker,
  voornaam: state.profile.medewerker.voornaam,
  achternaam: state.profile.medewerker.achternaam,
  role_name: state.profile.role.role_name,
  functie:state.profile.functie.functie,
  gebruiker: state.profile.gebruiker
})
export default connect(getPorfileitems,{})(Profile)
