import React,{useState,useEffect,useMemo} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import $ from 'jquery'
const Bouwkundig = (props) => {
    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
      
        $(function () {
          // $("#table7").enableCellNavigation();
        //   $("#table7").DataTable();
        });
       
      },[props])

    useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)
    },[props.props.data])
  
    const updateData = index => e => {
    
      let newArray = [...data];
      newArray[index][e.target.name] = e.target.value 
      props.props.setData(newArray)
      
    }
    const typeDakOptie = ['Zalderdak','Platdak']
    const positiebuitendeelOptie = ['Voorgevel','Achtergevel']

    const columns = useMemo(
      () => [
        {
          accessorKey: 'bouwnr',
          header: "Bouwnr",
          
        },
        {
          accessorKey: 'blok',
          header: "Blok",
          
        },
        {
          accessorKey: 'nokHoogte',
          header: "NokHoogte",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
          <input type='text' maxlength="3" style={{textTransform:"uppercase"}} name="nokHoogte" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
          :
          cell.getValue() ? cell.getValue():''
        },
        {
          accessorKey: 'nokDiepte',
          header: "nokDiepte",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
          <input type='text' maxlength="3" style={{textTransform:"uppercase"}} name="nokDiepte" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
          :
          cell.getValue() ? cell.getValue():'' 
        },
        {
          accessorKey: 'typeDak',
          header: "Type Dak",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
              <select
                onBlur={updateData(row.index)}
                defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
                className='form-select form-select-sm'
                name="typeDak"
              >
                {typeDakOptie.map(item => <option value={item}>{item}</option>)}
            </select>
            :
            cell.getValue() ? cell.getValue():'' 
        },
        {
          accessorKey: 'positiebuitendeel',
          header: "Positie buitendeel",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
         
              <select
                onBlur={updateData(row.index)}
                defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
                className='form-select form-select-sm'
                disabled={props.props.bewerken_actif === true ? false:true}
                name="positiebuitendeel"
            >
              {positiebuitendeelOptie.map(item => <option value={item}>{item}</option>)}
            </select>:
            cell.getValue() ? cell.getValue():''
        },
      ],
        [props.props.bewerken_actif, updateData,])
          
  return (
      isLoading ? <LinearProgress />:
       <MaterialReactTable
        columns={columns}
        data={data ? data: []}
        enablePinning
        enableColumnResizing
        initialState={{ 
          density: 'compact' ,
          columnPinning: { left: ['bouwnr'] },
      }}
      enableRowVirtualization
      muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
       />

  )
}

export default Bouwkundig
