import {
    LOAD_ROLES_SUCCESS,
    LOAD_ROLES_FAIL,
    AANMAKEN_ROLES_SUCCESS,
    AANMAKEN_ROLES_FAIL,
    LOAD_FUNCTIES_SUCCESS,
    LOAD_FUNCTIES_FAIL
   
} from '../actions/types'

const initialState = {
    roles: null,
    functies: null,
    msg:null
   

};

export default function(state= initialState, action){
    const { type, payload } = action;

    switch(type){
        case LOAD_FUNCTIES_SUCCESS:
            return{
                ...state,
                functies: payload.functies
            }
        case AANMAKEN_ROLES_SUCCESS:
        case LOAD_ROLES_SUCCESS:
            return{
                ...state,
                // msg: payload.message,
                roles: payload.roles,
                // functies: payload.functies
            }
        case AANMAKEN_ROLES_FAIL:  
        case LOAD_FUNCTIES_FAIL:
        case LOAD_ROLES_FAIL:
            return { ...state,
            msg: payload
        }
        default:
            return state
    };

};

