import React,{Fragment,useRef,useCallback, useEffect, useState, useMemo, memo} from 'react'
import { BrowserRouter as Routes, useParams } from 'react-router-dom';
import { get_projectById } from '../../actions/project';
import { connect } from 'react-redux';
import { get_productiebon } from '../../actions/mpo';
import { LinearProgress } from '@material-ui/core';
// CUSTOMS COMPONENTS
import ProductiebonDataGrid from '../../components/productie/ProductiebonDataGrid';
import { Link } from "react-router-dom";

// CUSTORM CSS
// import 'static/assets/css/mpo.css'

const Productiebonmpo = ({get_productiebon,context}) => {

    let projectId = useParams()
    const projectID = projectId.id
    const [nodata,setNodata] = useState(false)
    const [isLoading,setiSLoading] = useState(false)
    useEffect(() => {
        setiSLoading(true)
        if(get_productiebon(projectID))
        {
            setiSLoading(false)
        }
        
    },[projectID,isLoading])

    const [sitegegevens, setSitegegevens] = useState(context ? context.sitesgegevens: [])
    const [icemType, setIcemType] = useState([])
    useEffect(() => {
        if(context !== undefined && context !== [])
        {
            if(context.sitesgegevens !== undefined){
                setSitegegevens(context.sitesgegevens)
            }
            setNodata(false)
            
        }else{
            setNodata(true)
        }
    },[projectID,context])

    useEffect(() => {
        
        if(sitegegevens !== undefined && context !== [])
        {
            var icemTypes = []
            sitegegevens.map( item => {
                icemTypes.push(item.icemType)
            })
            setIcemType(icemTypes)
        }
    },[sitegegevens])


    const returnNumberOfIcemType = (e,x) => {
        let xArray = []
        for(let i in e){
           if(e[i] === x){
            xArray.push(e[i])
           }else if(e[i] === x){
  
           }
            
        }
       return xArray.length 
      }
      const aNumber = useRef(null)
      const eNumber = useRef(null)
      const fNumber = useRef(null)
      const iNumber = useRef(null)
      const pNumber = useRef(null)
    //   const sum = useRef(null)
      if(icemType !== undefined){
        aNumber.current = parseInt(returnNumberOfIcemType(icemType,"a"))
        eNumber.current = parseInt(returnNumberOfIcemType(icemType,"e"))
        fNumber.current = parseInt(returnNumberOfIcemType(icemType,"f"))
        iNumber.current = parseInt(returnNumberOfIcemType(icemType,"i"))
        pNumber.current = parseInt(returnNumberOfIcemType(icemType,"p"))
        
      }
      let sum = aNumber.current + eNumber.current + fNumber.current + iNumber.current + pNumber.current
      if(isNaN(sum)){
          sum = 0
      }
   
    // Algemeene-Content
    const gridComponent  = useCallback(() => <ProductiebonDataGrid 
        // mpodata= {state.current}
        projectnr = {context ? context.projectnr : null}
        projectnaam = {context ? context.projectnaam : null}
        projectId= {projectId.id}
        provincie = {context ? context.provincie : null}
        data = {sitegegevens ? sitegegevens : []}
        // setData={setSitegegevens}
        />,[sitegegevens,context,projectId.id])   

    //Icem-componenten


    const displayHeader = () => {
        return(
            <Fragment>
                <div className="col-xxl-6">
                    <div className="card card-animate ">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label class="form-label">Projectnummer</label>
                            </div>
                            <div class="col-lg-9">
                                <p type="text" class="form-control form-control-sm">{context  ? context.projectnr:'onbekend'} </p>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label class="form-label">Projectnaam</label>
                            </div>
                            <div class="col-lg-9">
                                <p type="text" class="form-control form-control-sm">{context ? context.projectnaam:'onbekend'} </p>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label class="form-label">Opdrachtgever</label>
                            </div>
                            <div class="col-lg-9">
                                <p type="text" class="form-control form-control-sm">{context && context.opdrachtgever[0] !== undefined ? context.opdrachtgever[0].klantnaam:'onbekend'} </p>
                            </div>
                        </div>
                     
                    </div>
                </div>
                <div className="col-xxl-6">
                    <div className="card card-animate ">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label class="form-label">Plaats</label>
                            </div>
                            <div class="col-lg-9">
                                <p type="text" class="form-control form-control-sm">{context ? context.plaats:'onbekend'} </p>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label class="form-label">Provincie</label>
                            </div>
                            <div class="col-lg-9">
                                <p type="text" class="form-control form-control-sm">{context ? context.provincie:'onbekend'} </p>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </Fragment>
            
        )
    }
    const displayIcem = () => {
        return(
        <Fragment>
            <div className="card card-animate">
                <div class='row m-3'>
                
                    <h6 className="mb-3">Totaal aantal iCEM: &nbsp;</h6>
                    <div className='row g-3'>
                        <div className='col-md-2'>
                            <label for="sum_icems" class="form-label" >-SUM:&nbsp; </label>
                            <input name="sum_icems" value={sum || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>
                        <div className='col-md-2'>
                            <label class="form-label">-i:&nbsp; </label>
                            <input name="icem_i" value={iNumber.current || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>
                        <div className='col-md-2'>
                            <label class="form-label">-p:&nbsp; </label>
                            <input name="icem_p" value={pNumber.current || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>
                        <div className='col-md-2'>
                            <label class="form-label" >-e:&nbsp; </label>
                            <input name="icem_e" value={eNumber.current || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>
                        <div className='col-md-2'>
                            <label class="form-label">-f:&nbsp; </label>
                            <input name="icem_f" value={fNumber.current || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>
                        <div className='col-md-2'>
                            <label>-a:&nbsp; </label>
                            <input name="icem_a" value={aNumber.current || 0}  className='form-control form-control-sm inputCss' type="text" placeholder='0' readOnly/>
                        </div>

                    </div>
                </div>
            </div>
        </Fragment>
        )
    }
    const displayContent = () => {
        if(isLoading === null)
        {
            return 'No data found voor dit project'
        }
        else if(isLoading === true)
        {
            return <LinearProgress />
        }else{
        return(
            <>
            
            {nodata !== true ? 
           <>
            <h5 className="mb-3">Algemene projectgegevens</h5>
            <div className='row'>
                {displayHeader()}
            </div>
            <h5 className="mb-3">Icem samenvatting</h5>
            <div className='row'>
                {displayIcem()}
            </div>
            <h5 className="mb-3">Productiebon table</h5>
            <div className='row'>
                {gridComponent()}
            </div>
           </>
           :
           <>
           <p>Er is nog geen project aangemaakt</p>
           Click op <Link to="/projectaanmaken" relative="path">projectnaamaken</Link> 
           </>
            }
        </>
        )
        }
    }
    return (
        <>
            <div class="page-title-box d-sm-flex align-items-center justify-content-between">
                <h4 class="mb-sm-0">Productiebon {context  ? context.projectnr:'onbekend'}</h4>
            </div>
            {displayContent()}
        </>
        
    )
}
const getProjectItems = state => ({
    context: state.mpo.productiebondata ? state.mpo.productiebondata.data:undefined,
    // mpodata:state.mpo.dataSet
})
export default connect(getProjectItems,{get_productiebon})(Productiebonmpo)