import React,{useEffect, useMemo, useState,useRef} from 'react'
import MaterialReactTable from 'material-react-table'
import FunctieModalAanmaken from '../../components/admin/FunctieModalAanmaken'
import { connect } from 'react-redux'
import {getFuncties,getRoles} from '../../actions/admin'
import { Link } from 'react-router-dom';

const Functieoverzicht = (props) => {

    const [ShowModalCreateFunctie,setShowModalCreateFunctie] = useState(false)
    const [functies,setFuncties] = useState([])
    const [edit,setEdit] = useState(false)
    const isCliked = useRef(null)

    useEffect(() => {
        props.getRoles()
        props.getFuncties()
    },[])

   
    useEffect(() => {
        var arrayFunctie = []
        if(props.functies !== undefined && props.functies !== null)
        {
            props.functies.map((item) => {
                props.roles.map((value) => {
                    if(item[2] === value[0])
                    {
                        arrayFunctie.push({
                            'id': item[0],
                            'functie': item[1],
                            'role_name': value[1]
                        })
                    }

                })
            })
          
        }
        setFuncties(arrayFunctie)
    },[props.functies])
    const onChange = e =>setFuncties({...functies, [e.target.name]: e.target.value}) 
    
    const editEnable = (row) => {
        isCliked.current = row.row.index
        setEdit(true)
      }

      const DeleteRow = (e) => {

      } 
      const handleSaveRow = async (e) => {

      }
      const cancelEdit = () => {
        isCliked.current = false
        setEdit(false)
    
      }
    const columns = useMemo(
        () => [
            {
                accessorKey: 'functie',
                header: 'Functie naam',
                Cell: ({cell,row}) => {
                    return(
                        isCliked.current !== row.index ? 
                        <span className=''>{cell.getValue()}</span>:
                        <input className='form-control form-control-sm' name="functie" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
                    )
                }
            },
            {
                accessorKey: 'role_name',
                header: 'Role naam',
                Cell: ({cell,row}) => {
                    return(
                        isCliked.current !== row.index ? 
                        <span className=''>{cell.getValue()}</span>:
                        <input className='form-control form-control-sm' name="role_name" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
                    )
                }
            },
            {
                accessorKey: 'actions',
                header: 'Actions',
                Cell:(row) => {
                    return(<>
                    <div className="actions_buttons" ref={isCliked}>
                        {isCliked.current !== row.row.index ? 
                        
                        <>
                        <Link onClick={() => editEnable(row)} ><i class=" ri-edit-2-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                        <Link onClick={() => DeleteRow(row)}><i class=" ri-delete-bin-6-line"></i></Link>
                        </>
                        :
                        <>
                        <Link onClick={() => cancelEdit()}><i class="ri-close-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                        <Link onClick={(e) => handleSaveRow(e)}><i class=" ri-check-fill "></i></Link>
                    </>
                        
                    }
                    </div>
                    </>)
                }
            }

        ]
    )
    const excelExport = async (args) => {
        console.log(args)
      }
    console.log(functies)
  return (
    <div>
            <div class="page-title-box d-sm-flex align-items-center justify-content-between">
                <h4 class="mb-sm-0">Functie overzicht</h4>
            </div>
            <MaterialReactTable 
                columns={columns}
                data = {functies ? functies: 'Loading...'}
                enableColumnResizing
                editingMode="row"
                enableRowVirtualization
                enableRowSelection={true}
                enablePinning
                muiSelectCheckboxProps={{
                    color: 'primary',
                  }}
                 initialState={{ 
                    density: 'compact' ,
                    // columnPinning: { left: ['email'], right: ['actions'] },
        
                }}
                renderTopToolbarCustomActions={({ table }) => (
                    <>
                      <div className='row mb-3'>
                        <div className='col-xxl-6'>
                          <button className="btn btn-success btn-icon waves-effect waves-light" id="exportexelButton" type="button" onClick={() => excelExport(table.getSelectedRowModel().rows)} ><i class=" ri-file-excel-2-line"></i></button>
                        </div>
                        <div className='col-xxl-6'>
                          <button type="button" onClick={() => setShowModalCreateFunctie(true)} className="btn btn-danger btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                          {<FunctieModalAanmaken props={{'ShowModalCreateFunctie':ShowModalCreateFunctie,'setShowModalCreateFunctie':setShowModalCreateFunctie}}/> }
                        </div>
                        
                      </div>
                        
                    </>
                 )}
            />
      
    </div>
  )
}
const getAllFuncties = state => ({
    roles: state.admin.roles,
    functies: state.admin.functies,
})
export default connect(getAllFuncties,{getRoles,getFuncties})(Functieoverzicht)
