import React,{Fragment, useEffect,useRef,useState} from 'react'
import { useParams } from 'react-router-dom'
import {get_project_bewerken,update_project} from '../../actions/project'

import { connect } from 'react-redux'
import MedewerkerModal from '../../components/admin/MedewerkerModal';
import KlantMedewerkerModalEdit from '../../components/project/KlantMedewerkerModalEdit'
import BedrijfAanemerModal from '../../components/project/BedrijfAanemerModal';
import { LinearProgress } from '@material-ui/core';
import { Link, useNavigate } from 'react-router-dom';
import KlantMedewerkerModal from '../../components/project/KlantMedewerkerModal';

const Projectbewerkern = (props) => {
    let projectParam = useParams()
    let projectnummer = projectParam.projectnummer
    // const [submited_success,setSubmited_success] = useState(null)
    const validationsMessages = "colorvalidations"
   
    const [submit,setSubimt] = useState()
    const [error,setError] = useState(false)
    const [pronvincies,setPronvincies] = useState([])
    const [provinciesarray,setProvinciesarray] = useState([])
    const [onlandchange,setOnlandchange] = useState(false)

    const [showModalAanemer, setShowModalAanemer] = useState(false);
    const [showModalAanemereEdit,setshowModalAanemereEdit] = useState(false)
    const [showModalFzMedewerker,setShowModalFzMedewerker] = useState(false)
    const [showOnderaanemerModal, setShowOnderaanemerModal] = useState(false);

    // NEW STATES
    const [isLoading,setIsLoading] = useState(null)
    const [projectData,setProjectData] = useState([])
    const [data,setData] = useState([])
    const [klant,setKlant] = useState([])
    const [medewerkerKlantdata,setMedewerkerKlantdata] = useState([])
    const [functiemedewerkerklant,setFunctiemedewerkerklant] = useState([])
    const [projectleider,setProjectleider] = useState({
        'id':null,
        'voornaam':null,
        'achternaam': null,
        'phone_no':null
    })
    const [projectleiders,setProjectleiders] = useState([])
    const [werkvoorbereider,setWerkvoorbereider] = useState({
        'id':null,
        'voornaam':null,
        'achternaam': null,
        'phone_no':null
    })
    const [werkvoorbereiders,setWerkvoorbereiders] = useState([])
    const [bedrijfAanemers,setBedrijfAanemers] = useState([])
    const [inopdracht,setInopdracht] = useState(null)
    const [installatieComponenten,setInstallatieComponenten] = useState({
        
    })
    
    useEffect(() => {
        props.get_project_bewerken(projectnummer)
    },[projectnummer,showModalAanemer,showModalAanemereEdit,showModalFzMedewerker,submit])

    useEffect(() => {
        setIsLoading(true)
        if(props.context !== undefined && props.context !== null)
        {
            setProjectData(props.context.projectdata)
            setMedewerkerKlantdata(props.context.contactpersonKlant)
            setKlant(props.context.klant)
            setProjectleiders(props.context.medewerkersfz.projectleiders)
            setProjectleider(props.context.contactpersonFz.projectleiderFz)
            setWerkvoorbereider(props.context.contactpersonFz.werkvoorbereiderFz)
            setWerkvoorbereiders(props.context.medewerkersfz.werkvoorbereiders)
            setInopdracht(props.context.inOpdrachten)
            setIsLoading(false)
        }
    },[props.context,projectnummer,submit])
    
    useEffect(() => {
       
        setInstallatieComponenten({
            'inopdrachtvoor_vloerverwarming': inopdracht && inopdracht.afgiftesystem ? inopdracht.afgiftesystem.aanemer.id: null ,
            'ordernr_onderaannemer_vloerverwarming': inopdracht && inopdracht.afgiftesystem ? inopdracht.afgiftesystem.odernr: null,
            'inopdrachtvoor_ventilatieinstallatie': null,
            'ordernr_onderaannemer_ventilatieinstallatie': null,
            'inopdrachtvoor_zonnepanelen': null,
            'ordernr_onderaannemer_zonnenpanelen': null
        })
        
    },[inopdracht])

    useEffect(() => {
        if(props.bedrijfaanemers !== undefined && props.bedrijfaanemers !== null)
        {
            setBedrijfAanemers(props.bedrijfaanemers)
        }
    },[props.bedrijfaanemers])

    useEffect(() => {
        if(props.context !== undefined && props.context !== null)
        {
            setPronvincies(props.context.provincies)
        }
    },[projectData.land])

 
    const projectstatus = ['Lead','Offerte','Onderhandeling','In uitvoering','In service']

    const onChangeProjectleiderFZ = e => {
       
        if(projectleiders.length !== 0)
        {
           
            for(var i = 0; i < projectleiders.length; i++)
            {   
                if(projectleiders[i].id === parseInt(e.target.value))
                {
                    setProjectleider(projectleiders[i])    
                }
            }

            // projectleiders.map((item) => item.id === id ? item.phone_no: null)
        }
        

    }
    const onChangeWerkvoorbereider = e => {
        if(werkvoorbereiders.length !== 0)
        {
            
            for(var i = 0; i < werkvoorbereiders.length; i++)
            {   
                if(werkvoorbereiders[i].id === parseInt(e.target.value))
                {
                    setWerkvoorbereider(werkvoorbereiders[i])    
                }
            }
            // projectleiders.map((item) => item.id === id ? item.phone_no: null)
        }
    }
    const displayModalAanemer = () => {
        return(<>
        <KlantMedewerkerModalEdit props={{ 'showModalAanemer':showModalAanemereEdit, 'setShowModalAanemer':setshowModalAanemereEdit,'data': data,'functie': functiemedewerkerklant ? functiemedewerkerklant:null ,'klantId': klant ? klant.id:null,'projectid': projectnummer ? projectnummer:null }} />
    </>)
    }
    const displayModalAanemerCreate = () => {
        return(<>
            <KlantMedewerkerModal props={{ 'showModalAanemer':showModalAanemer, 'setShowModalAanemer':setShowModalAanemer,'klantId': klant ? klant.id:null,'projectid': projectnummer ? projectnummer:null }} />
        </>)
    }
    const displayModalAanemerklantperrole = (e) => {
        if(e.currentTarget.id === 'projectleiderklantmedeweker')
        {
            setData(medewerkerKlantdata.projectleiderklantmedeweker)
            setFunctiemedewerkerklant('Projectleider')
            setshowModalAanemereEdit(true)

        }
        else if(e.currentTarget.id === 'werkvoorbereiderklantmedeweker')
        {
            setData(medewerkerKlantdata.werkvoorbereiderklantmedeweker)
            setFunctiemedewerkerklant('Werk-voorbereider')
            setshowModalAanemereEdit(true)
        }
        else if(e.currentTarget.id === 'uitvoerderklantmedeweker')
        {
            setData(medewerkerKlantdata.uitvoerderklantmedeweker)
            setFunctiemedewerkerklant('Uitvoerder')
            setshowModalAanemereEdit(true)
        }
        else{
            setData(null)
            setshowModalAanemereEdit(false)
        }
       
    }

    const displayModalBedrijfaannemer = () => {
        return(<>
            <BedrijfAanemerModal props={{ 'showOnderaanemerModal':showOnderaanemerModal, 'setShowOnderaanemerModal':setShowOnderaanemerModal }} />
        </>)
    }
    const displayModalFZmedewerker = () => {
        return(<>
        <MedewerkerModal props={{ 'showModalFzMedewerker':showModalFzMedewerker, 'setShowModalFzMedewerker':setShowModalFzMedewerker,'projectid': projectnummer ? projectnummer:null }} />
    </>)
    }
    
    const returnDateformat = (e) => {
        var parts = e.split('-')
        var date = new Date(parts[2],parts[1] -1,parts[0])
        return date

    }

    let land_opties = []
    if(projectData !== undefined){
        if(projectData.land === 'Nederland'){
            land_opties.push({'label': projectData.land,'value':'nl'},{'label': 'England','value':'en'})
        }else if(projectData.land === 'England'){
            land_opties.push({'label': projectData.land,'value':'en'},{'label': 'Nederland','value':'nl'})
        }else{
            land_opties = []
        }
    }
    const setInstallatieComponentenFunctie = (e,idSelected) => {
        if(projectData.idSelected !== null || projectData.idSelected !== "Derden")
        setInstallatieComponenten({...installatieComponenten,[e.target.name]:e.target.value})
    }
    
    const renderInputsInopdrachtvoorFZVloerverwarming = (idSelected,idSelectInput) => {
        console.log(installatieComponenten)
        return(<>
            <div className='row m-3'>
                <div class="col-lg-6">
                    <select 
                        id={idSelected}
                        name={idSelected}
                        value={installatieComponenten[idSelected]}
                        className="form-select form-select-sm"
                        onChange={(e) => setInstallatieComponentenFunctie(e,idSelected)}
                    >
                        <option> - Kies contact person - </option>
                       {bedrijfAanemers !== null && bedrijfAanemers !== undefined && bedrijfAanemers.length !==0 ? bedrijfAanemers.map((e) => <option value={e[0]}>{e[1]}</option>): ''}
                        
                    </select><br></br>
                    <div class="col-lg-12">
                        <input className="form-control form-control-sm" name={idSelectInput} value={installatieComponenten[idSelectInput]} type="text" onChange={e => setInstallatieComponentenFunctie(e)}  floatlabeltype="Always" placeholder="Odernr." required={true}/>
                    </div>
                </div>
                <div className="col-lg-3">
                    <Link type="button" onClick={() => setShowOnderaanemerModal(true)} className="btn btn-light btn-icon waves-effect waves-light sm"><i className="ri-add-fill"></i></Link>
                    {displayModalBedrijfaannemer()}
                </div>
                

            </div>
        </>)

    }
    const renderInstallatieComponenten = (idSelected) => {
        

            if(idSelected === "inopdrachtvoor_vloerverwarming")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming("inopdrachtvoor_vloerverwarming","ordernr_onderaannemer_vloerverwarming")
            }
            else if(idSelected === "inopdrachtvoor_ventilatieinstallatie")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming("inopdrachtvoor_ventilatieinstallatie","ordernr_onderaannemer_ventilatieinstallatie")
            }
            else if(idSelected === "inopdrachtvoor_zonnepanelen")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming("inopdrachtvoor_zonnepanelen","ordernr_onderaannemer_zonnenpanelen")
            }
           
            
    }
  
    const onChangeData = e => {
        setProjectData({...projectData, [e.target.name]: e.target.value})   
    } 
    
    const choosedCountry = (e) => {
       
        setOnlandchange(true)
        if(e.target.value === 'nl'){
            projectData.land = 'Nederland'
            setProvinciesarray(pronvincies.nl)

        }else if(e.target.value === 'en')
        {
            projectData.land = 'England'
            setProvinciesarray(pronvincies.en)
        }else{
            if(projectData.land === 'Nederland')
            {
                setProvinciesarray(pronvincies.nl)
            }else if(projectData.land === 'England'){

                setProvinciesarray(pronvincies.en)
            }
            
        }
        // console.log(e.target.value)

    }
    const returnNumberPhone = (data,id) => {
        var phoneNumber 
        for(var i in data){
            if(data[i].id === parseInt(id)){
                phoneNumber = data[i].phone_no
            }
        }
        return phoneNumber
    
    }

    const algemeneInput = () => {
        return(
            <>
                <div className="col-xxl-6">
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Project nummer</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" id="nameInput" value={projectData.projectnr} placeholder="Enter your name" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="projectnaam" class="form-label">Project naam</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="projectnaam" onBlur={(e) => onChangeData(e)} id="projectnaam" defaultValue={projectData.projectnaam} placeholder="voorbeeld: 3 woningen Amsterdam" required />
                                {error ===true ? projectData.projectnaam.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="plaats" class="form-label">Plaats</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="plaats" onBlur={(e) => onChangeData(e)} id="plaats" defaultValue={projectData.plaats} placeholder="voorbeeld: Amsterdam" />
                                {error ===true ? projectData.plaats.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="land" class="form-label">Land</label>
                            </div>
                            <div class="col-lg-9">
                               <>
                                <select 
                                    id="land" 
                                    name="land"
                                    // readonly={edit !== true ? false: true}
                                    defaultValue={projectData.land}
                                    className="form-select form-select-sm"
                                    onChange={(e) => choosedCountry(e)}
                                >
                                    {land_opties.length !== 0 ? land_opties.map((item) => <option  key={item.value} value={item.value}>{item.label}</option>):'geen opties'}
                                </select>
                                </>
                                
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="provincies" class="form-label">Provincie</label>
                            </div>
                            <div class="col-lg-9">
                               
                                <>
                                    <select 
                                    id="provincies"
                                    name="provincie" 
                                    defaultValue={projectData.provincie}
                                    className="form-select form-select-sm"
                                    onBlur={(e) => onChangeData(e)}
                                >
                                     {onlandchange !== true ? <option>{projectData.provincie}</option> : 
                                    
                                     provinciesarray !== undefined && provinciesarray.length !== 0 ? provinciesarray.map((item) => <option key={item} value={item}>{item}</option>): <option> - Geen resultaten - </option>
                                    
                                    }
                                </select>
                                
                                </>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-xxl-6">
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="projectstatus" class="form-label">Project status</label>
                            </div>
                            <div class="col-lg-9">
                                
                                <select 
                                    id="projectstatus"
                                    name="projectStatus" 
                                    className="form-select form-select-sm"
                                    defaultValue={projectData.projectStatus}
                                    onBlur={(e) => onChangeData(e)}
                                    
                                >
                                <option> - </option>
                                {
                                    projectstatus.map((item) => <option key={item} value={item}>{item}</option>)
                                }
                                </select>
                                
                              
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedKlanten" class="form-label">Kies opdrachtgever</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" id="selectedKlanten" value={klant.klantnaam} placeholder="voorbeeld: Amsterdam" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="offertenr" class="form-label">Offertenummer</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="offertenr" onBlur={(e) => onChangeData(e)} id="offertenr" defaultValue={projectData.offertenr !==null ? projectData.offertenr:'onbekend'} placeholder="voorbeeld: GH76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="exactnr" class="form-label">Exactnummer</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="exactnr" onBlur={(e) => onChangeData(e)} id="exactnr" defaultValue={projectData.exactnr !== null ? projectData.exactnr:'onbekend'} placeholder="voorbeeld: 76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="debiteurnr" class="form-label">Debiteurnummer</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="debiteurnr" onBlur={(e) => onChangeData(e)} id="debiteurnr" defaultValue={projectData.debiteurnr !== null ? projectData.debiteurnr: 'onbekend'} placeholder="voorbeeld: 76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="renovatie_nieuwbouw_" class="form-label">Renovatie / Nieuwbouw</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="renovatie_nieuwbouw_" 
                                    defaultValue={projectData.renovatie_nieuwbouw}
                                    name='renovatie_nieuwbouw'
                                    className="form-select form-select-sm"
                                    onBlur={(e) => onChangeData(e)}
                                    

                                >
                                    <option> - Kies option - </option>
                                    <option value="Renovatie">Renovatie</option>
                                    <option value="Nieuwbouw">Nieuwbouw</option> 
                                </select>
                      
                            </div>
                        </div>
                    </div>
                    
                </div>
            </>
        )
        
    }
    
    const contactpersonenAanemer = () => {
        return(
            <>
                <div className='col-xxl-6'>
                    <div className="card card-animate">
                        
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedProjecleider" class="form-label">Projectleider</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="selectedProjecleider" onChange={(e) => onChangeData(e)} id="selectedProjecleider" value={medewerkerKlantdata && medewerkerKlantdata.projectleiderklantmedeweker ? medewerkerKlantdata.projectleiderklantmedeweker.name_medewerker + ' ' + medewerkerKlantdata.projectleiderklantmedeweker.achternaam_medewerker:'Onbekend'} required disabled/>
                         
                            </div>
                            <div className='col-lg-3'>
                                {medewerkerKlantdata.projectleiderklantmedeweker !== null ? 
                                <>
                                 <button type="button" id='projectleiderklantmedeweker' onClick={(e) => displayModalAanemerklantperrole(e)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-edit-2-fill"></i></button>
                                    {displayModalAanemer()}  
                                </>
                                   
                                :
                                <>
                                    <button type="button" id='projectleiderklantmedeweker' onClick={() => setShowModalAanemer(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                                    {displayModalAanemerCreate()}  
                                
                                </>
                                }
                                 
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedwerkvoorbereider" class="form-label">Werkvoorbereider</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="selectedwerkvoorbereider" onChange={(e) => onChangeData(e)} id="selectedwerkvoorbereider" value={medewerkerKlantdata && medewerkerKlantdata.werkvoorbereiderklantmedeweker ? medewerkerKlantdata.werkvoorbereiderklantmedeweker.name_medewerker + ' ' + medewerkerKlantdata.werkvoorbereiderklantmedeweker.achternaam_medewerker:'Onbekend'} required disabled/>
                            </div>
                            <div className='col-lg-3'>
                                {medewerkerKlantdata.werkvoorbereiderklantmedeweker !== null ? 
                                <>
                                    <button type="button" id='werkvoorbereiderklantmedeweker' onClick={(e) => displayModalAanemerklantperrole(e)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-edit-2-fill "></i></button>
                                    {displayModalAanemer()}   
                                
                                </>:
                                <>
                                    <button type="button" id='werkvoorbereiderklantmedeweker' onClick={() => setShowModalAanemer(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill "></i></button>
                                    {displayModalAanemerCreate()} 
                                </>
                            
                                }
                                
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedUivoerder" class="form-label">Uitvoerder</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="selectedUivoerder" onChange={(e) => onChangeData(e)} id="selectedUivoerder" value={medewerkerKlantdata && medewerkerKlantdata.uitvoerderklantmedeweker ?  medewerkerKlantdata.uitvoerderklantmedeweker.name_medewerker + ' ' + medewerkerKlantdata.uitvoerderklantmedeweker.achternaam_medewerker:'Onbekend'}  required disabled/>
                            </div>
                            <div className='col-lg-3'>
                                {medewerkerKlantdata.uitvoerderklantmedeweker !== null ? 
                                <>
                                 <button type="button" id='uitvoerderklantmedeweker' onClick={(e) => displayModalAanemerklantperrole(e)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-edit-2-fill"></i></button>
                                 {displayModalAanemer()}   
                                </>
                                :
                                <>
                                 <button type="button" id='uitvoerderklantmedeweker' onClick={() => setShowModalAanemer(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                                 {displayModalAanemerCreate()}   
                                </>
                                
                            
                                }
                               
                            </div>
                        </div>
                    </div>
                </div>
                <div className='col-xxl-6'>
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="phone" class="form-label">Telefoon projectleider</label>
                            </div>
                            <div class="col-lg-9">
                    
                                <input type="text" class="form-control form-control-sm" name='phone'  id="phone" value={medewerkerKlantdata && medewerkerKlantdata.projectleiderklantmedeweker ? '+' +  medewerkerKlantdata.projectleiderklantmedeweker.phone:'Onbekend'} placeholder="" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Telefoon werkvoorbereider</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm"  id="nameInput"  value={medewerkerKlantdata && medewerkerKlantdata.werkvoorbereiderklantmedeweker ? medewerkerKlantdata.werkvoorbereiderklantmedeweker.phone:'Onbekend'} placeholder="" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Telefoon uitvoerder</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm"  id="nameInput"  value={medewerkerKlantdata && medewerkerKlantdata.uitvoerderklantmedeweker ?  medewerkerKlantdata.uitvoerderklantmedeweker.phone:'onbekend'} placeholder="" disabled/>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
   
    const contactpersonenFZ = () => {
        return(
        <>
            <div className='col-xxl-6'>
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="projectleiderFz" class="form-label">Projectleider</label>
                        </div>
                        <div class="col-lg-6">
                            
                            {/* <input type="text" class="form-control form-control-sm" defaultValue={data.projectleiderFZname} disabled/> */}
                            <select 
                                id="projectleiderFZname"
                                name='id'
                                value={projectleider && projectleider !== null ? projectleider.id: null}
                                className="form-select form-select-sm"
                                onChange={(e) => onChangeProjectleiderFZ(e)}
                            >
                                <option> - Kies projecleider - </option>
                                {projectleiders !== undefined && projectleiders !== null && projectleiders.length !== 0 ? 
                                    projectleiders.map((item) => <option key={item.id} value={item.id}>{item.voornaam + ' ' + item.achternaam}</option>): <option>Geen gegevens</option>}
                                
                            </select>
                            
                        </div>
                        <div className="col-lg-3">

                            <button type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                            {displayModalFZmedewerker()}
                           
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="selectedProjecleiderFz" class="form-label">Werkvoorbereider</label>
                        </div>
                        <div class="col-lg-6">
                            {/* <input type="text" class="form-control form-control-sm" defaultValue={data.werkvoorbereiderFZname} disabled/> */}
                            <select 
                                id="werkvoorbereiderFZname"
                                name='id'
                                defaultValue={werkvoorbereider && werkvoorbereider !== null ? werkvoorbereider.id: null}
                                className="form-select form-select-sm"
                                onChange={(e) => onChangeWerkvoorbereider(e)}
                            >
                                <option> - Kies werkvoorbereider - </option>
                                { werkvoorbereiders !== undefined && werkvoorbereiders !== null && werkvoorbereiders.lengh !== 0 ? 
                                    werkvoorbereiders.map((item) => <option key={item.id} value={item.id}>{item.voornaam + ' ' + item.achternaam}</option>): <option>Geen gegevens</option>}
                  
                            </select>
                        </div>
                        <div className="col-lg-3">
                            <button type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                            {displayModalFZmedewerker()}
                        </div>
                    </div>
                </div> 
            </div>
            <div className='col-xxl-6'>
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Telefoon projectleider</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm"  id="nameInput" value={projectleider !== null ? projectleider.phone_no: 'Onbekend'} placeholder="" disabled/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Telefoon Werkvoorbereider</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm"  id="nameInput" value={werkvoorbereider !== null ? werkvoorbereider.phone_no: 'Onbekend'} placeholder="" disabled/>
                        </div>
                    </div>
                </div>
            </div>
        </>
        )
    }

    const installationComponenten = () => {
        return(
        <>
            
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Afgifte systeem</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtAfgifteSystem"
                                    name='inopdrachtvoor_vloerverwarming'
                                    defaultValue={projectData ? projectData.inopdrachtvoor_vloerverwarming: null}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {projectData && projectData.inopdrachtvoor_vloerverwarming === "FZ" ? <>{renderInstallatieComponenten("inopdrachtvoor_vloerverwarming")}</> :null}
                               
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Ventilatieinstallatie</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtvoorventillatie"
                                    name='inopdrachtvoor_ventilatieinstallatie'
                                    defaultValue={projectData ? projectData.inopdrachtvoor_ventilatieinstallatie: null}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {projectData && projectData.inopdrachtvoor_ventilatieinstallatie === "FZ" ? <>{renderInstallatieComponenten("inopdrachtvoor_ventilatieinstallatie")}</>: null}
                               
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Zonnepanelen</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtvoorzonnepanelen"
                                    name='inopdrachvoor_zonnepanelen'
                                    defaultValue={projectData ? projectData.inopdrachvoor_zonnepanelen: null}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {projectData && projectData.inopdrachvoor_zonnepanelen === "FZ" ? <>{renderInstallatieComponenten("inopdrachvoor_zonnepanelen")}</>:null}
                                {/* {renderInstallatieComponenten(inopdrachtvoor_,"inopdrachtvoor_")} */}
                            </div>
                        </div>
                    </div>    
                </div>
            </div>
            

        </>
        )
    }

    const datumsenopmerkingen = () => {
        return(
        <>
            <div className="col-xxl-6">
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Datum systeem invoer</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm" data-provider="flatpickr"  name='datumSystemInvoer' value={projectData ? projectData.datumSystemInvoer:''}  placeholder="" disabled/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Start datum</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="startDatum" value={projectData ? projectData.startDatum:''} onChange={e => onChangeData(e)}  placeholder="" />
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Offerte datum</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="offertedatum" value={projectData ? projectData.offertedatum: ''} onChange={e => onChangeData(e)} placeholder="offerteDatum"/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Uitlijk datum opdracht indien WTW</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="uitlijkDatumOpdrachtIndienWTW" value={projectData ? projectData.uitlijkDatumOpdrachtIndienWTW:''} onChange={e => onChangeData(e)}  placeholder="Uitlijk datum opdracht indien WTW"/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Uitlijk datum opdracht alleen Icem</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="uitlijkDatumOpdrachtAlleenICEM" value={projectData ? projectData.uitlijkDatumOpdrachtAlleenICEM:''} onChange={e => onChangeData(e)}  placeholder="Uitlijk datum opdracht alleen Icem"/>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-xxl-6">
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="textarea" className='form-label'>Opmerking</label>
                        </div>
                        <div class="col-lg-9">
                            <textarea className='form-control' id="textarea" name="opmerking" defaultValue={projectData ? projectData.opmerking: ''}  onBlur={e => onChangeData(e)}></textarea>
                        </div>
                    </div>
                </div>
            </div>
        </>
        )
    }
    function checkifaanemerIsderden(string,param1){
        var data 
        console.log(string)
        if(string === 'FZ')
        {
            data = param1

        }else{
            data = null
        }
        return data
    }
    const checkifMedewerkerFZisnull = (medewerker) => {
        
        if(medewerker !== null && medewerker.id !== null)
        {
            return medewerker.id

        }else{
            return null
        }
    }   
    const submitForm = (e) => {
        e.preventDefault()
        projectData.selectedProjecleiderFz = checkifMedewerkerFZisnull(projectleider)
        projectData.selectedWerkvoorbereiderFz = checkifMedewerkerFZisnull(werkvoorbereider)

        installatieComponenten.inopdrachtvoor_vloerverwarming = checkifaanemerIsderden(projectData.inopdrachtvoor_vloerverwarming,installatieComponenten.inopdrachtvoor_vloerverwarming)
        installatieComponenten.inopdrachtvoor_ventilatieinstallatie = checkifaanemerIsderden(projectData.inopdrachtvoor_ventilatieinstallatie,installatieComponenten.inopdrachtvoor_ventilatieinstallatie)
        installatieComponenten.inopdrachtvoor_zonnepanelen = checkifaanemerIsderden(projectData.inopdrachtvoor_zonnepanelen,installatieComponenten.inopdrachtvoor_zonnepanelen)

        var formData = {
            'project_data' : projectData,
            'aanemers_data': installatieComponenten

        }
        if(props.update_project(JSON.stringify(formData)))
        {
            setSubimt(true)
            setError(false)
        }
        setSubimt(false)
       
        
    }
    const navigate = useNavigate()
    const submitCancel = (e) => {
        e.preventDefault()
        navigate('/projectenlist')

    }
    const displayData = () => {
        if(isLoading === null)
        {
            return 'No data found voor dit project'
        }
        else if(isLoading === true)
        {
            return <LinearProgress />
        }else{
            return (
                <>
                <h5 className="mb-3">Algemene projectgegevens</h5>
                    <div class='row'> 
                        { algemeneInput() }
                    </div>
                    <h5 className="mb-3">Contactpersonen aanemer</h5>
                    <div className="row">
                        { contactpersonenAanemer() }
                    </div>
                    <h5 className="mb-3">Contactpersonen Factory zero</h5>
                    <div className="row">
                        { contactpersonenFZ() }
                    </div>
                    <h5 className="mb-3">Installations componenten</h5>
                    <div className="row">
                        { installationComponenten() }
                    </div>
                    <h5 className='mb-3'>Datum en opmerking </h5>
                    <div className='row'>
                        { datumsenopmerkingen()}
                    </div>
                    <div className="row mb-3">
                        <div className="col-lg-6">
                            <button type="button" onClick={(e) => submitForm(e)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                        </div>
                        <div className="col-lg-6">
                            <button type="button" onClick={(e) => submitCancel(e)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Cancel</button>
                        </div>
                    </div>
                </>

            )
 
        }
    }
  return (
    <>
        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
            <h4 class="mb-sm-0">Project bewerken {projectData !== undefined && projectData !== null ? projectData.projectnr: ''}</h4>
        </div>
        {displayData()}
    </>
  )
}

const getProjectItems = state => ({
    context: state.project.bewerkendata,
    medewerkers: state.medewerkers.context,
    bedrijfaanemers: state.project.data,
    // provincies: state.project.provincies
})

export default connect(getProjectItems,{get_project_bewerken,update_project})(Projectbewerkern)