import {
    AANMAKEN_OPLEVERRAPPORT_SUCCESS,
    AANMAKEN_OPLEVERRAPPORT_FAIL,
    LOAD_OPLEVERRAPPORT_SUCCESS,
    LOAD_OPLEVERRAPPORT_FAIL,
    LOAD_PRODUCTIESTATUSICEM_SUCCESS ,
    LOAD_PRODUCTIESTATUSICEM_FAIL

} from '../actions/types'

const initialState = {
    data:[]                                                                                          
 }

 export default function(state= initialState, action){
    const { type, payload } = action;
    switch(type){
        case LOAD_PRODUCTIESTATUSICEM_SUCCESS:
        case LOAD_OPLEVERRAPPORT_SUCCESS:
        case AANMAKEN_OPLEVERRAPPORT_SUCCESS:
            return {...state,
            data: payload.data
            	}
        case LOAD_PRODUCTIESTATUSICEM_FAIL:
        case LOAD_OPLEVERRAPPORT_FAIL:
        case AANMAKEN_OPLEVERRAPPORT_FAIL:
            return {
            data: payload.data
                }
                    
            default:
                return state
    }
 }