import React,{useState,useEffect} from 'react'
import $ from 'jquery'
const Energiebundel = (props) => {
    const [activeIndex, setActiveIndex] = useState(-1);
    const [numRows,setnumRows] = useState(0)
    const [bewerken_actif,setbewerkenactif] = useState(false)
    const thead = ['Bouwnr','Blok']
    const numCols = thead.length
    useEffect(() => {
        if(props !== undefined)
        {    
            setnumRows(props.props.row)
            setbewerkenactif(props.props.bewerken_actif)  
        }
    },[props])
    let thead_view = []

    const valuesField = (cell,i) => {
        return(
            <>
             <td key={cell[0].index} id={cell[0].cellId}>
                <div className={`tile ${activeIndex === cell[0].index ? "active" : ""}`}>
                  <input
                      name={cell[0].idx*numRows + i}
                      value={i+1 || ''}
                      className="form-control form-control-sm"
                    //   onFocus={() => setActiveIndex(cell[0].index)}
                    //   ref={(el) => (inputRefs.current[cell[0].index] = el)}
                      readOnly
                  />
                  </div>
              </td>
              <td key={cell[1].index} id={cell[1].cellId}>
                  <div className={`tile ${activeIndex === cell[1].index ? "active" : ""}`}>
                  <input
                      name={cell[1].idx*numRows + i}
                      value={ props.props.blok !== undefined && props.props.blok.length !== 0 ? props.props.blok[i] : ''}
                      className="form-control form-control-sm"
                    //   onFocus={() => setActiveIndex(cell[1].index)}
                    //   ref={(el) => (inputRefs.current[cell[1].index] = el)}
                      readOnly
                  />
                  </div>
              </td>
          
            </>
        )
    }
    for(const i in thead){
        thead_view.push(
            <th scope="col" key={i} id={thead[i]}>{thead[i]}</th>
        )   
        }
    
        let rows = []
        for (var i = 0; i < numRows; i++) {
        let rowID = `row${i}`;
    
        let cell = [];
        
        for (var idx = 0; idx < numCols; idx++) {
            let cellID = `cell${i}-${idx}`;
            const index = i * numCols + idx;
            cell.push(
                {'index':index,'cellId':cellID,'idx':idx}
            )
        }
        
        rows.push(
            <tr  key={i} id={rowID}>
            {valuesField(cell,i)}
            </tr>
              
            );
          }
          useEffect(() => {
            $(function () {
              // $("#table4").enableCellNavigation();
              // $("#table").DataTable();
            });
        
           })

  return (
    <div>
         {/* <table className="table table-hover table-nowrap mb-0" id="table4">
            <thead>
                <tr>
                    {thead_view}
                </tr>
                
            </thead>
   
          <tbody>{rows}</tbody>
          
        </table> */}
    </div>
  )
}

export default Energiebundel