import {
    AANMAKEN_MPO_SUCCESS,
    AANMAKEN_MPO_FAIL,
    LOAD_MPO_SUCCESS,
    LOAD_MPO_FAIL,
    AANMAKEN_PRODUCTIESTATUS_SUCCESS,
    AANMAKEN_PRODUCTIESTATUS_FAIL,
    LOAD_PRODUCTIESTATUS_SUCCESS,
    LOAD_PRODUCTIESTATUS_FAIL,
    UPDATE_PRODUCTIESTATUS_SUCCESS,
    UPDATE_PRODUCTIESTATUS_FAIL,
    LOAD_SITESPROJECT_SUCCESS,
    LOAD_SITESPROJECT_FAIL,
    LOAD_PRODUCTIEBON_FAIL,
    LOAD_PRODUCTIEBON_SUCCESS,
    LOAD_PRODUCTIEDATATOPRINT_SUCCESS,
    LOAD_PRODUCTIEDATATOPRINT_FAIL
 } from '../actions/types'

 const initialState = {
    is_success:'',
    dataSet:[],
    data:[],
    sites: [],
    productiebondata: [],
    datatoprint: null                                                                                     
 }

 export default function(state= initialState, action){
    const { type, payload } = action;

    switch(type){
        case LOAD_SITESPROJECT_SUCCESS:
            return {
                ...state,
                sites: payload
            }
        case LOAD_PRODUCTIEBON_SUCCESS:
            return {
                ...state,
                productiebondata: payload
            }
        case LOAD_PRODUCTIEDATATOPRINT_SUCCESS:
            return {
                ...state,
                datatoprint: payload.data
            }
        case LOAD_PRODUCTIESTATUS_SUCCESS:
            return {...state,
            data: payload.data
            }
        case LOAD_MPO_SUCCESS:
            return{
                ...state,
                dataSet: payload.data
 
            }
        
        case UPDATE_PRODUCTIESTATUS_SUCCESS:
        case AANMAKEN_PRODUCTIESTATUS_SUCCESS:
        case AANMAKEN_MPO_SUCCESS:
           return{
               ...state,
               is_success: payload.success,
               dataSet: payload.data

           }
        case LOAD_PRODUCTIEDATATOPRINT_FAIL:
        case LOAD_PRODUCTIEBON_FAIL:
        case LOAD_SITESPROJECT_FAIL:
        case UPDATE_PRODUCTIESTATUS_FAIL:
        case LOAD_PRODUCTIESTATUS_FAIL:
        case AANMAKEN_PRODUCTIESTATUS_FAIL:
        case LOAD_MPO_FAIL:
        case AANMAKEN_MPO_FAIL:
           return {
               is_success: payload
           }
           default:
            return state
    };
}