import React, { useState, useEffect,useRef, useCallback,useMemo } from 'react'
import {load_medewerkers,medewerker_updated} from '../../actions/medewerker'
import MaterialReactTable from 'material-react-table'
import { Link } from 'react-router-dom';
import { Delete, Edit } from '@mui/icons-material';
import MedewerkerModal from '../../components/admin/MedewerkerModal'
import { connect } from 'react-redux'

const Medewerkeroverzicht = (props) => {
 
    const [showModalFzMedewerker,setShowModalFzMedewerker] = useState(false)
    const [tableData, setTableData] = useState([])
    
    const isCliked = useRef(null)
    const [edit,setEdit] = useState(null)
    const [roles,setRoles] = useState([])
    const [functies,setFuncties] = useState([])
    const [bewerktData,setBewerktData] = useState([])
    useEffect(() => {
      props.load_medewerkers()
     
    },[])

    useEffect(() => {
      if(props.context !== undefined)
      {
        setTableData(props.context)
      }
    },[props.context])
    
    
    const displayModalFZmedewerker = () => {
      return (<><MedewerkerModal props={{'showModalFzMedewerker':showModalFzMedewerker, 'setShowModalFzMedewerker':setShowModalFzMedewerker}} /></>)
    }
    
    const return_role_id = (args) => {
      for (let i = 0; i < roles.length; i++) {
        if(args === roles[i][1])
        {
          return roles[i][0]
        }
      }
    }
    const onchange_role = (args) => {
      
      var functies_ = []

      if(props.functies !== undefined)
      {
        for (let i = 0; i < props.functies.length; i++) {

          var roleid = return_role_id(args)
          bewerktData.rol_name = args
          if(roleid === props.functies[i][2])
          {
            functies_.push({'functienaam':props.functies[i][1],'id':props.functies[i][0]})
          }
         
        }

      } 
      setFuncties(functies_)
      
    }
    const excelExport = async (args) => {
      console.log(args)
    }
    
    const DeleteRow = (e) => {

    }
    const cancelEdit = () => {
      isCliked.current = null
      setTableData(props.context)
      setEdit(null) 

    }
    const return_functie_array = (e) => {
      var functie_ = []
      props.functies.map((value) => {
        
        if(value[2] === e)
        {
          functie_.push({'id': value[0],'functie': value[1]})
          
        }
      })
      return functie_
    }
    const return_functie_id_value = (functie_,e) => {
        var id
        functie_.map((item) => {
          if(item.functie === e)
          {
            id = item.id
            
          }
        })
        return id
    }
    const return_functie_id = (e) => {
      var id
      roles.map((item) => {
       
        if(bewerktData.rol_name === item[1])
        {
          var functie_array = return_functie_array(item[0])
          id = return_functie_id_value(functie_array,e)   
        }
      })
     
      return id
    }
    const onChange = e =>setBewerktData({...bewerktData, [e.target.name]: e.target.value}) 

    const handleSaveRow = async (e) => {
      e.preventDefault()
      var id_functie = return_functie_id(bewerktData.functie)
      bewerktData.functie = id_functie
      if(bewerktData.functie)
      {
        if(props.medewerker_updated(bewerktData))
        {
          console.log(bewerktData)
          console.log('updated')
          isCliked.current = null

        }else{
          console.log('something went wrong')
        }

      }else{
        bewerktData.functie = id_functie
        console.log('functie niet beschikbaar')
      }
      
    }
    const editEnable = (row) => {
      // console.log(row.row.original)
      setBewerktData(row.row.original)
      setEdit(true)
      setRoles(props ? props.roles: 'geen roles')
      isCliked.current = row.row.index
      // console.log(row)
    }
    const handleDeleteRow = useCallback(
      (row) => {
        if (
          alert(`Are you sure you want to delete ${row.getValue('voornaam')}`)
        ) {
          return;
        }
        //send api delete request here, then refetch or update local table data for re-render
        tableData.splice(row.index, 1);
        setTableData([...tableData]);
      },
      [tableData],
    )
    const columns = useMemo(
      () => [
        {
          accessorKey: 'geslacht',
          header: 'Aanhef',
          Cell: ({cell,row}) => {
            return(
               isCliked.current !== row.index ? 
              <span className=''>{cell.getValue()}</span>:
              <select
              name='geslacht'
              className="form-select form-select-sm"
              defaultValue={cell.getValue()}
              onBlur={(e) => onChange(e)}
              >
                <option value={"Dhr"}>Dhr</option>
                <option value={"Mevr"}>Mevr</option>
              </select>
            )

          }
        },
        {
          accessorKey: 'voornaam',
          header: 'Voornaam',
          Cell: ({cell,row}) => {
            return(
              isCliked.current !== row.index ? <span >{cell.getValue()}</span> : <input className='form-control form-control-sm' name="voornaam" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
            )
          }
        },
        {
          accessorKey: 'tussenvoegsel',
          header: 'Tussenvoegsel',
          Cell: ({cell,row}) => {
            return(
              isCliked.current !== row.index ? <span >{cell.getValue()}</span>
               : 
               <input type='text' className='form-control form-control-sm' name="tussenvoegsel" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
            )
          }
        },
        {
          accessorKey: 'achternaam',
          header: 'Achternaam',
          Cell: ({cell,row}) => {
            return(
              isCliked.current !== row.index ? <span >{cell.getValue()}</span>
               : 
               <input type="text" className='form-control form-control-sm' name="achternaam" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
            )
          }
          
        },
        {
          accessorKey: 'rol_name',
          header: 'Afdeling',
          Cell:({cell,row}) => {
            return(
              isCliked.current !== row.index ?
              <span >{cell.getValue()}</span>
              :
              <select
              name='rol_name'
              defaultValue={cell.getValue()}
              className="form-select form-select-sm"
              onBlur={(e) => onchange_role(e.target.value)}
              >
                {roles.length !== 0 && roles !== null? roles.map((item) => <option key={item[0]} value={item[1]}>{item[1]}</option>) :'geen roles'}
              </select>
            )
          }
        },
        {
          accessorKey: 'functie',
          header: 'Functie',
          Cell:({cell,row}) => {
            return(
              isCliked.current !== row.index ? <span>{cell.getValue()}</span>
              :
              <select 
              name='functie'
              defaultValue={cell.getValue()}
              className="form-select form-select-sm"
              onBlur={(e) => onChange(e)}
              >
                {functies.length !==0 ? functies.map((item) => <option key={item.id} value={item.functienaam}>{item.functienaam}</option>):<option>geen functie beschikbaar</option>}
              </select>
            )
          }
        },
       
        {
          accessorKey: 'phone_no',
          header: 'Telefoon',
          Cell: ({cell,row}) => {
            return(
              isCliked.current !== row.index ? <span >{cell.getValue()}</span>
               : 
               <input type='phone' className='form-control form-control-sm' name="phone_no" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
            )
          }
        },
        {
          accessorKey: 'email',
          header: 'E-mail'
        },
        {
          accessorKey: 'status',
          header: 'Status',
          Cell:({cell,row}) => {
            return (
              isCliked.current !== row.index ? 
              cell.getValue() === true ? <span>Actif</span> : <span>Inactif</span>:
              <select
              name='status'
              defaultValue={cell.getValue() === true ? <span>Actif</span> : <span>Inactif</span>}
              className="form-select form-select-sm"
              onBlur={(e) => onChange(e)}
              >
                <option value={1}>Actif</option>
                <option value={0}>Inactif</option>
              </select>
              )
          }
        },
        {
          accessorKey:'actions',
          header: 'Actions',
          Cell:(row) => {
            return(
            <>
              <div className="actions_buttons" ref={isCliked}>
                {isCliked.current !== row.row.index ? 
                
                 <>
                  <Link onClick={() => editEnable(row)} ><i class=" ri-edit-2-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                  <Link onClick={() => DeleteRow(row)}><i class=" ri-delete-bin-6-line"></i></Link>
                </>
                :
                <>
                  <Link onClick={() => cancelEdit()}><i class="ri-close-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                  <Link onClick={(e) => handleSaveRow(e)}><i class=" ri-check-fill "></i></Link>
              </>
                
              }
              </div>
            </>
            )
          }

        }

      ] 
    )
  return (
    <div>
      <div class="page-title-box d-sm-flex align-items-center justify-content-between">
          <h4 class="mb-sm-0">Medewerker overzicht</h4>
      </div>
      <MaterialReactTable
        columns={columns}
        data = {tableData ? tableData: 'Loading...'}
        enableColumnResizing
        // enableEditing
         editingMode="row"
         enableRowVirtualization
         enableRowSelection={true}
        //  enableEditing = {edit ===true ? true:false}
         muiSelectCheckboxProps={{
            color: 'primary',
          }}
         initialState={{ 
            density: 'compact' ,
            columnPinning: { right: ['actions'] },

        }}
         enablePinning

         renderTopToolbarCustomActions={({ table }) => (
            <>
              <div className='row mb-3'>
                <div className='col-xxl-6'>
                  <button className="btn btn-success btn-icon waves-effect waves-light" id="exportexelButton" type="button" onClick={() => excelExport(table.getSelectedRowModel().rows)} ><i class=" ri-file-excel-2-line"></i></button>
                </div>
                <div className='col-xxl-6'>
                  <button type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-danger btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                  {displayModalFZmedewerker()}
                </div>
                
              </div>
                
            </>
         )}
      />

    </div>
  )
}
const getAllmedewerkers = state => ({
  context: state.medewerkers.context,
  roles: state.admin.roles,
  functies: state.admin.functies,
})

export default connect(getAllmedewerkers,{load_medewerkers,medewerker_updated})(Medewerkeroverzicht)