import React,{useState} from 'react'
import Modal from 'react-bootstrap/Modal'
import {create_role} from '../../actions/admin'
import { connect } from 'react-redux'

const RoleModalAanmaken = (props) => {
    const [role,setRole] = useState({'role_name':null})
    const [error,setError] = useState(false)
    const validationsMessages = "colorvalidations"
    const submitRole = (e) => {
        e.preventDefault()
        if(role.role_name !== null && role.role_name !== '')
        {
            if(props.create_role(JSON.stringify(role)))
            {
                console.log('role succesfuly aangemaakt')
            }else{
                console.log('wij hebben hier probleem')
            }
            setError(false)
            props.props.setShowModalCreateRole(false)

        }else{
            setError(true)
        }
    }
  return (
    <div>
        <Modal
        show={props.props.ShowModalCreateRole}
        onHide={() => props.props.setShowModalCreateRole(false)}
        animation={true}
        centered={true}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
    >   
        <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
                Role aanmaken
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
        <div className="row m-3">
            <div className="col-lg-3">
                <label for="role" className="form-label">Role</label>    
            </div>
            <div className="col-lg-9">
                <input type="text" name="role_name" value={role.role_name}   onChange={(e) => setRole({...role,[e.target.name]:e.target.value})} className="form-control form-control-sm" id="role" placeholder="Voorbeeld: Doe" required/>
                {error ===true ? role.role_name !== null && role.role_name !== '' ? null: <span className={validationsMessages}>role moet worden ingevuld</span> :null}
            </div>
        </div>
        <div class="text-end">
            <div class="row m-3">
                <div className='col'>
                    <button type="button" onClick={(e) => submitRole(e)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                </div>
                <div className='col'>
                    <button type="button" onClick={() => props.props.setShowModalCreateRole(false)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                </div>
            </div> 
        </div>
        </Modal.Body>
    </Modal>

    </div>
  )
}

export default connect(null,{create_role})(RoleModalAanmaken)