import React, { useState } from 'react'
import {bedrijf_aanemer_bedrijf_aanmaken} from '../../actions/project'
import Modal from 'react-bootstrap/Modal'
import { connect } from 'react-redux'

const BedrijfAanemerModal = (props) => {
    // const objectBefrijdaanemer = {name:''}
    const [formBedrijfaanemer,setFormBedrijfaanemer] = useState({naam:''})
    // const {naam} = formBedrijfaanemer
    const [error,setError] = useState(false)
    const validationsMessages = "colorvalidations"
    const onChangeBedrijfaanemer  = e => setFormBedrijfaanemer({...formBedrijfaanemer, [e.target.name]: e.target.value})
    const submitFormBedrijfaanemer = (e) => {
        e.preventDefault()
        if(formBedrijfaanemer.naam.trim())
        {
            // console.log(formBedrijfaanemer)
            props.bedrijf_aanemer_bedrijf_aanmaken(JSON.stringify(formBedrijfaanemer))
            setFormBedrijfaanemer({naam:''})
            setError(false)
            props.props.setShowOnderaanemerModal(false)
        }else{
            console.log(formBedrijfaanemer)
            setError(true)
        }
        
        
     }
     console.log(error)
    return (
    <>
        <Modal
        show={props.props.showOnderaanemerModal}
        onHide={() => props.props.setShowOnderaanemerModal(false)}
        animation={true}
        centered={true}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
    >
        <Modal.Header closeButton>
        <Modal.Title id="example-custom-modal-styling-title">
            Bedfrijf onderaanemer
        </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <div className='card card-animate'>
                <form action="" novalidate>
                    <div className="row m-3">
                        <div className="col-lg-3">
                            <label for="nameInput" className="form-label">Naam</label>
                            
                        </div>
                        <div className="col-lg-9">
                            <input type="text" name="naam" defaultValue={formBedrijfaanemer.naam} onBlur={(e) => onChangeBedrijfaanemer(e)} className="form-control form-control-sm" id="nameInput" placeholder="Voorbeeld: afgite bedrijf" required/>
                            {error ? <span className={validationsMessages}>Veld moet worden ingevuld</span> : null}
                        </div>
                    </div>
                    <div class="text-end">
                        <div class="row m-3">
                            <div className='col'>
                                <button type="button" onClick={(e) => submitFormBedrijfaanemer(e)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                            </div>
                            <div className='col'>
                                <button type="button" onClick={() => props.props.setShowOnderaanemerModal(false)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                            </div>
                        </div>
                        
                        
                    </div>
                </form>
            </div>
            
        </Modal.Body>
        </Modal>
    </>
  )
}


export default connect(null,{bedrijf_aanemer_bedrijf_aanmaken})(BedrijfAanemerModal)
