import React from 'react'
import { useParams } from 'react-router-dom'
import TestrapportAanmaken from '../containers/Productie/TestrapportAanmaken.js'
import OpleverrapportAanmaken from '../containers/Productie/OpleverrapportAanmaken.js'
import ProjectAanmaken from '../containers/Projecten/ProjectAanmaken.js'

const DynamicSubComponents = () => {
    const { name , id, site} = useParams()

    switch (name) {
        case 'testrapport':
           return <TestrapportAanmaken />
        case 'opleverrapport':
            return  <OpleverrapportAanmaken />
        default:
            return null
    }
//   return (
//     <div>DynamicSubComponents</div>
//   )
}

export default DynamicSubComponents