import React,{useState,useEffect, useRef,useMemo} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import $ from 'jquery'

const Icemdebieten = (props) => {
   
    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true);
   
    useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)
    },[props.props.data])
    
    const updateData = index => e => {
    
      let newArray = [...data];
      newArray[index][e.target.name] = e.target.value 
      props.props.setData(newArray)
      
    }
    const columns = useMemo(
        () => 
        [
            {
              accessorKey: 'bouwnr',
              header: "Bouwnr",
            },
            {
                accessorKey: 'blok',
                header: "Blok",
            },
            {
                accessorKey: 'stand1',
                header: "Stand 1",
                Cell: ({ cell,row }) => 
                props.props.bewerken_actif === true ?
                
                  <input type='text' maxlength="5" style={{textTransform:"uppercase"}} name="stand1" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
                  :
                  cell.getValue() ? cell.getValue():'' 
              },
              {
                accessorKey: 'stand2',
                header: "Stand 2",
                Cell: ({ cell,row }) => 
                props.props.bewerken_actif === true ?
                <input type='text' maxlength="5" style={{textTransform:"uppercase"}} name="stand2" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
                :
                cell.getValue() ? cell.getValue():''
                
              },
              {
                accessorKey: 'stand3',
                header: "Stand 3",
                Cell: ({ cell,row }) => 
                props.props.bewerken_actif === true ?
                <input type='text' maxlength="5" style={{textTransform:"uppercase"}} name="stand3" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
                :
                cell.getValue() ? cell.getValue():''
              },
              {
                accessorKey: 'stand4',
                header: "Stand 4",
                Cell: ({ cell,row }) => 
                props.props.bewerken_actif === true ?

                <input type='text' maxlength="5" style={{textTransform:"uppercase"}} name="stand4" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>
                :
                cell.getValue() ? cell.getValue():'' 
              },
              {
                accessorKey: 'stand5',
                header: "Stand 5",
                Cell: ({ cell,row }) => 
                props.props.bewerken_actif === true ?

                <input type='text' maxlength="5" style={{textTransform:"uppercase"}} name="stand5" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
                :
                cell.getValue() ? cell.getValue():'' 
              },
        ],[data,props.props.bewerken_actif,updateData])

  return (
      isLoading ? <LinearProgress />:
         <MaterialReactTable
            columns={columns}
            data={data ? data: []}
            enableColumnResizing
            enablePinning
            initialState={{ 
            density: 'compact' ,
            columnPinning: { left: ['bouwnr'] },
        }}
        enableRowVirtualization
        muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
        
       />
  )
}

export default Icemdebieten
