import React,{useEffect, useState} from 'react'
import Modal from 'react-bootstrap/Modal'
import {create_functie} from '../../actions/admin'
import { connect } from 'react-redux'
const FunctieModalAanmaken = (props) => {

    const [error,setError] = useState(false)
    const validationsMessages = "colorvalidations"
    const [ roles,setRoles] = useState([])
    const [functie,setFunctie] = useState({
        'functie': null,
        'rol_id': null
    })
    const onChangeRole = e => setFunctie({...functie,[e.target.name]: e.target.value})
    useEffect(() => {
        if(props.roles !== undefined)
        {
            setRoles(props.roles)
        }

    },[props.roles])

    const submitFunctie = (e) => {
        e.preventDefault()
        if(functie.functie !== null && functie.functie !== '')
        {
            functie.rol_id = parseInt(functie.rol_id)
            if(props.create_functie(JSON.stringify(functie)))
            {
                console.log('role succesfuly aangemaakt')
            }else{
                console.log('wij hebben hier probleem')
            }
            setError(false)
            props.props.setShowModalCreateFunctie(false)
        }else{
            setError(true)
        }
       
    }
  return (
    <div>
        <Modal
        show={props.props.ShowModalCreateFunctie}
        onHide={() => props.props.setShowModalCreateFunctie(false)}
        animation={true}
        centered={true}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
    >   
        <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
                Functie aanmaken
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
        <div className="row m-3">
            <div className="col-lg-3">
                <label for="functie" className="form-label">Functie</label>    
            </div>
            <div className="col-lg-9">
                <input type="text" name="functie" value={functie.functie}   onChange={(e) => onChangeRole(e)} className="form-control form-control-sm" id="functie" placeholder="Voorbeeld: Directie" required/>
                {error ===true ? functie.functie !== null && functie.functie !== '' ? null: <span className={validationsMessages}>functie moet worden ingevuld</span> :null}
            </div>
        </div>
        <div className="row m-3">
            <div className="col-lg-3">
                <label for="role" className="form-label">Role</label>    
            </div>
            <div className="col-lg-9">
            <select 
                id="rol_id" 
                name="rol_id"
                defaultValue={functie.rol_id}
                className="form-select form-select-sm"
                onChange={(e) => onChangeRole(e)}
            >
                <option> -- Kies Role -- </option>
                    {roles !==undefined && roles !==null && roles.length !== 0 ? roles.map((item) => <option key={item[0]} value={item[0]}>{item[1]}</option> ):'geen gegevens gevonden'}
            </select>
                {error ===true ? functie.rol_id !== null && functie.rol_id !== '' ? null: <span className={validationsMessages}>role moet worden ingevuld</span> :null}
            </div>
        </div>
        <div class="text-end">
            <div class="row m-3">
                <div className='col'>
                    <button type="button" onClick={(e) => submitFunctie(e)} className="btn btn-danger btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                </div>
                <div className='col'>
                    <button type="button" onClick={() => props.props.setShowModalCreateFunctie(false)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                </div>
            </div> 
        </div>
        </Modal.Body>
    </Modal>

    </div>
  )
}

const getRolesItems = state => ({
    roles: state.admin.roles
})

export default connect(getRolesItems,{create_functie})(FunctieModalAanmaken)
