import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import PasswordReset from '../../components/PasswordReset';
import context from 'react-bootstrap/esm/AccordionContext';
const Userprofile = (props) => {
  const styles = {
    Width: '50%',
  }
  const [data,setData] = useState([])
  const [edit,setEdit] = useState(null)
  useEffect(() => {
    if(props.context !== undefined)
    {
        setData({
            'voornaam': props.context.medewerker.voornaam,
            'voorletter' : props.context.medewerker.voorletter,
            'achternaam' : props.context.medewerker.achternaam,
            'geslacht' : props.context.medewerker.geslacht,
            'geboortdatum' : props.context.medewerker.geboortdatum,
            'role': props.context.role,
            'functie': props.context.functie,
            'phone_no': props.context.medewerker.phone_no,
            'email': props.context.email,
        })
    }

  },[props.context])
  
  const dataOnchange = e => setData({ ...data,[e.target.name]:e.target.value}) 
  const submitForm = (e) => {
    e.preventDefault()
    // console.log(data)
  }
  const modeEdit = () => {
    setEdit(true)
  }
  const cancelEdit = () => {
    setEdit(false)
  }
  return (
    <>
     <div class="position-relative mx-n4 mt-n4">
        <div class="profile-wid-bg profile-setting-img">
            <img src={process.env.PUBLIC_URL + "/assets/images/image_bg.jpg"}class="profile-wid-img" alt="" />
            <div class="overlay-content">
                <div class="text-end p-3">
                    <div class="p-0 ms-auto rounded-circle profile-photo-edit">
                        <input id="profile-foreground-img-file-input" type="file" class="profile-foreground-img-file-input" />
                        <label for="profile-foreground-img-file-input" class="profile-photo-edit btn btn-light">
                            <i class="ri-image-edit-line align-bottom me-1"></i> Change Cover
                        </label>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="row">
        <div class="col-xxl-3">
            <div class="card mt-n5">
                <div class="card-body p-4">
                    <div class="text-center">
                        <div class="profile-user position-relative d-inline-block mx-auto  mb-4">
                            <img src={process.env.PUBLIC_URL + "/assets/images/users/avatar-1.jpg"} class="rounded-circle avatar-xl img-thumbnail user-profile-image" alt="user-profile-image" />
                            <div class="avatar-xs p-0 rounded-circle profile-photo-edit">
                                <input id="profile-img-file-input" type="file" class="profile-img-file-input" />
                                <label for="profile-img-file-input" class="profile-photo-edit avatar-xs">
                                    <span class="avatar-title rounded-circle bg-light text-body">
                                        <i class="ri-camera-fill"></i>
                                    </span>
                                </label>
                            </div>
                        </div>
                        <h5 class="fs-16 mb-1">{props.context ? props.context.medewerker.voornaam + ' ' + props.context.medewerker.achternaam:''}</h5>
                        <p class="text-muted mb-0">{props.context.role + ' / ' + props.context.functie}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-xxl-9">
            <div class="card mt-xxl-n5">
                <div class="card-header">
                    <ul class="nav nav-tabs-custom rounded card-header-tabs border-bottom-0" role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active" data-bs-toggle="tab" href="#personalDetails" role="tab">
                                <i class="fas fa-home"></i> Persoonlijke gegevens
                            </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" data-bs-toggle="tab" href="#changePassword" role="tab">
                                <i class="far fa-user"></i> Wachtwoord wijzigen
                            </a>
                        </li>
                    </ul>
                </div>
                <div class="card-body p-4">
                    <div class="tab-content">
                        <div class="tab-pane active" id="personalDetails" role="tabpanel">
                            <form>
                                <div class="row">
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="firstnameInput" class="form-label">Voornaam</label>
                                            <input type="text" class="form-control" id="firstnameInput" name="voornaam" onChange={(e) => dataOnchange(e)} placeholder="bijvoorbeeld: John" value={data ? data.voornaam: ''}  disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="voorletterInput" class="form-label">Voorleter</label>
                                            <input type="text" class="form-control" id="voorletterInput" name="voorletter" onChange={(e) => dataOnchange(e)} placeholder="bijvoorbeeld: J" value={data.voorletter} disabled = {edit === true?false:true} />
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="lastnameInput" class="form-label">Achternaam</label>
                                            <input type="text" class="form-control" name='achternaam' id="lastnameInput" onChange={(e) => dataOnchange(e)} placeholder="bijvoorbeeld: Doe"  value={data.achternaam} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                     {/* <!--end col--> */}
                                     <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="geslachtInput" class="form-label">Geslacht</label>
                                            {/* dropdownmenu ici */}
                                            <input type="text" class="form-control" name='geslacht' id="geslachtInput" onChange={(e) => dataOnchange(e)} placeholder="bijvoorbeeld: Dhr"  value={data.geslacht} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="geboortdatumInput" class="form-label">Geboordatum</label>
                                            {/* dropdownmenu ici */}
                                            <input type="text" class="form-control" name='geboortdatum' id="geboortdatumInput" onChange={(e) => dataOnchange(e)} value={data.geboortdatum} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="roleInput" class="form-label">Role</label>
                                            {/* dropdownmenu ici */}
                                            <input type="text" class="form-control" name='role' id="roleInput" onChange={(e) => dataOnchange(e)} value={data.role} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="functieInput" class="form-label">Functie</label>
                                            {/* dropdownmenu ici */}
                                            <input type="text" class="form-control" name='role' id="functieInput" onChange={(e) => dataOnchange(e)} value={data.functie} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="phonenumberInput" class="form-label">Phone nummer</label>
                                            <input type="text" class="form-control" id="phonenumberInput" placeholder="Enter your phone number" value={data.phone_no} disabled = {edit === true?false:true}/>
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                    <div class="col-lg-6">
                                        <div class="mb-3">
                                            <label for="emailInput" class="form-label">Email address</label>
                                            <input type="email" class="form-control" id="emailInput" placeholder="Enter your email" value={data.email} disabled = {edit === true?false:true} />
                                        </div>
                                    </div>
                                   
                                    {/* <!--end col--> */}
                                    <div class="col-lg-12">
                                        <div class="hstack gap-2 justify-content-end">
                                            <button type="submit" onClick={(e) => submitForm(e)} class="btn btn-primary">Opslaan</button>
                                            {edit === true ? <button type="button" onClick={() => cancelEdit()} class="btn btn-soft-success">Cancel</button>:
                                                <button type="button" onClick={() => modeEdit()} class="btn btn-soft-success">Edit</button>
                                            }
                                        </div>
                                    </div>
                                    {/* <!--end col--> */}
                                </div>
                                {/* <!--end row--> */}
                            </form>
                        </div>
                        <div class="tab-pane" id="changePassword" role="tabpanel">
                            <PasswordReset />
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
    {/* <div class="profile-foreground position-relative mx-n4 mt-n4">
        <div class="profile-wid-bg">
            <img src={process.env.PUBLIC_URL + "/static/img/image_bg.jpg"} alt="" class="profile-wid-img" />
        </div>
    </div> */}
    {/* <div class="pt-4 mb-4 mb-lg-3 pb-lg-4">
        <div class="row g-4">
            <div class="col-auto">
                <div class="avatar-lg">
                    <img src={process.env.PUBLIC_URL + "/static/img/users/avatar-1.jpg"} alt="user-img" class="img-thumbnail rounded-circle" />
                </div>
            </div> */}
            {/* <!--end col--> */}
            {/* <div class="col">
                <div class="p-2">
                    <h3 class="text-white mb-1">{props.context ? props.context.medewerker.voornaam + ' ' + props.context.medewerker.achternaam:''}</h3>
                    <p class="text-white-75">{props.context.role.role_name + ' & ' + props.context.functie}</p>
                    <div class="hstack text-white-50 gap-1">
                        {/* <div class="me-2"><i class="las la-envelope me-1 text-white-75 fs-16 align-middle"></i>{props.context.email}</div> */}
                   /
            {/* <!--end col--> */}
            {/* <div class="col-12 col-lg-auto order-last order-lg-0">
                <div class="row text text-white-50 text-center">
                    <div class="col-lg-6 col-4">
                        <div class="p-2">
                            <h4 class="text-white mb-1">24.3K</h4>
                            <p class="fs-14 mb-0">Followers</p>
                        </div>
                    </div>
                    <div class="col-lg-6 col-4">
                        <div class="p-2">
                            <h4 class="text-white mb-1">1.3K</h4>
                            <p class="fs-14 mb-0">Following</p>
                        </div>
                    </div>
                </div>
            </div> */}
            {/* <!--end col--> */}

        {/* </div> */}
    {/* <!--end row--> */}
    {/* </div> */}
  
    
    </>
   
  )
}
const getPorfileitems = state => ({
    context: state.profile
})
export default connect(getPorfileitems,{})(Userprofile)