import axios from "axios";
import Cookies from 'js-cookie'
import {
    AANMAKEN_PRODUCTIESTATUSICEM_SUCCESS,
    AANAMAKEN_PRODUCTIESTATUSICEM_FAIL,
    LOAD_PRODUCTIESTATUSICEM_SUCCESS ,
    LOAD_PRODUCTIESTATUSICEM_FAIL
} from './types'

axios.defaults.withCredentials = true

export const productiestatusicemOverzicht = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = formData
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/productiestatusicem/get-productiestatusicem`,body,config)
        if(res.data){
            dispatch({
                type: LOAD_PRODUCTIESTATUSICEM_SUCCESS,
                payload: res.data
            })
        }else{
            dispatch({LOAD_PRODUCTIESTATUSICEM_FAIL,
                payload: res.data.error
            })
        }
    }
    catch (err) {
        dispatch({
            type: LOAD_PRODUCTIESTATUSICEM_FAIL
        });
    }
}


