import React, {useEffect, useMemo, useState,useRef} from 'react'
import MaterialReactTable from 'material-react-table'
import RoleModalAanmaken from '../../components/admin/RoleModalAanmaken'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom';
import {getRoles }from '../../actions/admin'

const Roleoverzicht = (props) => {
  const [ShowModalCreateRole,setShowModalCreateRole] = useState(false)
  const [edit,setEdit] = useState(false)
  const [tableData, setTableData] = useState([])
  const [roles,setRoles] = useState([])
  const isCliked = useRef(null)
  var arrayobject = []

  useEffect(() => {
    props.getRoles()
  },[])

  useEffect(() => {
    if(props.roles !== undefined && props.roles !== null)
    {
        props.roles.map((item) => arrayobject.push({'id': item[0],'role_name':item[1]}))
        setRoles(arrayobject)
    }
  },[props.roles])
  
  const onChange = e =>setRoles({...roles, [e.target.name]: e.target.value}) 

  const editEnable = (row) => {
    isCliked.current = row.row.index
    setEdit(true)
  }
  const cancelEdit = () => {
    isCliked.current = false
    setEdit(false)

  }

  const DeleteRow = (e) => {

  }
  const handleSaveRow = async (e) => {

  }
  const excelExport = async (args) => {
    console.log(args)
  }
  const displayModalRoles = () => {
    return (<><RoleModalAanmaken props={{'ShowModalCreateRole':ShowModalCreateRole, 'setShowModalCreateRole':setShowModalCreateRole}} /></>)
  }

  const columns = useMemo(
    () => [
        {
            accessorKey: 'role_name',
            header: 'Role',
            Cell: ({cell,row}) => {
                return(
                    isCliked.current !== row.index ? 
                    <span className=''>{cell.getValue()}</span>:
                    <input className='form-control form-control-sm' name="role_name" defaultValue={cell.getValue()} onBlur={(e) => onChange(e)} />
                )
            }
        },
        {
            accessorKey: 'actions',
            header: 'Actions',
            Cell:(row) => {
                return(
                    <>
                    <div className="actions_buttons" ref={isCliked}>
                {isCliked.current !== row.row.index ? 
                
                 <>
                  <Link onClick={() => editEnable(row)} ><i class=" ri-edit-2-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                  <Link onClick={() => DeleteRow(row)}><i class=" ri-delete-bin-6-line"></i></Link>
                </>
                :
                <>
                  <Link onClick={() => cancelEdit()}><i class="ri-close-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                  <Link onClick={(e) => handleSaveRow(e)}><i class=" ri-check-fill "></i></Link>
              </>
                
              }
              </div>

                    </>
                )
            }
        }
    ]
  )
  return (
    <>
        <div>
            <div class="page-title-box d-sm-flex align-items-center justify-content-between">
                <h4 class="mb-sm-0">Role overzicht</h4>
            </div>
            <MaterialReactTable 
                columns={columns}
                data = {roles ? roles: 'Loading...'}
                enableColumnResizing
                editingMode="row"
                enableRowVirtualization
                enableRowSelection={true}
                enablePinning
                muiSelectCheckboxProps={{
                    color: 'primary',
                  }}
                 initialState={{ 
                    density: 'compact' ,
                    // columnPinning: { left: ['email'], right: ['actions'] },
        
                }}
                renderTopToolbarCustomActions={({ table }) => (
                    <>
                      <div className='row mb-3'>
                        <div className='col-xxl-6'>
                          <button className="btn btn-success btn-icon waves-effect waves-light" id="exportexelButton" type="button" onClick={() => excelExport(table.getSelectedRowModel().rows)} ><i class=" ri-file-excel-2-line"></i></button>
                        </div>
                        <div className='col-xxl-6'>
                          <button type="button" onClick={() => setShowModalCreateRole(true)} className="btn btn-danger btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                          {displayModalRoles()}
                        </div>
                        
                      </div>
                        
                    </>
                 )}
            />
        </div>
      
    </>
  )
}

const getAllRoles = state => ({
    roles: state.admin.roles,
})
export default connect(getAllRoles,{getRoles})(Roleoverzicht)
