import React, {useState} from 'react'
import {register } from '../actions/auth' 
import { connect } from 'react-redux'
import { Navigate,Link } from 'react-router-dom'
import CSRFToken from '../components/CSRFToken'

const Register = ({register}) => {
  const [formData, setFormData] = useState({
    email: '',
    is_staff: true,
    password: '',
    re_password: ''
  })
  const [accountCreated, setAccountCreated] = useState(false)
  const { email,is_staff,password,re_password} = formData;

  const onChange = e => setFormData({...formData, [e.target.name]: e.target.value})
  
  const onSubmit = e => {
    
    e.preventDefault();
    if(password === re_password){
      
      register(JSON.stringify(formData))
      setAccountCreated(true);
    }
    else{
      setAccountCreated(false);
    }
    
  }
 
    if (accountCreated){
      return <Navigate  to="/home" />
    }
  return (
          <div className='container'>
        <h3 className="text-center">Medewerker Aanmaken</h3>
        <form className='main' autoComplete="on" onSubmit={e=>onSubmit(e)}>
          {/* <CSRFToken /> */}
          <div className='form-group'>
          <label>Email:</label>
            <input 
              type="email" 
                // id="form12" 
                className="form-control form-control-sm" 
                placeholder="E-mail" 
                name='email'
                autoComplete="on" 
                value={email}
                onChange = {e => onChange(e)} 
                aria-labelledby="E-mail" 
                required />

          </div>
          <label className='form-group'>Is_staff:</label>
          <select 
              name = "is_staff"
              onChange={e => onChange(e)} 
              value= {is_staff}
              className="form-control form-control-sm"
              >
            <option value="true">True</option>
            <option value="false">False</option>
          </select><br></br>
          <label>Password:</label>
          <input 
            className="form-control form-control-sm" 
            name="password"
            type="password" 
            placeholder="Wachtwoord*"
            value={password} 
            onChange = {e => onChange(e)} 
            autoComplete="on"
            minLength='6'
            required
            /><br></br>
            <label>Type opnieuw Wachtwoord:</label>
            <input 
            className="form-control form-control-sm mt-3" 
            type="password" 
            name="re_password"
            placeholder="Type opnieuw wachtwoord*"
            value={re_password} 
            onChange = {e => onChange(e)} 
            autoComplete="on" 
            minLength='6'
            required
            />
            <button onClick={register} type="submit" className="btn btn-primary mt-3">Aanmaken</button>
        </form>
        <p className='mt-3'>
          {/* Already have ann Account? <Link to='/'>Login</Link> */}
        </p>
      </div>
  )
}
const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
})
export default connect(mapStateToProps, { register })(Register)
