import React,{useState,useEffect,useMemo} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import $ from 'jquery'


const ProductieExact = (props) => {
 
    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true)

    useEffect(() => {
         
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)

    },[props.props.data])

    const updateData = index => e => {
    
        let newArray = [...data];
         
        if(e.target.name === 'exactnummer' || e.target.name === 'bomId')
        {
          newArray[index][e.target.name] = parseInt(e.target.value) 
        }else{
          newArray[index][e.target.name] = e.target.value
        }
        props.props.setData(newArray)
        
      }
    const columns = useMemo(
        () => [

          {
            accessorKey: 'bouwnr',
            header: "Bouwnr",
          },
          {
            accessorKey: 'blok',
            header: "Blok",
          },
          {
            accessorKey: 'exactnummer',
            header: "Exactnummer",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ?
            <input type='number' name="exactnummer" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },
          {
            accessorKey: 'bomId',
            header: "Bom ID",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ? 
            <input type='text' name="bomId" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },
  
      ],[props.props.bewerken_actif, updateData,])

   
    useEffect(() => {
        $(function () {
            // $("#table8").enableCellNavigation();
            // $("#table").DataTable();
        });
    
        })
    return (
      isLoading ? <LinearProgress />:
      <MaterialReactTable
          columns={columns}
          data={data ? data: []}
          enablePinning
          enableColumnResizing
          initialState={{ 
            density: 'compact' ,
            columnPinning: { left: ['bouwnr'] },
        }}
        enableRowVirtualization
        muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
       
         />
    )
}

export default ProductieExact
