import React from 'react'
import SearchNavbar from '../../components/SearchNavbar'

const Productiebon = () => {
  const path = "/productiebon/mpo/"
  return (
    <>
    <div>Productiebon</div>
        <div className='navigation'>
            <SearchNavbar path={path}/>
        </div>
       <div>
        <p>Producttie bon pagina. via de breadcumb kies een project</p>
       </div>
    </>
    

  )
}

export default Productiebon