import React,{useState,useEffect} from 'react'

const PdfComponent = (props) => {
    const [state,setState] = useState(null)
    useEffect(() => {
        if(props !== undefined)
        {
            setState(props.props.props.data)
        }
    },[props])

    return (
        <>
        <div class="container-pdf">
            <small class="productie-title">Productiebon - bouwnummer {state ? state.bouwnr:'Onbekend'}</small>
            <img className="img_logo" src={process.env.PUBLIC_URL + '/assets/logo.png'} />

            <div>
                <table class="myTable1">
                 <tr>
                    <td ><p><b>{state ? state.projectnr: 'Onbekend'}</b></p></td>
                    <td><p><b>{state ? state.projectnaam: 'Onbekend'}</b></p></td>
                </tr>
            
                </table>
            </div>
        </div>

        {/* <div className='container'>
            
            <div class="container text-center">
                <div class="row align-items-start">
                    <div class="col">
                    One of three columns
                    </div>
                    <div class="col">
                    One of three columns
                    </div>
                    <div class="col">
                    One of three columns
                    </div>
                </div>
            </div>
            <h1>My Printable Component {state ? state.bouwnr:'Onbekend'}</h1>
            <p>This is the content of my printable component.</p>
        </div> */}
        </>
    )
}

export default PdfComponent;