import {
    GET_PROJECTBYID_SUCCESS,
    GET_PROJECTBYID_FAIL,
    GET_PROJECTBEWERKEN_SUCCESS,
    GET_PROJECTBEWERKEN_FAIL,
    LOAD_ALLPROJECT_SUCCESS,
    LOAD_ALLPROJECT_FAIL,
    LOAD_PROJECT_SUCCESS,
    LOAD_PROJECT_FAIL,
    AANMAKEN_PROJECT_SUCCESS,
    AANMAKEN_PROJECT_FAIL,
    AANMAKEN_KLANTMEDEWERKER_SUCCESS,
    AANMAKEN_KLANTMEDEWERKER_FAIL,
    AANMAKEN_KLANT_SUCCESS,
    AANMAKEN_KLANT_FAIL,
    LOAD_ALLPROJECTLIST_FAIL,
    LOAD_ALLPROJECTLIST_SUCCESS,
    AANMAKEN_BEDRIJFAANEMER_SUCCESS,
    AANMAKEN_BEDRIJFAANEMER_FAIL,
    LOAD_BEDRIJFAANEMER_FAIL,
    LOAD_BEDRIJFAANEMER_SUCCESS,
    UPDATE_PROJECT_SUCCESS,
    UPDATE_PROJECT_FAIL

 } from '../actions/types'
 
 const initialState = {
   is_success: '',
   context:[],
   projects: [],
   projectsall: [],
   data:[],
   dataProjectID:[],
   bewerkendata:null

//    roles:[],
//    functies: []
 };





 export default function(state= initialState, action){
     const { type, payload } = action;
 
     switch(type){
         case GET_PROJECTBYID_SUCCESS:
            return{
                ...state,
                dataProjectID: payload

            }
        case GET_PROJECTBEWERKEN_SUCCESS:
            return{
                ...state,
                bewerkendata: payload

            }
         case LOAD_BEDRIJFAANEMER_SUCCESS:
            return{
                ...state,
                data:payload.data,
                
            }
         case LOAD_ALLPROJECTLIST_SUCCESS:
            return{
                ...state,
                projects:payload.data,
                
            }
         case LOAD_ALLPROJECT_SUCCESS:
            return{
                ...state,
                projectsall:payload.data,
                
            }
         case UPDATE_PROJECT_SUCCESS:
         case AANMAKEN_BEDRIJFAANEMER_SUCCESS:
         case AANMAKEN_KLANT_SUCCESS:
         case AANMAKEN_KLANTMEDEWERKER_SUCCESS:
         case AANMAKEN_PROJECT_SUCCESS:
         case LOAD_PROJECT_SUCCESS:
            return{
                ...state,
                is_success: payload.success,
                context: payload.context

            }
         case UPDATE_PROJECT_FAIL:
         case LOAD_BEDRIJFAANEMER_FAIL:
         case LOAD_ALLPROJECTLIST_FAIL:
         case GET_PROJECTBYID_FAIL:
         case GET_PROJECTBEWERKEN_FAIL:
         case LOAD_ALLPROJECT_FAIL:
         case AANMAKEN_BEDRIJFAANEMER_FAIL:
         case AANMAKEN_KLANT_FAIL:
         case AANMAKEN_KLANTMEDEWERKER_FAIL:
         case AANMAKEN_PROJECT_FAIL:
         case LOAD_PROJECT_FAIL:
            return {
                ...state,
                is_success: payload.error,
                // context: payload.context

            }
         default:
             return state
     };
 
 };
