import React, { useState, useEffect,useRef } from 'react';
import { ToggleButton } from 'primereact/togglebutton';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Toast } from 'primereact/toast';
import {load_medewerkers} from '../../actions/medewerker'
import { connect } from 'react-redux'
import { Button } from 'primereact/button';
import { Toolbar } from 'primereact/toolbar';
import { InputText } from 'primereact/inputtext';
import { Dialog } from 'primereact/dialog';
import { classNames } from 'primereact/utils';
import { Dropdown } from 'primereact/dropdown';
import { InputMask } from 'primereact/inputmask';
import { Password } from 'primereact/password';
import {medewerker_aanmaken }from '../../actions/medewerker'

const Medewerker = ({load_medewerkers,context,roles,functies,medewerkerAanmaken,medewerkerUpdated}) => {
    const emptyMedewerker = {
        aanhef: '',
        voornaam: '',
        voorletter: '',
        tussenvoegsel: '',
        achternaam: '',
        functie: '',
        role: '',
        phone: '',
        geboortDatum: '',
        email: '',
        status: '',
        password: '',
        re_password: '',

    }
    // const [medewerkers, setMedewerkers] = useState(null)
    let medewerkers = []
    const [medewerker, setMedewerker] = useState(emptyMedewerker)
    const [created,setCreated] = useState(null)
    const [updated,setUpdated] = useState(null)
    const [medewerkerDialog, setMedewerkerDialog] = useState(false);
    const [loading, setLoading] = useState(true);
    const {aanhef,voornaam,voorletter,tussenvoegsel,achternaam,functie,role,phone,geboortDatum,email,status,password,re_password} = medewerker
    const effectRun = useRef(false)
    
    const [globalFilter, setGlobalFilter] = useState(null);
    const [submitted, setSubmitted] = useState(false);
    const toast = useRef(null);
    
    const [selectedProducts, setSelectedProducts] = useState(null);
    const dt = useRef(null);

    if(context !== [] ){
        if(context !== undefined){
          
            medewerkers = context
           
        }
    }

    useEffect(() => {
        // if(effectRun.current === false){
            
            setLoading(true);
            load_medewerkers()
            // setMedewerkers(context)
            setLoading(false);
        // }  
        // initFilters1();
        // return () => { return effectRun.current = true }
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const displayFunctie = (data) => {
        
        let functies = []
        if(role){
            for(let i in data){
                if(data[i].rol_id === role){
                    functies.push(
                        
                        {label: data[i].functie,value: data[i].id}
                        )
                }
            }
            return functies
        }
        
    }

    const openNew = () => {
        setMedewerker(emptyMedewerker);
        setSubmitted(false);
        setMedewerkerDialog(true);
    }

    const hideDialog = () => {
        setSubmitted(false);
        setMedewerkerDialog(false);
    }
    
    const [idFrozen, setIdFrozen] = useState(false);

    const header = (
        <div className="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
            <h5 className="m-0">MEDEWERKERS OVERZICHT</h5>
            <span className="block mt-2 md:mt-0 p-input-icon-left">
                <i className="pi pi-search" />
                <InputText type="search" onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Search..." />
            </span>
        </div>
    );
    const exportCSV = () => {
        dt.current.exportCSV();
    }
    const rightToolbarTemplate = () => {
        return (
            <React.Fragment>
                {/* <FileUpload mode="basic" accept="image/*" maxFileSize={1000000} label="Import" chooseLabel="Import" className="mr-2 inline-block" /> */}
                <Button label="Export" icon="pi pi-upload" className="p-button-help" onClick={exportCSV} />
            </React.Fragment>
        )
    }
    const leftToolbarTemplate = () => {
        return (
            <React.Fragment>
                <div className="my-2">
                    <Button label="New" icon="pi pi-plus" className="p-button-success mr-2"  onClick={openNew}/>
                    {/* <Button label="Delete" icon="pi pi-trash" className="p-button-danger"  /> */}
                </div>
            </React.Fragment>
        )
    }

    const editMedewerker = (medeweker) => {
        setMedewerker({ ...medeweker });
        setMedewerkerDialog(true);
    }
    
    const citySelectItems = [
        {label: 'van', value: 'van'},
        
    ];
    const aanhefSelectItems = [
        {label: 'Dhr',value:'Dhr'},
        {label: 'Mevr',value:'Mevr'},
    ]
    const statusItems = [
        {label: 'Actief',value:1},
        {label: 'Inactief',value:0}
    ]
    const roleSelection = (data) => {
        let something = []
        for(var i in data){
            var label = data[i].role_name
            var value = data[i].id 
            something.push(
                {label:label,value:value}
            )
        } 
        return something  
    }
     
    const onChange = e =>setMedewerker({...medewerker, [e.target.name]: e.target.value})
    

    const onSubmit = e => {

        setSubmitted(true);
        e.preventDefault();

        if(medewerker.email.trim() && medewerker.aanhef.trim() && medewerker.voornaam.trim() && medewerker.voorletter.trim() 
            && medewerker.achternaam.trim() && medewerker.geboortDatum.trim() && medewerker.phone.trim() 
            && medewerker.password.trim() && medewerker.re_password.trim()
            ){
            

            if (password.length < 8 && re_password.length < 8){
                setMedewerkerDialog(true);
                toast.current.show({ severity: 'error', summary: 'Error', detail: 'Wachtwoord moet tenminste 8 charachters zijn', life: 3000 });
            }else{
                if(email_(medewerkers)){    
                    // function voor update
                    if(password === re_password){ 
                        medewerkerUpdated(JSON.stringify(medewerker))
                        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Medewerker is met success Updated', life: 3000 });
                        setMedewerkerDialog(false);
                        setSubmitted(false);
                    }else{
                        setMedewerkerDialog(true);
                        toast.current.show({ severity: 'error', summary: 'Error', detail: 'Wachtwoord komen niet overheen', life: 3000 });
                    }
                        
                }else{
    
                    // function voor update
                    if(password === re_password){ 
                        medewerker_aanmaken(JSON.stringify(medewerker))
                        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Medewerker met successfull created', life: 3000 });
                        setMedewerkerDialog(false);
                        setSubmitted(false);
                    }else{
                        setMedewerkerDialog(true);
                        toast.current.show({ severity: 'error', summary: 'Error', detail: 'Wachtwoord komen niet overheen', life: 3000 });
    
                    }
                    
                }

            }
            
                
        }else{
            setMedewerkerDialog(true);
            toast.current.show({ severity: 'error', summary: 'Error', detail: 'Form moet volledig ingevuld zijn', life: 3000 });
        }
        
        
    }

    const email_ = (medewerkers) => {
        var email = ''
        for(let i in medewerkers){
           if(medewerker.email === medewerkers[i].email){
                email = medewerker.email
           }
        }
        return email
    }

    const productDialogFooter = (
        <>
            <Button label="Delete" icon="pi pi-trash" className="p-button-text"  />
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideDialog} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={onSubmit} />
            
        </>
    );
    const actionBodyTemplate = (rowData) => {
        return (
            <div className="actions">
                <Button icon="pi pi-pencil" className="p-button-rounded p-button-success mr-2" onClick={() => editMedewerker(rowData)} />
                {/* <Button icon="pi pi-trash" className="p-button-rounded p-button-warning mt-2" onClick={() => confirmDeleteProduct(rowData)} /> */}
            </div>
        );
    }


    // const initFilters1 = () => {
    //     setFilters1({
    //         'global': { value: null, matchMode: FilterMatchMode.CONTAINS },
    //         'name': { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }] },
    //         'country.name': { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }] },
    //         'representative': { value: null, matchMode: FilterMatchMode.IN },
    //         'date': { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.DATE_IS }] },
    //         'balance': { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.EQUALS }] },
    //         'status': { operator: FilterOperator.OR, constraints: [{ value: null, matchMode: FilterMatchMode.EQUALS }] },
    //         'activity': { value: null, matchMode: FilterMatchMode.BETWEEN },
    //         'verified': { value: null, matchMode: FilterMatchMode.EQUALS }
    //     });


    // }


    return (
        <div>
            <Toast ref={toast} />
            <ToggleButton checked={idFrozen} onChange={(e) => setIdFrozen(e.value)} onIcon="pi pi-lock" offIcon="pi pi-lock-open" onLabel="Unfreeze Id" offLabel="Freeze Id" style={{ width: '10rem' }} />
            <Toolbar className="mb-4" left={leftToolbarTemplate} right={rightToolbarTemplate}></Toolbar>
            <DataTable ref={dt} value={medewerkers}  scrollable scrollHeight="400px" dataKey="id"
                loading={loading} header={header} globalFilter={globalFilter} scrollDirection="both" globalFilterFields={['name', 'country.name', 'representative.name', 'balance', 'status']} 
                selection={selectedProducts} onSelectionChange={(e) => setSelectedProducts(e.value)} className="mt-3" responsiveLayout="scroll">
                <Column selectionMode="multiple" headerStyle={{ width: '3rem' }} frozen></Column>
                <Column field="aanhef" sortable header="Aanhef" style={{ flexGrow: 1, flexBasis: '100px' }} frozen></Column>
                <Column field="voornaam" sortable header="Voornaam" style={{ flexGrow: 1, flexBasis: '100px' }}  alignFrozen="left" frozen={idFrozen}></Column>
                <Column field="tussenvoegsel" sortable header="Tussenvoegsel" style={{ flexGrow: 1, flexBasis: '100px' }}></Column>
                <Column field="achternaam" sortable header="Achternaam" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column>
                <Column field="functie" sortable header="Functie" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column> 
                <Column field="role" sortable header="Role" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column> 
                <Column field="gebruiker" sortable header="Gebruiker" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column> 
                <Column field="phone" sortable header="Telefoon" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column> 
                <Column field="email" sortable header="Email" style={{ flexGrow: 1, flexBasis: '200px' }} ></Column> 
                <Column field="status" sortable header="Status" style={{ flexGrow: 1, flexBasis: '200px' }}></Column> 
                <Column body={actionBodyTemplate} header="Actions" alignFrozen="right" frozen></Column> 
                {/* <Column field="country.name" header="Country" style={{ flexGrow: 1, flexBasis: '200px' }} body={countryBodyTemplate}></Column> */}
                {/* <Column field="date" header="Date" style={{ flexGrow: 1, flexBasis: '200px' }} body={dateBodyTemplate}></Column> */}
                {/* <Column field="company" header="Company" style={{ flexGrow: 1, flexBasis: '200px' }}></Column> */}
                {/* <Column field="status" header="Status" style={{ flexGrow: 1, flexBasis: '200px' }} body={statusBodyTemplate}></Column> */}
                {/* <Column field="activity" header="Activity" style={{ flexGrow: 1, flexBasis: '200px' }}></Column> */}
                {/* <Column field="representative.name" header="Representative" style={{ flexGrow: 1, flexBasis: '200px' }} body={representativeBodyTemplate}></Column> */}
                {/* <Column field="balance" header="Balance" body={balanceTemplate} frozen style={{ flexGrow: 1, flexBasis: '120px' }} alignFrozen="right"></Column> */} 
            </DataTable>
            <Dialog visible={medewerkerDialog} style={{ width: '950px' }} header="Medewerker aanmaken" modal className="p-fluid" footer={productDialogFooter} onHide={hideDialog}>
                {/* {medewerker.image && <img src={`assets/demo/images/product/${medewerker.image}`} alt={medewerker.image} width="150" className="mt-0 mx-auto mb-5 block shadow-2" />} */}
                <div className="formgrid grid">
                        <div className="field col">
                            <label htmlFor="aanhef">Aanhef</label>
                            <Dropdown id="aanhef" name='aanhef' value={aanhef} options={aanhefSelectItems} onChange={ e => onChange(e)} placeholder="Aanhef" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.aanhef })}/>
                            {submitted && !medewerker.aanhef && <small className="p-invalid">Aanhef is required.</small>}
                        </div>
                    <div className="field col">
                        <label htmlFor="voornaam">Voornaam</label>
                        <InputText id="voornaam" placeholder='Voornaam' name="voornaam" value={voornaam} onChange={e => onChange(e)} required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.voornaam })}/>
                        {submitted && !medewerker.voornaam && <small className="p-invalid">Voornaam is required.</small>}
                    </div>
                    <div className="field col">
                        <label htmlFor="voorletter">Voorletter</label>
                        <InputText maxLength={1}  id="voorletter" name="voorletter" value={voorletter} onChange={e => onChange(e)} placeholder="Voorletter" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.voorletter })}/>
                        {submitted && !medewerker.voorletter && <small className="p-invalid">Voorletter is required.</small>}
                    </div>
                    
                </div>
                <div className="formgrid grid">
                    <div className="field col">
                        <label htmlFor="tussenvoegsel">Tussenvoegsel</label>
                        <Dropdown id="tussenvoegsel" name='tussenvoegsel' value={tussenvoegsel} options={citySelectItems} onChange={ e => onChange(e)} placeholder="Tussenvoegsel"/>
                        
                    </div>

                    <div className="field col">
                        <label htmlFor="achternaam">Achternaam</label>
                        <InputText id="achternaam" name="achternaam" placeholder='Achternaam' value={achternaam} onChange={ e => onChange(e)} required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.achternaam })}/>
                        {submitted && !medewerker.achternaam && <small className="p-invalid">Achternaam is required.</small>}
                    </div>
                </div>
                <div className="formgrid grid">
                    <div className="field col">
                        <label htmlFor="role">Roles</label>
                        <Dropdown id="role" name='role' value={role} options={roleSelection(roles)} onChange={ e => onChange(e)} placeholder="Role" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.role })}/>
                        {submitted && !medewerker.role && <small className="p-invalid">role is required.</small>}
                    </div>
                    <div className="field col">
                        <label htmlFor="functie">Functie</label>
                        <Dropdown id="functie" name='functie' value={functie} options={displayFunctie(functies)} onChange={ e => onChange(e)} placeholder="Functie" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.functie })}/>
                        {submitted && !medewerker.functie && <small className="p-invalid">functie is required.</small>}
                    </div>
                    <div className="field col">
                        <label htmlFor="status">Status</label>
                        <Dropdown id="status" name='status' value={status} options={statusItems} onChange={ e => onChange(e)} placeholder="Status" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.status })}/>
                        {submitted && !medewerker.status && <small className="p-invalid">status is required.</small>}
                    </div>
                </div>
                <div className="formgrid grid">
                    <div className="field col">
                        <label htmlFor="geboortDatum">GeboortDatum</label>
                        <InputMask id="geboortDatum" name='geboortDatum' mask="99/99/9999" slotChar="mm/dd/yyyy" value={geboortDatum} onChange={ e => onChange(e)} placeholder="GeboortDatum" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.geboortDatum })}></InputMask>
                        {submitted && !medewerker.geboortDatum && <small className="p-invalid">GeboortDatum is required.</small>}
                    </div>
                    <div className="field col">
                        <label htmlFor="phone">Telefoon</label>
                        <InputMask id="phone" name="phone" mask="(9999)-999999999" value={phone} onChange={ e => onChange(e)} placeholder="Telefoon" required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.phone })}></InputMask>
                        {submitted && !medewerker.phone && <small className="p-invalid">Telefoon is required.</small>}
                    </div>
                </div>
                <div className="field">
                    <label htmlFor="email">E-mail</label>
                    <InputText id="email" placeholder='E-mail' name="email" value={email} onChange={e => onChange(e)} required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.email })}/>
                    {submitted && !medewerker.email && <small className="p-invalid">Email is required.</small>}
                </div>

                <div className="formgrid grid">
                    <div className="field col">
                        <label htmlFor="password">Wachtwoord</label>
                        <Password minLength={8} placeholder='Wachtwoord' id="password" name="password" value={password} onChange={e => onChange(e)} required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.password })} />
                        {submitted && !medewerker.password && <small className="p-invalid">Password is required.</small>}
                    </div>
                    <div className="field col">
                        <label htmlFor="re_password">Confirm Wachtwoord</label>
                        <Password minLength={8} placeholder='Confirm Wachtwoord' id="re_password" name="re_password" value={re_password} onChange={e => onChange(e)} required autoFocus className={classNames({ 'p-invalid': submitted && !medewerker.re_password })} />
                        {submitted && !medewerker.re_password && <small className="p-invalid">re_password is required.</small>}
                    </div>
                </div>
            </Dialog>    
        </div>
    )
    }

const getMedewerkersitems = state => ({
    context: state.medewerkers.context,
    roles: state.medewerkers.roles,
    functies: state.medewerkers.functies

  })
export default connect(getMedewerkersitems,{load_medewerkers,medewerker_aanmaken})(Medewerker)