import React, { useEffect, useMemo, useRef, useState } from 'react'
import { BrowserRouter as Routes, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from '@mui/material/node/Button';
import { Box, Modal } from '@mui/material';
import MaterialReactTable from 'material-react-table';
import { get_projectById } from '../../actions/project';
import PrintIcon from '@mui/icons-material/Print';
import { productiestatusicemOverzicht } from '../../actions/projectstatusicem'
import { Link } from "react-router-dom";
// CUSTOMS COMPONENTS
import SearchNavbar from '../../components/SearchNavbar'
const ProductiestatusicemOverzicht = (props) => {
    let projectId = useParams()
    const projectID = projectId.id

    const [projectnummer,setProjectnummer] = useState(null)
    const [klantnaam,setKlantnaam] = useState(null)
    const [projectnaam,setProjectnaam] = useState('')
    const [projectstatus,setProjectstatus] = useState(null)
    const hoeveelSitemetMissendeOnderdelen = useRef(null)
    const hoeveelSitemetTransportgereed = useRef(null)
    const hoeveelSitemettestrapportdefiniet = useRef(null)
    const [noData,setNodata] = useState(false)
    const totaalSiteinProject = useRef(null)

    const icemData = {
      sum: 0,
      a:0, 
      p:0,
      e:0,
      i:0,
      f:0
    }

    useEffect(() => {
        props.get_projectById(projectID)
        props.productiestatusicemOverzicht(projectID)
        if(props.context !== undefined)
        {
            setProjectnummer(props.context.projectnummer)
            setKlantnaam(props.context.Klantnaam)
            setProjectnaam(props.context.projectnaam)
            setProjectstatus(props.context.projectstatus)
        }

    },[props.context.projectnummer,props.context.klantnaam,projectID])

    useEffect(() => {
      if(projectnaam !== ''){
          setNodata(false)
      }else{
          setNodata(true)
      }

  },[projectnaam])

  const returnNumberOfIcemType = (e,x) => {
      let xArray = []
      for(let i in e){
         if(e[i] === x){
          xArray.push(e[i])
         }else if(e[i] === x){

         }
          
      }
     return xArray.length 
    } 

    if(props.headerprops !== undefined)
      {
        var icemTypeForm = props.headerprops.icemType
        var array = props.headerprops.missendeOnderdelen
        var transportgereed  = props.headerprops.transportgereed
        var transportdefinitief = props.headerprops.testrapportdefinitief
        var datagrid = props.headerprops.datagrid

        // if(datagrid !== undefined){

        // }
        if(transportdefinitief !== undefined)
        {
          var transportdefinitiefArray = []
          var transportnietDefiniefArray = []
          transportdefinitief.forEach(element => {
            if(element !== true)
            {
              transportnietDefiniefArray.push(element)
            }
            else
            {
              transportdefinitiefArray.push(element)
            }
          })
          hoeveelSitemettestrapportdefiniet.current = transportdefinitiefArray.length
        }

        if(transportgereed !== undefined)
        {
          var transportgereedArray = []
          var transportgereedNietGereed = []
          transportgereed.forEach(element =>{
            if(element !== true){
              transportgereedNietGereed.push(element)
            }
            else
            {
              transportgereedArray.push(element)
            }
          })
          hoeveelSitemetTransportgereed.current = transportgereedArray.length
        }

        if(array !== undefined)
        {
          var missendeArray = []
          var nietmissendeArray = []
          array.forEach(element => {
            if(element === false)
            {
              missendeArray.push(element)
            }
            else
            {
              nietmissendeArray.push(element)
            }

          });
          totaalSiteinProject.current = array.length
          hoeveelSitemetMissendeOnderdelen.current = missendeArray.length
        }
        let aNumber = parseInt(returnNumberOfIcemType(icemTypeForm,"a"))
        let eNumber = parseInt(returnNumberOfIcemType(icemTypeForm,"e"))
        let fNumber = parseInt(returnNumberOfIcemType(icemTypeForm,"f"))
        let iNumber = parseInt(returnNumberOfIcemType(icemTypeForm,"i"))
        let pNumber = parseInt(returnNumberOfIcemType(icemTypeForm,"p"))
        let sum = aNumber + eNumber + fNumber + iNumber + pNumber
        if(isNaN(sum)){
            sum = 0
        }
      
        icemData.sum = sum
        icemData.a = aNumber
        icemData.i = iNumber
        icemData.e = eNumber
        icemData.p = pNumber
        icemData.f = fNumber

      }
    
    const displayheader = () => {
      return(
      <>
        <div className="e-card-content">
          <div className='row'>
            <div className='col'>
              <p>Algemene projectgegevens:</p><br></br>
              <p><b>Projectnummer:</b><span>&nbsp; {projectnummer}</span></p>
              <p><b>Klantnaam:</b><span>&nbsp; {klantnaam}</span></p>
              <p><b>Projectnaam:</b><span>&nbsp; {projectnaam}</span></p>
              <p><b>Projectstatus:</b><span>&nbsp; {projectstatus}</span></p>
              <p><b></b>Totaal aantal iCEM's: &nbsp; <input name="sum_icems" value={icemData.sum || 0}  className='inputCss' type="text" placeholder='0' readOnly/></p>
              <div className='row g-3'>
                <div className='col-2'>
                    <label>- i:&nbsp; </label>
                    <input name="icem_i" value={icemData.i || 0}  className=' inputCss' type="text" placeholder='0' readOnly/>
                </div>
                <div className='col-2'>
                    <label>- p:&nbsp; </label>
                    <input name="icem_p" value={icemData.p || 0}  className=' inputCss' type="text" placeholder='0' readOnly/>
                </div>
                <div className='col-2'>
                    <label>- e:&nbsp; </label>
                    <input name="icem_e" value={icemData.e || 0}  className=' inputCss' type="text" placeholder='0' readOnly/>
                </div>
                <div className='col-2'>
                    <label>- f:&nbsp; </label>
                    <input name="icem_f" value={icemData.f || 0}  className=' inputCss' type="text" placeholder='0' readOnly/>
                </div>
                <div className='col-2'>
                    <label>- a:&nbsp; </label>
                    <input name="icem_a" value={icemData.a || 0}  className=' inputCss' type="text" placeholder='0' readOnly/>
                </div>
              </div>
            </div>
            <div className='col'>
            <p>Samenvatting:</p><br></br>
            <p><b>Missende onderdelen:</b><span>&nbsp; {hoeveelSitemetMissendeOnderdelen.current !== null ? hoeveelSitemetMissendeOnderdelen.current + ' van '+ totaalSiteinProject.current: 0 + ' van ' + totaalSiteinProject.current}</span></p>
            <p><b>Transportgereed:</b><span>&nbsp; {hoeveelSitemetTransportgereed.current !==null ? hoeveelSitemetTransportgereed.current + ' van ' +  totaalSiteinProject.current: 0 + ' van ' + totaalSiteinProject.current}</span></p>
            <p><b>Testrapport definitief:</b><span>&nbsp; {hoeveelSitemettestrapportdefiniet.current !== null ? hoeveelSitemettestrapportdefiniet.current + ' van ' + totaalSiteinProject.current: 0 + ' van ' + totaalSiteinProject.current}</span></p>
            </div>
          </div>
        </div>
      </>
      )
    }
    const columns = useMemo(
      () => [
        {
          accessorKey: 'projectnummer',
          header: 'Projectnr'
        },
        {
          accessorKey: 'bouwnr',
          header: 'Bouwnr'
        },
        {
          accessorKey: 'straat',
          header: 'Straat'
        },
        {
          accessorKey: 'huisnr',
          header: 'Huisnr'
        },
        {
          accessorKey: 'postcode',
          header: 'Postcode'
        },
        {
          accessorKey: 'missendeOnderdelen',
          header: 'Missende onderdelen',
          Cell: ({ row }) => (
            row.original.missendeOnderdelen !== true ? <p>Missende onderdelen</p>:
            <p>Geen missende onderdelen</p>
          ),
        },
        {
          accessorKey: 'transportgereed',
          header: 'Transport gereed',
          Cell: ({ row }) => (
            row.original.transportgereed !== true ? <p>Nee</p>:
            <p>Ja</p>
          ),
        },
        {
          accessorKey: 'testrapportdefinitief',
          header: 'Transport definitief',
          Cell: ({ row }) => (
            row.original.testrapportdefinitief !== true ? <p>Nee</p>:
            <p>Ja</p>
          ),
        },
        {
          accessorKey: 'leverdatum',
          header: 'Leverdatum Module'
        },


      ]
    )
    const handleExportData = (rows) => {
      console.log(rows)
    }
    const dateTable = () => {
      return(
        <>
          <div>
        <MaterialReactTable
            columns={columns}
            data={datagrid !== undefined ? datagrid: []}
            enableColumnResizing
            renderTopToolbarCustomActions={({ table }) => (
                <Box
                  sx={{ display: 'flex', gap: '1rem', p: '0.5rem', flexWrap: 'wrap' }}
                >
                   <Button
                    // disabled={
                    //   !table.getIsSomeRowsSelected() && !table.getIsAllRowsSelected()
                    // }
                    color="primary"
                    //export all data that is currently in the table (ignore pagination, sorting, filtering, etc.)
                    onClick={() => handleExportData(table.getSelectedRowModel().rows)}
                    startIcon={<PrintIcon />}
                    variant="contained"
                  >
                Print PDF
              </Button>
                  </Box>
              )} 
            // renderBottomToolbarCustomActions={() => (
            //     <Typography sx={{ fontStyle: 'italic', p: '0 1rem' }} variant="body2">
            //       Double-Click a Cell to Edit
            //     </Typography>
            //   )}
            />
    </div>
        </>
      )
    }

  return (
    <div className='control-pane mpo'>
      
      <div className='title-page'>
        <h5>Productiestatus Icem {projectnummer}</h5>
      </div><b></b>
      {noData !==true ?  
      <>
        <div className='control-section card-control-section basic_card_layout'>
          <div className="e-card-resize-container">
            <div className="row card-layout">
              {displayheader()}
            </div>
          </div>
        </div>
        {dateTable()}
      </>
      :
      <>
        <p>Er is nog geen project aangemaakt</p>
        Click op <Link to="/projectaanmaken" relative="path">project naanmaken</Link> 
      </>
        }
      
    </div>
  )
}

const getProjectItems = state => ({
    context: state.project.dataProjectID,
    headerprops: state.productiestatusicem.data
})
export default connect(getProjectItems,{get_projectById,productiestatusicemOverzicht})(ProductiestatusicemOverzicht)
