import {
   LOAD_USER_PROFILE_FAIL,
   LOAD_USER_PROFILE_SUCCESS,
} from '../actions/types'

const initialState = {
    email: '',
    medewerker: '',
    role: '',
    functie: '',
    gebruiker: '',
    is_loggedin: null,
    msgStatus: null,
  

};

export default function(state= initialState, action){
    const { type, payload } = action;

    switch(type){
        case LOAD_USER_PROFILE_SUCCESS:
            return {
                ...state,
                email:payload.email,
                medewerker: payload.medewerker,
                is_loggedin: payload.is_loggedin,
                role: payload.role,
                functie: payload.functie,
                gebruiker: payload.gebruiker

            }
        case LOAD_USER_PROFILE_FAIL:
            return {
                ...state,
                msgStatus: payload.err
                
            }
        default:
            return state
    };

};