import axios from "axios";
import Cookies from 'js-cookie'
import { load_medewerkers } from "./medewerker";
import { load_project,loadallprojectsList,get_projectById } from './project'
import {
    LOAD_MPO_SUCCESS,
    LOAD_MPO_FAIL,
    AANMAKEN_MPO_SUCCESS,
    AANMAKEN_MPO_FAIL,
    AANMAKEN_PRODUCTIESTATUS_SUCCESS,
    AANMAKEN_PRODUCTIESTATUS_FAIL,
    LOAD_PRODUCTIESTATUS_SUCCESS,
    LOAD_PRODUCTIESTATUS_FAIL,
    UPDATE_PRODUCTIESTATUS_SUCCESS,
    UPDATE_PRODUCTIESTATUS_FAIL,
    LOAD_SITESPROJECT_FAIL,
    LOAD_SITESPROJECT_SUCCESS,
    LOAD_PRODUCTIEBON_FAIL,
    LOAD_PRODUCTIEBON_SUCCESS,
    LOAD_PRODUCTIEDATATOPRINT_SUCCESS,
    LOAD_PRODUCTIEDATATOPRINT_FAIL
   
} from './types'

axios.defaults.withCredentials = true


export const mpoGet = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    }
    const body = formData
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/mpo/get-mpo`,body,config)
        
        if(res.data){
            dispatch({
                type: LOAD_MPO_SUCCESS,
                payload: res.data
            })
            dispatch(get_projectById(formData)) 
            dispatch(loadallprojectsList())
         
        }else{
            dispatch({LOAD_MPO_FAIL,
                payload: res.data
            })
      
        }
    }
    catch (err) {
        dispatch({
            type: LOAD_MPO_FAIL
        });
      
    }
}
export const mpoAanmaken = (formData,id) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = formData
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/mpo/update-mpo`,body,config)
        
        if(res.data.success){
            dispatch({
                type: AANMAKEN_MPO_SUCCESS,
                payload: res.data.success
            })
            dispatch(load_medewerkers())
            dispatch(load_project())
            dispatch(mpoGet(id))
        }else{
            dispatch({AANMAKEN_MPO_FAIL,
                payload: res.data.error
            })
            dispatch(load_medewerkers)
        }
    }
    catch (err) {
        dispatch({
            type: AANMAKEN_MPO_FAIL
        });
        dispatch(load_medewerkers)
    }
}
