export const productieStatusLoad = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
            
        }
    };
    const body = JSON.stringify(formData)
    try{
        // const res = await axios.get(`${process.env.REACT_APP_API_URL}/mpo/get-productiestatus`,{withCredentials:true},config);
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/mpo/get-productiestatus`,body,config)
        if(res.data){
            dispatch ({
                type: LOAD_PRODUCTIESTATUS_SUCCESS,
                payload: res.data
            });
        }
        else{
            dispatch ({
                type: LOAD_PRODUCTIESTATUS_FAIL,
                // payload: res.data
            })
        }
        
    }catch (err){

        dispatch ({
            type: LOAD_PRODUCTIESTATUS_FAIL,
            payload: err
        });
    }
}

