import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    PASSWORD_CHANGE_SUCCESS,
    PASSWORD_CHANGE_FAIL,
    LOCKSCREEN_FAIL,
    LOCKSCREEN_SUCCESS,
    UNLOCKSCREEN_FAIL,
    UNLOCKSCREEN_SUCCESS,
    LOAD_PERMISSIONS_USER_SUCCESS,
    LOAD_PERMISSIONS_USER_FAIL
} from '../actions/types'

const initialState = {
    isAuthenticated: null,
    is_success: null,
    message:'',
    data:'',
    permissions: ''

};

export default function(state= initialState, action){
    const { type, payload } = action;

    switch(type){
        case PASSWORD_CHANGE_SUCCESS:
            return {
                ...state,
                is_success: true,
                message: payload

            }
        case LOAD_PERMISSIONS_USER_SUCCESS:
            return {
                ...state,
                is_success: true,
                permissions: payload

            }
        case PASSWORD_CHANGE_FAIL:  
            return{
                ...state,
                is_success: false,
                message:payload,
                
            }

        case AUTHENTICATED_SUCCESS:
        case AUTHENTICATED_FAIL:
            return{
                ...state,
                isAuthenticated: payload,
            }
        
        case REGISTER_SUCCESS:
            return{
                ...state,
                isAuthenticated: false
            }
        case LOGIN_SUCCESS:
            return {
                ...state,
                isAuthenticated: true,
            }
        case LOGOUT_SUCCESS:
            return {
                ...state,
                isAuthenticated: false,
            }
        case UNLOCKSCREEN_SUCCESS:
        case LOCKSCREEN_SUCCESS:
            return {
                ...state,
                // isAuthenticated: false,
                data: payload.data
            }
        case LOAD_PERMISSIONS_USER_FAIL:
        case UNLOCKSCREEN_FAIL:
        case LOCKSCREEN_FAIL:
        case REGISTER_FAIL:
        case LOGIN_FAIL:
        case LOGOUT_FAIL:
            
            return state
        default:
            return state
    };

};