import React, { useEffect,useMemo, useState,useRef, useCallback } from 'react'
import {load_all_projects,update_project }from '../../actions/project'
import { connect } from 'react-redux'
import MaterialReactTable from 'material-react-table';
import { Country, State, City }  from 'country-state-city';
import { Link } from 'react-router-dom';
import Modal from 'react-bootstrap/Modal';
import { LinearProgress } from '@material-ui/core';


// Redux

// EXCEL LIBRARY
import XLSX from 'sheetjs-style'
const ProjectList = (props) => {

    const [edit,setEdit] = useState(false)
    const [provincies,setProvincies] = useState([])
    const isCliked = useRef(null)
    const [formData,setformData] = useState([])
    const [openModal,setOpenModal] = useState(false)
    const [isLoading,setIsLoading] = useState(true)
    var cities = State.getAllStates()
   
    const countryOnchange = (e,rowIndex) => {
        
      
        let provincies_array = []
        var countryCode = ""
        if(e === "Nederland")
        {
            countryCode = "NL"
        }else if(e === "England")
        {
            countryCode = "GB"
        }

        for(let i in cities){
          if(cities[i].countryCode === countryCode)
          {
            console.log(cities[i].countryCode)
            provincies_array.push({value:cities[i].name,name:cities[i].name},)
          }
    
        }
        // console.log(provincies_array)
        // formData.land[rowIndex] = e
        setProvincies(provincies_array)
      }

    useEffect(() => {
        props.load_all_projects()  
    },[])

    useEffect(() => {
        setIsLoading(true)
        if(props.context !== undefined){
            setformData(props.context)
            setIsLoading(false)
        }
    },[props.context])
    
   

    const [form,setForm] = useState({})
    const [messageAlert,setMessageAlert] = useState(null)

    function padTo2Digits(num) {
        return num.toString().padStart(2, '0');
      }
    const returnDateFormat = (date) => {
        console.log(date)
        if(date === null || date === undefined)
        {
            // alert("message date can not be null")
            setMessageAlert(true)
        }else{
            const new_date = new Date(date)
            {
                return [
                    padTo2Digits(new_date.getDate()),
                    padTo2Digits(new_date.getMonth() + 1),
                    new_date.getFullYear(),
                ].join('-');

            }
        }
        
        
    }

    const get_row_index = (e) => {
        return e
    }
    const modal_function_bewerken = () => {
       
        // const row = get_row_index()
        return(<Modal
        show={openModal}
        onHide = {setOpenModal(false)}
        animation={true}
        centered={true}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
        >
            <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
                Project Bewerken 
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className='card card-animate'>
                    <form action='' novalidate>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="nameInput" className="form-label">Projectnaam</label>
                            </div>
                        </div>
                    </form>
                </div>
            </Modal.Body>

        </Modal>)
    }
    const editRow = (row) => {
        console.log(row.row.original.projectnr)
        setOpenModal(true)
        {modal_function_bewerken()}
    }

    const cancelEdit = () => { 
       isCliked.current = null
       setEdit(null)  
    }

    const DeleteRow = (e) => {

    }
    const getData = e => setForm({...form, [e.target.name]: returnDateFormat(e.target.value)})

    const handleSaveRow = async (e) => {

        e.preventDefault()
        console.log(form)
        if(messageAlert !== true)
        {
            props.update_project(form)
            isCliked.current = null
        }else{
            // alert('date must not be null')
        }
    
      };

    const columns = useMemo(
        () => [
            {
                accessorKey: 'projectnr',
                header: "Project nummer",
                Cell:({cell}) => {
                   return  (<a class="fw-semibold" href="#">{cell.getValue()}</a>)
                }
            },
            {
                accessorKey: 'klantnaam',
                header: "Klant naam",
                Cell:({ cell,row }) => {
       
                    return (row.index === isCliked.current ? <input className='form-control form-control-sm' defaultValue={cell.getValue()} name="klantnaam" onBlur={(e) => getData(e)} />:cell.getValue())
                } 
            },
            {
                accessorKey: 'renovatie_nieuwbouw',
                header: "Renovatie / Nieuwbouw",
                Cell:({ cell,row }) => {
       
                    return (row.index ===  isCliked.current ? 
                        <select
                        className="form-select form-select-sm"
                        defaultValue={cell.getValue()}
                        onBlur={(e) => getData(e)}
                        name="renovatie_nieuwbouw"
                         >
                            <option value={"Renovatie"}>Renovatie</option>
                            <option value={"Nieuwbouw"}>Nieuwbouw</option>
                        </select>
                    // <input className='form-control form-control-sm' defaultValue={cell.getValue()} onChange={() => getData(cell)} />
                    :
                    cell.getValue())
                } 
            },
            {
                accessorKey: 'projectstatus',
                header: "Project status",
                Cell:({ cell,row }) => {
       
                    return (row.index ===  isCliked.current ? 
                        <select 
                        className="form-select form-select-sm"
                        onBlur={(e) => getData(e)}
                        defaultValue={cell.getValue()}
                        name="projectstatus"
                        >
                            <option value={"Lead"}>Lead</option>
                            <option value={"Offerte"}>Offerte</option>
                            <option value={"Onderhandeling"}>Onderhandeling</option>
                            <option value={"uitvoering"}>In uitvoering</option>
                            <option value={"In service"}>In service</option>
                        </select>
                    // <input className='form-control form-control-sm' defaultValue={cell.getValue()} onChange={() => getData(cell)} />
                    :
                    cell.getValue())
                } 
            },
            {
                accessorKey: 'projectnaam',
                header: "Project naam",
                Cell:({ cell,row }) => {
       
                    return (row.index ===  isCliked.current ? <input className='form-control form-control-sm' name="projectnaam" defaultValue={cell.getValue()} onBlur={(e) => getData(e)} />:cell.getValue())
                } 
            },
            {
                accessorKey: 'sum',
                header: "-Sum"
            },
            {
                accessorKey: 'i',
                header: "-I"
            },
            {
                accessorKey: 'p',
                header: "-P"
            },
            {
                accessorKey: 'e',
                header: "-E"
            },
            {
                accessorKey:'f',
                header: "-F"
            },
            {
                accessorKey:'a',
                header: "-A"
            },
            {
                accessorKey:'something',
                header: "-"
            },
            {
                accessorKey:'afgifte_opwek',
                header: "Afgifte/Opwek"
            },
            {
                accessorKey:'provincie',
                header: "Provincies",
                Cell:({ cell,row }) => {
               
                    return (row.index ===  isCliked.current ? 
                        <select 
                        className="form-select form-select-sm"
                        name='provincie'
                        defaultValue={cell.getValue()}
                        onBlur={(e) => getData(e)}
                        >
                            { provincies !== undefined ? provincies.map( (item,index) =>  <option key={index} value={item.value}>{item.name}</option> ) : 'Kies eerste land'}
                        </select>
                    :
                    cell.getValue())
                } 

            },
            {
                accessorKey:'land',
                header: "Land",
                Cell: ({ cell, row}) => {
                    return (row.index ===  isCliked.current ?
                            <select
                            className="form-select form-select-sm"
                            defaultValue={cell.getValue()}
                            name="land"
                            onChange={(e) => countryOnchange(e.target.value,row.index)}
                              >
                                <option value={"Nederland"}>Nederland</option>
                                <option value={"England"}>England</option>
                            </select>
                            :
                            cell.getValue())
                }

            },
            {
                accessorKey:'projectsom',
                header: "Project sum"
            },
            {
                accessorKey:'datum_systeem_invoer',
                header: "Datum systeem invoer",
                Cell:({ cell,row }) => {

                    return ( row.index ===  isCliked.current ?
                        <input type='date' name='datum_systeem_invoer' className='form-control form-control-sm' defaultValue={cell.getValue()} onBlur={(e) => getData(e)} />
                        :
                        cell.getValue()
                        )
                }
            },
            {
                accessorKey:'startdatum',
                header: "Start datum",
                Cell:({ cell,row }) => {
                    return ( row.index ===  isCliked.current ?
                        <input type='date' name='startdatum' className='form-control form-control-sm' defaultValue={cell.getValue()} onBlur={(e) => getData(e)} />
                        :
                        cell.getValue()
                        )
                }
            },
            {
                accessorKey:'offertedatum',
                header: "Offerte datum",
                Cell:({ cell,row }) => {
                    return ( row.index ===  isCliked.current ?
                        <input type='date' name='offertedatum' className='form-control form-control-sm' defaultValue={cell.getValue()} onChange={(e) => getData(e)} />
                        :
                        cell.getValue()
                        )
                }
            },
            {
                accessorKey:'actions',
                header: "Actions",
                Cell:(row) => {
                    return (
                        <>
                        <div className="actions_buttons" ref={isCliked}>
                            {isCliked.current !== row.row.index ? 
                            <>
                                <Link to={'/bewerkerken_project/'+row.row.original.id} ><i class=" ri-edit-2-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                                <Link onClick={() => DeleteRow(row)}><i class=" ri-delete-bin-6-line"></i></Link>
                            </>
                            : 
                            <>
                                <Link onClick={() => cancelEdit()}><i class="ri-close-fill"></i></Link>&nbsp;&nbsp;&nbsp;&nbsp;
                                <Link onClick={(e) => handleSaveRow(e)}><i class=" ri-check-fill "></i></Link>
                            </>
                            
                            }
                        </div>
                        
                        </>)
                }
            },   

        ],[countryOnchange, editRow, getData, handleSaveRow, provincies, returnDateFormat]
    )
   

 
    const exportExcelFunctie = (data,filename) => {
        var jsonData = data
        // for(var i in data)
        // {
        //    jsonData.push(data[i].mpodata)
           
        // }
       
         // console.log(data[i].mpodata)
        // if(typeof XLSX === 'undefined') XLSX = require('xlsx');
         /* make the worksheet */
        var ws = XLSX.utils.json_to_sheet(jsonData);

         /* add to workbook */
         
        var wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, `Project List ${filename}`);

         /* generate an XLSX file */
        XLSX.writeFile(wb, filename+".xlsx");
             
    }
    const excelExport = async (args) => {

        var jsonData = []
    
        for(var i in args){
            console.log(args[i].original)
            jsonData.push(
                {
                    'ID': args[i].original.id,
                    'Project nummer': args[i].original.projectnr,
                    'Klant naam': args[i].original.klantnaam,
                    'Renovatie / Nieuwbouw': args[i].original.renovatie_nieuwbouw,
                    'Project status': args[i].original.projectstatus,
                    'Project naam': args[i].original.projectnaam,
                    'Sum': args[i].original.sum,
                    'I': args[i].original.i,
                    'P': args[i].original.p,
                    'E': args[i].original.e,
                    'F': args[i].original.f,
                    'A': args[i].original.a,
                    '-': args[i].original.something,
                    'Afgifte / Opwek': args[i].original.afgifte_opwek,
                    'Energie prestatie garantie': args[i].original.energie_prestatie_garantie,
                    'Plaats': args[i].original.plaats,
                    'Provincie': args[i].original.provincie,
                    'Land': args[i].original.land,
                    'Project sum': args[i].original.projectsom,
                    'Datum systeem invoer': returnDateFormat(args[i].original.datum_systeem_invoer),
                    'Start datum': returnDateFormat(args[i].original.startdatum),
                    'Offerte datum': returnDateFormat(args[i].original.offertedatum),


                })

        }
        exportExcelFunctie(jsonData,`Project list `)
    }


  return (
    <>
    
    <div>
        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
            <h4 class="mb-sm-0">Projecten Overzicht</h4>
        </div>
        {isLoading ? <LinearProgress /> : 
            <MaterialReactTable 
            columns={columns}
            data={formData ? formData:<LinearProgress />}
            enableColumnResizing
            enableRowVirtualization
            enableRowSelection={true}
            muiSelectCheckboxProps={{
                color: 'primary',
            }}
            initialState={{ 
                density: 'compact' ,
                columnPinning: {right: ['actions'] },

            }}
            enablePinning
            renderTopToolbarCustomActions={({ table }) => (
                <>
                    <button id="exportexelButton" type="button" onClick={() => excelExport(table.getSelectedRowModel().rows)} className="btn btn-primary btn-sm"><i class=" ri-file-excel-2-line"></i> Excel export</button>
                </>
            )}

            />
        
        
        } 
        
       
       
        {/* </div> */}
    </div>
    </>
   
  )
}

const getALLprojectsitems = state => ({
    context: state.project.projectsall,
  
  })
export default connect(getALLprojectsitems,{load_all_projects,update_project})(ProjectList)