import React from 'react'
import SearchNavbar from '../../components/SearchNavbar'


const Opleverrapport = () => {
  const path = "/opleverrapport/mpo/"
  return (
    <>
    <div>Opleverrapport</div>
        <div className='navigation'>
            <SearchNavbar path={path}/>
        </div>
       <div>
        <p>Opleverrapport pagina. via de breadcumb kies een project</p>
       </div>
    </>
  )
}

export default Opleverrapport