import React,{useState,useMemo,useEffect} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import { Link } from "react-router-dom";
import $ from 'jquery'
const Bewoners = (props) => {

    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true);

    const aanhefOpties = ['Dhr','Mevr']

    useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)
    },[props.props.data])
    const updateData = index => e => {
    
        let newArray = [...data];
        newArray[index][e.target.name] = e.target.value 
        props.props.setData(newArray)
        
      }

    const columns = useMemo(
        () => [
          {
            accessorKey: 'project',
            header: "Project nummer",
            Cell: ({ cell }) =>
            <a class="fw-semibold" href='#'>{cell.getValue() ? cell.getValue():''}</a>

          },
     
          {
            accessorKey: 'bouwnr',
            header: "Bouwnr",
          },
          {
            accessorKey: 'aanhef',
            header: "Aanhef",
            Cell: ({ cell,row }) =>
            
              props.props.bewerken_actif === true ?
              <select
                onBlur={updateData(row.index)}
                defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
                className='form-select form-select-sm'
                name="aanhef"
              >
                {aanhefOpties.map(item => <option value={item}>{item}</option>)}

              </select>:
              cell.getValue() ? cell.getValue():''
              
          },
  
          {
            accessorKey: 'achternaam',
            header: "Achternaam",
            Cell: ({ cell,row }) =>

            props.props.bewerken_actif === true ?
            <input type='text' name="achternaam" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />:
            cell.getValue() ? cell.getValue():''
          },
          {
            accessorKey: 'voorletter',
            header: "Voorletter",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ?
            <input type='text' name="voorletter" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },
          {
            accessorKey: 'phone',
            header: "Phone",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ?
            <input type='text' name="phone" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },
          {
            accessorKey: 'tussenvoegsel',
            header: "Tussenvoegsel",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ?
            <input type='text' name="tussenvoegsel" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },
          {
            accessorKey: 'email',
            header: "Email",
            Cell: ({ cell,row }) =>
            props.props.bewerken_actif === true ?
            <input type='text' name="email" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
            :
            cell.getValue() ? cell.getValue():''
          },

  
      ],[ props.props.bewerken_actif, updateData,])
   
    useEffect(() => {
        $(function () {
        //   $("#table9").enableCellNavigation();
          // $("#table").DataTable();
        });
    
       })
  
    return (
      isLoading ? <LinearProgress />:
        <MaterialReactTable
          columns={columns}
          data={data ? data: []}
          enablePinning
          enableColumnResizing
          initialState={{ 
            density: 'compact' ,
            columnPinning: { left: ['bouwnr'] },
        }}
        enableRowVirtualization
        muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
        
        />
        
    )
}

export default Bewoners
