import React, {useEffect} from 'react'
import {BrowserRouter as Router,Routes,Route} from 'react-router-dom'
import { Provider } from 'react-redux'
import store from './store.js'
import Login from './containers/Login'
import Home from './containers/Home'
import Register from './containers/Register'
import TestComponent from './components/TestComponent'
import Check from './hocs/Check'
import ProtectedRoute from './utils/ProtectedRoutes'
import Profile from './containers/User/Profile'
import Userprofile from './containers/User/Userprofile.js'
import Medewerker from './containers/Admin/Medewerker'
// import Project from './containers/Projecten/Project'
import Roles from './containers/Admin/Roles'
import Functies from './containers/Admin/Functies.js'
import Productiebon from './containers/Productie/Productiebon.js'
import Testrapport from './containers/Productie/Testrapport.js'
import Opleverrapport from './containers/Productie/Opleverrapport.js'
import Productiestatusicem from './containers/Productie/Productiestatusicem.js'
