export const get_projectById = (id) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
            
        }
    };
    const body = JSON.stringify(id)
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/project/getProjectID`,body,config);
        if(res.data.dataprojectbyID){

            dispatch ({
                type: GET_PROJECTBYID_SUCCESS,
                payload: res.data.dataprojectbyID
            });
            // dispatch(loadallprojectsList())
            dispatch(getRoles())
            dispatch(getFuncties())
            dispatch(load_bedrijf_aanemer_bedrijf())
            // dispatch(mpoGet(id))
            
        }
        else{
            dispatch ({
                type: GET_PROJECTBYID_FAIL,
                // payload: res.data
            })
            
        }
    }catch (err){
        dispatch ({
            type: GET_PROJECTBYID_FAIL,
            payload: err
        });
    }
}

