import React,{useEffect, useRef,useState} from 'react'
import { Link } from 'react-router-dom';
import { getAllsitesByProject } from '../../../actions/mpo'
import { useNavigate } from "react-router-dom";
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'

const Sidebar = ({projects,menuState,getAllsitesByProject,permissions}) => {

    const projects_array = projects
    const sites = projects.sites
    const siteLinks = useRef('')
    const [permissionsArray,setpermissionsArray] = useState([])
   
    const navigate = useNavigate();
    useEffect(() => {
        if(permissions)
        {
            setpermissionsArray(permissions.data)
        }
    },[permissions])
    const getAllsites_from_Project = (id,path) => {
        // get all site from the project
        if(getAllsitesByProject(JSON.stringify(id)))
        {
            if(sites !== undefined)
            {
               siteLinks.current =  sites.map((site) => <>
                    <Link to="" className="nav-item">
                        <a href="apps-email-basic.html" className="nav-link" data-key="t-basic-action"> {site.id} </a>
                    </Link>
               </>)
            }
        }
        navigate(path+id)
    }

    const { name} = useParams()
    
    const listItems = projects_array.map(
        (project) => <>
            <li key={project.id} className="nav-item">
                <a href="#sidebaremailTemplates" onClick={() => getAllsites_from_Project(project.id,'/mpo/')} className="nav-link" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebaremailTemplates" data-key="t-email-templates">
                {project.projectnr} <small>{project.projectnaam}</small> 
                </a>
                <div className="collapse menu-dropdown" id="sidebaremailTemplates">
                     <ul className="nav nav-sm flex-column">
                        {siteLinks.current}
                     </ul>
                </div>
                
            </li>
        </>  );
    const listItems_productiebon = projects_array.map(
        (project) =>
        <>
            <li className="nav-item">
                <Link href="#sidebarProductieTemplates" to={"/productiebon/"+project.id} className="nav-link" data-key="t-basic">
                {project.projectnr + ' ' + project.projectnaam}
                </Link>
            </li>
        </>
    )
    const listItems_testrapport = projects_array.map(
        (project) =>
        <>
            <li className="nav-item">
                <Link href="#sidebarProductieTemplates" to={"/testrapport/"+project.id} className="nav-link" data-key="t-basic">
                {project.projectnr + ' ' + project.projectnaam}
                </Link>
            </li>
        </>
    )
    const listItems_opleverrapport = projects_array.map(
        (project) => 
        <>
            <li className="nav-item">
                <Link  to={"/opleverrapport/"+project.id} className="nav-link" data-key="t-basic">
                    {project.projectnr + ' ' + project.projectnaam} 
                </Link>
            </li>
        </>
    )
    const listItems_productiestatusIcem = projects_array.map(
        
        (project) => <>
            <li className="nav-item">
                <Link  to={"/productiestatusicem/"+project.id} className="nav-link" data-key="t-basic">
                    {project.projectnr + ' ' + project.projectnaam} 
                </Link>
            </li>
        </>
    )


    const display_navbar = () => {
        let array_linkItemsSearchNavbar = null 
        switch (name) {
            case 'mpo':
                array_linkItemsSearchNavbar = listItems
                break
            case 'productiebon':
                
                array_linkItemsSearchNavbar = listItems_productiebon
                break;
            case 'testrapport':
                array_linkItemsSearchNavbar = listItems_testrapport
                
                break;
            case 'opleverrapport':
                
                array_linkItemsSearchNavbar = listItems_opleverrapport
                break;
            case 'productiestatusicem':
               
                array_linkItemsSearchNavbar = listItems_productiestatusIcem
                break;
            default:
                
                array_linkItemsSearchNavbar = null
    
        }
        return (
            array_linkItemsSearchNavbar !== null ?
                <>
                    <ul className='navbar-nav'>
                        {array_linkItemsSearchNavbar}
                    </ul>
                </>
             :
             null
        )
    }

    console.log(display_navbar())
   
    const isClicked = useRef(false);
    const [menusmall,setMenusmall] = useState('')
    const [windowSize,setWindowSize] = useState({
        width: window.innerWidth
    });
    const classNavbar = useRef(null) 
    useEffect(() => {
        setMenusmall(menuState)
    },[menuState])

    useEffect(() => {
        function handleResize(){
            setWindowSize({ width: window.innerWidth}) 
        }
        window.addEventListener('resize', handleResize)
    },[])
    if(windowSize.width < 767)
        {
           
            classNavbar.current = 'container_navextra3'
    }else
    {
        if(menusmall === "sm")
        {
            classNavbar.current = 'container_navextra2'
        }else{
            classNavbar.current = 'container_navextra'
        }
    }
    
    console.log()
    
    const showNavbar = () => {
        
        if (isClicked.current !== true)
        {
            isClicked.current = true
            
        }else{
            isClicked.current = false
        }
    }
    const [searchvalue,setSearchvalue] = useState('')  
    // console.log(array_linkItemsSearchNavbar.current)
    // console.log(listItems)
  return (
    <>
        <div className="app-menu navbar-menu">
            {/* <!-- LOGO --> */}
            <div className="navbar-brand-box">
                {/* <!-- Dark Logo--> */}
                <Link to="/" className="logo logo-dark">
                    <span className="logo-sm">
                        <img src={process.env.PUBLIC_URL + "/assets/logo_transparent.png"} alt="" height="22" />
                    </span>
                    <span className="logo-lg">
                        <img src={process.env.PUBLIC_URL + "/assets/logo_transparent.png"} alt="" height="17" />
                    </span>
                </Link>
                {/* <!-- Light Logo--> */}
                <Link href="/" className="logo logo-light">
                    <span className="logo-sm">
                        <img src={process.env.PUBLIC_URL + "/assets/logo_transparent2.png"} alt="" height="35" />
                    </span>
                    <span className="logo-lg">
                        <img src={process.env.PUBLIC_URL + "/assets/logo_transparent.png"} alt="" width='194px' />
                    </span>
                </Link>
                <button type="button" className="btn btn-sm p-0 fs-20 header-item float-end btn-vertical-sm-hover" id="vertical-hover">
                    <i className="ri-record-circle-line"></i>
                </button>
            </div>
           
            <div id="scrollbar">
                <div className="container-fluid">
                    <div id="two-column-menu">
                    </div>
                    <ul className="navbar-nav" id="navbar-nav">
                        <li className="menu-title"><span data-key="t-menu">Menu</span></li>
                         {/* <!-- Dashboard Menu --> */}
                        {/* <li className="nav-item">
                            <a className="nav-link menu-link" href="#sidebarDashboards" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebarDashboards">
                                <i className="ri-dashboard-2-line"></i> <span data-key="t-dashboards">Dashboards</span>
                            </a>
                            <div className="collapse menu-dropdown" id="sidebarDashboards">
                                <ul className="nav nav-sm flex-column">
                                    <li className="nav-item">
                                        <a href="dashboard-analytics.html" className="nav-link" data-key="t-analytics"> Analytics </a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="dashboard-crm.html" className="nav-link" data-key="t-crm"> CRM </a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="index.html" className="nav-link" data-key="t-ecommerce"> Ecommerce </a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="dashboard-crypto.html" className="nav-link" data-key="t-crypto"> Crypto </a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="dashboard-projects.html" className="nav-link" data-key="t-projects"> Projects </a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="dashboard-nft.html" className="nav-link" data-key="t-nft"> NFT</a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="dashboard-job.html" className="nav-link"><span data-key="t-job">Job</span> <span className="badge badge-pill bg-success" data-key="t-new">New</span></a>
                                    </li>
                                </ul>
                            </div>
                        </li> */}
                        {/* <!-- end Dashboard Menu --> */}

                        {/* <!-- Project Menu --> */}
                        { permissionsArray && permissionsArray.includes('view_project') | permissionsArray.includes('add_permission') ?
                            <>
                             <li className="nav-item">
                                    <a className="nav-link menu-link" href="#sidebarApps" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebarApps">
                                        <i className="ri-apps-2-line"></i> <span data-key="t-apps">Projects</span>
                                    </a>
                                    <div className="collapse menu-dropdown" id="sidebarApps">
                                        <ul className="nav nav-sm flex-column">
                                            {/* Functon to return nav */}
                                        
                                            <li className="nav-item">
                                                <Link to="/projectaanmaken" className="nav-link" data-key="projectaanmaken"> Project Aanmaken </Link>
                                            </li>

                                            {/* <li className="nav-item">
                                                <Link to="/projectenoverzicht" className="nav-link" data-key="projectlist"> Projects list </Link>
                                            </li> */}
                                            <li className="nav-item">
                                                <Link to="/projectenlist" className="nav-link" data-key="projectlist"> Project lijsten</Link>
                                            </li>
                                            <li className="nav-item">
                                                <Link to={'/mpo/'+ 1} className="nav-link" data-key="mpo"> MPO </Link>
                                            </li>
                                            {/* <li className="nav-item">
                                            <Link to={'/mpo'} className="nav-link" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebarEmail" data-key="t-email">
                                                Mpo
                                            </Link> */}
                                        {/* <div className="collapse menu-dropdown" id="sidebarEmail">
                                            <ul className="nav nav-sm flex-column">
                                                {listItems}  
                                            </ul>
                                        </div> */}
                                    {/* </li> */}
                                            
                                        </ul>
                                    </div>
                                </li>
                            </>
                        :
                        null
                        }

                        {/* <!-- end Project Menu --> */}
                         {/* <!-- Productie Menu --> */}
                        { permissionsArray && permissionsArray.includes('view_productiebonstatus') | permissionsArray.includes('add_permission') ? 
                        
                         <li className="nav-item">
                            <a className="nav-link menu-link" href="#sidebarAuth" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebarAuth">
                                <i className="ri-account-circle-line"></i> <span data-key="t-authentication">Productie</span>
                            </a>
                            <div className="collapse menu-dropdown" id="sidebarAuth">
                                <ul className="nav nav-sm flex-column">
                                    <li className="nav-item">
                                        <Link to={'/productiebon/'+ 1} className="nav-link" data-key="t-chat"> Productiebon
                                        </Link>
                                        {/* <div className="collapse menu-dropdown" id="sidebarSignIn">
                                            <ul className="nav nav-sm flex-column">
                                                {listItems_productiebon}  
                                            </ul>
                                        </div> */}
                                    </li>
                                    <li className="nav-item">
                                        <Link to={'/testrapport/'+ 1} className="nav-link" data-key="t-chat"> 
                                            Testrapport
                                        </Link>
                                        {/* <div className="collapse menu-dropdown" id="sidebarSignUp">
                                            <ul className="nav nav-sm flex-column">
                                                {listItems_testrapport}
                                            </ul>
                                        </div> */}
                                    </li>

                                    <li className="nav-item">
                                        <Link to={'/opleverrapport/'+ 1} className="nav-link" data-key="t-chat">
                                            Opleverrapport
                                        </Link>
                                        {/* <div className="collapse menu-dropdown" id="sidebarResetPass">
                                            <ul className="nav nav-sm flex-column">
                                                {listItems_opleverrapport}
                                            </ul>
                                        </div> */}
                                    </li>
                                    <li className="nav-item">
                                        <Link to={'/productiestatusicem/'+ 1} className="nav-link" data-key="t-chat">
                                            Productiestatusicem
                                        </Link>
                                    </li>
                                    {/* <li className="nav-item">
                                        <Link to={'/testComponet'} className="nav-link" data-key="t-chat"> Test component </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to={'/mpocomponent/'+1} className="nav-link" data-key="t-chat"> Mpo new </Link>
                                    </li> */}
                                </ul>
                            </div>
                         </li>
                        :
                        null
                        }
                         
                         {/* <!-- end Productie Menu --> */}
                        
                        { permissionsArray && permissionsArray.includes('view_customuser') | permissionsArray.includes('add_permission') ? 
                        <>
                             <li className='nav-item'>
                            <a className="nav-link menu-link" href="#sidebarAdmin" data-bs-toggle="collapse" role="button" aria-expanded="false" aria-controls="sidebarAuth">
                                <i className="ri-account-circle-line"></i> <span data-key="t-authentication">Admin</span>
                            </a>
                            <div className="collapse menu-dropdown" id="sidebarAdmin">
                                <ul className="nav nav-sm flex-column">
                                    <li className="nav-item">
                                        <Link to={'/medewerker/'} className="nav-link" data-key="t-chat"> Medeweker overzicht
                                        </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to={'/roleoverzicht/'} className="nav-link" data-key="t-chat"> Role overzicht
                                        </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to={'/functieoverzicht/'} className="nav-link" data-key="t-chat"> Functie overzicht
                                        </Link>
                                    </li>
                                </ul>
                            </div>

                         </li>
                        </>
                         :
                         null
                        }
                         
                    </ul>
                </div>
                {/* <!-- Sidebar --> */}
            </div>

        </div>
        {/* <!-- Left Sidebar End --> */}
        {/* <!-- Vertical Overlay--> */}
         {display_navbar() !== null ? 
            <div className={classNavbar.current}>
            <div className='btn-position'>
                <Link className={isClicked.current !== false ? 'bull-red_1_actif ': 'bull-red_1_inactif'} onClick={showNavbar}>{isClicked.current !== false ?  <i class='bx bxs-chevron-left icon' ></i>:<i class='bx bxs-chevron-right icon' ></i>}</Link>
                {/* <Link className='bull-red_2 actif' id="actif" onClick={showNavbar}><i class='bx bxs-chevron-left' ></i></Link> */}
            </div>
            <div  className={isClicked.current !== false ? menusmall !== 'sm'? ' sidesidebar': ' sidesidebar2': ' sidesidebar inactif_customcss'}>
            <div className='searchNav' >
                <form className="app-search d-none d-md-block">
                    <div className="position-relative">
                        <input type="text" onChange={(e) => setSearchvalue(e.target.value)} className="form-control" placeholder="Search..." autoComplete="off" id="search-options" value={searchvalue} />
                        <span className="mdi mdi-magnify search-widget-icon"></span>
                        <span className="mdi mdi-close-circle search-widget-icon search-widget-icon-close d-none" id="search-close-options"></span>
                    </div>
                </form>
                <ul className="nav nav-sm flex-column">
                    {display_navbar()}
                </ul>
                {/* {searchMenu_Items.length === 0 || searchMenu_Items === null || searchMenu_Items === undefined ? `Geen item geselecteerd`: searchMenu_Items} */}
           
                
            </div>

            </div>
        </div>
        : null }
       
        <div className="sidebar-background"></div>
        <div classNameName="vertical-overlay"></div>
        </>
        )
}
const mapStateToProps = state => ({ 
    sites: state.mpo.sites,
    projects: state.project.projects,
    permissions: state.auth.permissions
  
  })
export default connect(mapStateToProps,{getAllsitesByProject})(Sidebar)