import axios from "axios";
import Cookies from 'js-cookie'
import {
    AANMAKEN_TESTRAPPORT_SUCCESS,
    AANMAKEN_TESTRAPPORT_FAIL,
    LOAD_TESTRAPPORT_SUCCESS,
    LOAD_TESTRAPPORT_FAIL,
    LOAD_TESTRAPPORTOVERZICHT_SUCCESS,
    LOAD_TESTRAPPORTOVERZICHT_FAIL
} from './types'

axios.defaults.withCredentials = true

export const testrapportgetbyId = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = formData
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/testrapport/get-testrapportById `,body,config)
        
        if(res.data){
            dispatch({
                type: LOAD_TESTRAPPORT_SUCCESS,
                payload: res.data
            })
        }
        else{
            dispatch({LOAD_TESTRAPPORT_FAIL,
                payload: res.data.error
            })
        }
    }
    catch (err) {
        dispatch({
            type: LOAD_TESTRAPPORT_FAIL
        });
        // dispatch(load_medewerkers)
    }
}
export const testrapportAanmaken = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    const body = JSON.stringify(formData)
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/testrapport/create-testrapport`,body,config)
        
        if(res.data.success){
            dispatch({
                type: AANMAKEN_TESTRAPPORT_SUCCESS,
                payload: res.data.success
            })
            var formdata = {
                'site_id': formData.site_id,
                'project_id': formData.project_id
            }
            dispatch(testrapportgetbyId(formdata))
            // dispatch(load_medewerkers())
            // dispatch(load_project())
            // dispatch(mpoGet(id))
        }else{
            dispatch({AANMAKEN_TESTRAPPORT_FAIL,
                payload: res.data.error
            })
            // dispatch(load_medewerkers)
        }
    }
    catch (err) {
        dispatch({
            type: AANMAKEN_TESTRAPPORT_FAIL
        });
        // dispatch(load_medewerkers)
    }
}

