import axios from "axios";
import Cookies from 'js-cookie'
import { load_project,get_projectById } from "./project";
import { getRoles,getFuncties } from "./admin";
import {
    LOAD_MEDEWERKERS_SUCCESS,
    LOAD_MEDEWERKERS_FAIL,
    AANMAKEN_MEDEWERKERS_SUCCESS,
    AANMAKEN_MEDEWERKERS_FAIL,
    UPDATED_MEDEWERKERS_SUCCESS,
    UPDATED_MEDEWERKERS_FAIL,
    LOAD_ALL_MEDEWERKERS_PERMISSIONS_SUCCESS,
    LOAD_ALL_MEDEWERKERS_PERMISSIONS_FAIL

   
} from './types'


axios.defaults.withCredentials = true

export const medewerker_aanmaken = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }

    };
    
    const body = JSON.stringify(formData)
   
    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/users/all-medewerkerAanmaken`,body,config)
        if(res.data.success){
            dispatch({
                type: AANMAKEN_MEDEWERKERS_SUCCESS,
                payload: res.data.success
            })
            dispatch(load_medewerkers())
            dispatch(load_project())
            if(formData.projectid.trim())
            {
                dispatch(get_projectById(formData.projectid))
            }
            
        }else{
            dispatch({
                type: AANMAKEN_MEDEWERKERS_FAIL,
                payload: res.data.error
            })
            // dispatch(load_medewerkers)
        }
    }
    catch (err) {
        dispatch({
            type: AANMAKEN_MEDEWERKERS_FAIL,
            payload: err
        });
        dispatch(load_medewerkers)
    }
}
// update medewerker
export const medewerker_updated = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }

    }
    const body = formData

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/users/medewerkers_update`,body,config)
        if(res.data.success){
            dispatch({
                type: UPDATED_MEDEWERKERS_SUCCESS,
                payload: res.data.success
            })
            dispatch(load_medewerkers())
        }else{
            dispatch({
                type: UPDATED_MEDEWERKERS_FAIL,
                payload: res.data.error
            })
        }
    }
    catch (err) {
        dispatch({
            type: UPDATED_MEDEWERKERS_FAIL
        });
    }
}

export const load_medewerkers = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            // 'X-CSRFToken': Cookies.get('csrftoken'),
            
        }
    };
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/users/get-allmedewerkers`,{withCredentials:true},config);
        if(res.data.success){
            dispatch ({
                type: LOAD_MEDEWERKERS_SUCCESS,
                payload: res.data
            });
            dispatch(getRoles())
            dispatch(getFuncties())
        }
        else{
            dispatch ({
                type: LOAD_MEDEWERKERS_FAIL,
                // payload: res.data
            })
        }
        
    }catch (err){

        dispatch ({
            type: LOAD_MEDEWERKERS_FAIL,
            payload: err
        });
    }
    

}

