import {
    AANMAKEN_TESTRAPPORT_SUCCESS,
    AANMAKEN_TESTRAPPORT_FAIL,
    LOAD_TESTRAPPORT_SUCCESS,
    LOAD_TESTRAPPORT_FAIL,
    LOAD_TESTRAPPORTOVERZICHT_SUCCESS,
    LOAD_TESTRAPPORTOVERZICHT_FAIL

} from '../actions/types'

const initialState = {
    is_success:'',
    data:[]                                                                                          
 }

 export default function(state= initialState, action){
    const { type, payload } = action;
    switch(type){
        case LOAD_TESTRAPPORTOVERZICHT_SUCCESS:
        case LOAD_TESTRAPPORT_SUCCESS:
        case AANMAKEN_TESTRAPPORT_SUCCESS:
            return {...state,
            is_success: true,
            data: payload.context
            	}
        case LOAD_TESTRAPPORTOVERZICHT_FAIL:
        case LOAD_TESTRAPPORT_FAIL:
        case AANMAKEN_TESTRAPPORT_FAIL:
            return {
            is_success: false
                }
                    
            default:
                return state
    }
 }