import React, {useEffect, useState} from 'react'
import {Box, CircularProgress, Fab} from '@mui/material'
import { green } from '@mui/material/colors'
import { update_project } from '../../actions/project';
import SaveIcon from '@mui/icons-material/Save';
import CheckIcon from '@mui/icons-material/Check';
import { connect } from 'react-redux';

const UserActions = ({params, rowId, setRowId,update_project}) => {
  const [loading, setLoading] = useState(false) 
  const [success, setSuccess] = useState(false)

  const handleSubmit = () => {
    setLoading(true)
    setTimeout(async () => {
        const {projectnaam,projectnr,id} = params.row
        const formData = {projectnaam,projectnr,id}
        console.log(formData)
        const result = update_project(JSON.stringify(formData))
        if(result){
            setSuccess(true)
            setRowId(null)
        }
        setLoading(false)

    }, 1500)
   
  }

  useEffect(()=>{
    if(rowId === params.id && setSuccess(false));

  },[rowId])
  return (
    <Box
        sx={{
            m:1,
            position:'relative'
        }}
        >
            {success ? (
                <Fab
                color='primary'
                sx={{
                    width:40,
                    height: 40,
                    bgColor: green[500],
                    '&:hover': {bgcolor: green[700]}
                }}
                
                >
                    <CheckIcon />
                </Fab>
            ) : (
                <Fab
                color='primary'
                sx={{
                    width:40,
                    height: 40,
                }}
                disabled={params.id !== rowId || loading}
                onClick={handleSubmit}
                >
                    <SaveIcon />
                </Fab>
            )}
            {loading && (
                <CircularProgress 
                size={52}
                sx={{
                    color: green[500],
                    position: 'absolute',
                    top: -6,
                    left:-6,
                    zIndex: 1

                }}
                />
            )}
      
    </Box>

  )
}
const getProjectItems = state => ({
    context: state.project.context
})
export default connect(getProjectItems,{update_project})(UserActions)
