import React, {useRef, useMemo, useEffect, useState } from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';


const Algemeentable = (props) => {

    const dropdown_props = ['Huur','Koop']
    
    const [data,setData] = useState(props.props.data !== null ? props.props.data:[])
    const [isLoading, setIsLoading] = useState(true);
   
    useEffect(() => {
        setIsLoading(true)
        if(props !== undefined && props.props.data !== undefined) {
          
          setData(props.props.data !== null ? props.props.data:[])
          
        }
        setIsLoading(false) 

    },[props.props.data])
 
    console.log(props.props.bewerken_actif)
    const updateData = index => e => {
    
      let newArray = [...data];
      newArray[index][e.target.name] = e.target.value 
      props.props.setData(newArray)
      
    }
   
   
   const columns = useMemo(
      () => [
        {
          accessorKey: 'bouwnr',
          header: "Bouwnr",
        },
        {
          accessorKey: 'blok',
          header: "Blok",
          Cell: ({ cell,row }) => 
          props.props.bewerken_actif === true ? 
          <input type='text' maxlength="1" style={{textTransform:"uppercase"}} name="blok" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} /> 
          : cell.getValue() ? cell.getValue():'' 
        },
        {
          accessorKey: 'straat',
          header: "Straat",
          Cell: ({ cell,row }) => props.props.bewerken_actif === true ? 
            <input type='text' name="straat" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>:
            cell.getValue() ? cell.getValue():''
        },
        {
          accessorKey: 'postcode',
          header: "Postcode",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ? 
          <input type='text' name="postcode" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>:
          cell.getValue() ? cell.getValue():''
        },
        {
          accessorKey: 'huisnr',
          header: "Huisnr",
          Cell: ({ cell,row }) => 
          props.props.bewerken_actif === true ? 
          <input type='text' name="huisnr" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''}/>:
          cell.getValue() ? cell.getValue():''
          
        },
        {
          accessorKey: 'bijzonderheden',
          header: "Bijzonderheden",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ? 
          <input type='text' name="bijzonderheden" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />:
          cell.getValue() ? cell.getValue():''
          
        },
        
        {
          accessorKey: 'koop_huur',
          header: "Koop/Huur",
          Cell: ({ cell,row }) =>
              props.props.bewerken_actif === true ? 
              <select 
              className='form-select form-select-sm'
              defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
              onBlur={updateData(row.index)}
              // disabled={props.props.bewerken_actif === true ? false:true}
              name="koop_huur"
              >
                <option> - Kies optie - </option>
                {dropdown_props.map(item => <option value={item}>{item}</option>)}
              </select>:cell.getValue() !== null ? cell.getValue(): ''
      
        },

    ]
    ,[data,props.props.bewerken_actif,updateData])

  return (
    isLoading ? <LinearProgress /> :
    <>
      <MaterialReactTable
          columns={columns}
          data={data ? data: 'Loading'}
          enableColumnResizing
          enablePinning
          initialState={{ 
            density: 'compact' ,
            columnPinning: { left: ['bouwnr'] },
        }}
        enableRowVirtualization
        muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
      />
     </>
    
 
  )
}

export default Algemeentable
