
import React,{useState,useEffect,useMemo} from 'react'
import MaterialReactTable from 'material-react-table';
import { LinearProgress } from '@material-ui/core';
import $ from 'jquery'
const Omvormer = (props) => {

    const [data,setData] = useState([])
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
      setIsLoading(true) 
      if(props !== undefined && props.props.data !== undefined) {
        setData(props.props.data) 
      } 
      setIsLoading(false)
    },[props.props.data])

    const updateData = index => e => {
    
      let newArray = [...data];
      newArray[index][e.target.name] = e.target.value 
      props.props.setData(newArray)
      
    }

    const ownerOptie = [{label:'Fz',value:0},{label:'Klant',value: 1}]
  
    const leveringdoorOptie = [{label:'Nee',value:0},{label:'Ja',value: 1}]
    const merkOptie = ['ABB','Enphase','Growatt','SolarEdge','Solax'] 
    const columns = useMemo(
      () => [
        {
          accessorKey: 'bouwnr',
          header: "Bouwnr",
        },
        {
          accessorKey: 'blok',
          header: "Blok",
        },
        {
          accessorKey: 'omvormerOwner',
          header: "Owner",
          Cell: ({ cell,row }) =>

            props.props.bewerken_actif === true ?
            <select
              onBlur={updateData(row.index)}
              defaultValue={cell.getValue() !== null ? cell.getValue() === true ? 'Klant': 'Fz': ''}
              className='form-select form-select-sm'
           
              name="omvormerOwner"
             >
              <option> - Kies optie - </option>
              {ownerOptie.map(item => <option value={item.value}>{item.label}</option>)}
            </select>:
            cell.getValue() !== null ? cell.getValue() === true ? 'Klant': 'Fz': ''
        },
        {
          accessorKey: 'levering_door',
          header: "Levering door klant",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
              <select
                onBlur={updateData(row.index)}
                defaultValue={cell.getValue() !== null ? cell.getValue() === true ? 1:0 : ''}
                className='form-select form-select-sm'
         
                name="levering_door"
             >
              <option> - Kies optie - </option>
              {leveringdoorOptie.map(item => <option value={item.value}>{item.label}</option>)}
             </select>:
              cell.getValue() !== null ? cell.getValue() === true ? 'Ja':'Nee' : ''
          
        },
        {
          accessorKey: 'omvromerMerk',
          header: "Merk",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
              <select
              onBlur={updateData(row.index)}
              defaultValue={cell.getValue() !== null ? cell.getValue(): ''}
              className='form-select form-select-sm'
           
              name="omvromerMerk"
             >
              <option> - Kies optie - </option>
              {merkOptie.map(item => <option value={item}>{item}</option>)}
             </select>:
             cell.getValue() ? cell.getValue():''
        },
        {
          accessorKey: 'capaciteit',
          header: "Capaciteit (W)",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
          <input type='text'  style={{textTransform:"uppercase"}} name="capaciteit" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null && cell.getValue() !=='' ? cell.getValue():''} />
          :
          cell.getValue() ? cell.getValue():''
        },
        
        {
          accessorKey: 'levering_datum',
          header: "Levering ddatum",
          Cell: ({ cell,row }) =>
          props.props.bewerken_actif === true ?
          <input type='date' style={{textTransform:"uppercase"}} name="levering_datum" onBlur={updateData(row.index)} className='form-control form-control-sm' defaultValue={cell.getValue() !== null ? cell.getValue() :''} />
          :
          cell.getValue() ? cell.getValue():''
        },
      ],[props.props.bewerken_actif, updateData,])

          
  return (
    isLoading ? <LinearProgress />:
    <MaterialReactTable
        columns={columns}
        data={data ? data: []}
        enablePinning
        enableColumnResizing
        initialState={{ 
          density: 'compact' ,
          columnPinning: { left: ['bouwnr'] },
      }}
      enableRowVirtualization
      muiTableBodyProps={{ sx: { maxWeight: '600px' } }}
     
       />
  )
}

export default Omvormer