import {
    LOAD_MEDEWERKERS_FAIL,
    LOAD_MEDEWERKERS_SUCCESS,
    AANMAKEN_MEDEWERKERS_SUCCESS,
    AANMAKEN_MEDEWERKERS_FAIL,
    UPDATED_MEDEWERKERS_SUCCESS,
    UPDATED_MEDEWERKERS_FAIL

 } from '../actions/types'
 
 const initialState = {
   is_success: '',
   context:[],
   roles:[],
   functies: []
 };





 export default function(state= initialState, action){
     const { type, payload } = action;
 
     switch(type){
         case UPDATED_MEDEWERKERS_SUCCESS:
         case AANMAKEN_MEDEWERKERS_SUCCESS:
            return{
                ...state,
                is_success: payload

            }
         case UPDATED_MEDEWERKERS_FAIL:
         case AANMAKEN_MEDEWERKERS_FAIL:
            return {
                is_success: payload
            }
         case LOAD_MEDEWERKERS_SUCCESS:
             return {
                 ...state,
                 context: payload.context,
                 roles: payload.roles,
                 functies: payload.functies
                 
 
             }
         case LOAD_MEDEWERKERS_FAIL:
             return {
                 ...state,
                 
             }
         default:
             return state
     };
 
 };
