import React, { useEffect, useState, useRef } from 'react'
import Modal from 'react-bootstrap/Modal';
import {getRoles,getFuncties} from '../../actions/admin'
import {medewerker_aanmaken,load_all_medewerkers_permissions} from '../../actions/medewerker'
import { connect } from 'react-redux'
import RoleModalAanmaken from './RoleModalAanmaken';
import FunctieModalAanmaken from './FunctieModalAanmaken';
const MedewerkerModal = (props) => {
    const submitted = useRef(false) 
    const [roles,setRoles] = useState([])
    const [functies,setFuncties] = useState([])
    const [role,setRole] = useState({'role_id':''})
    const [ShowModalCreateRole,setShowModalCreateRole]=useState(false)
    const [ShowModalCreateFunctie,setShowModalCreateFunctie]=useState(false)
    // useEffect(() => {
    //     props.getRoles()
    //     props.getFuncties()
    // },[])
    useEffect(() => {
        props.load_all_medewerkers_permissions()
    },[])

    const pages_array = [
        // Super User
        {'key': 'Super user','codename':'add_permission'},
        // Medewerker
        {'key': 'Gebruiker toevoegen','codename':'add_customuser'},
        {'key': 'Gebruiker bekijken','codename':'view_customuser'},
        {'key': 'Gebruiker bewerken','codename':'change_customuser'},
        {'key': 'Gebruiker verwijderen','codename':'delete_customuser'},
        // Role
        // {'key': 'Role toevoegen','codename':'add_role'},
        // {'key': 'Role bekijken','codename':'view_role'},
        // {'key': 'Role bewerken','codename':'change_role'},
        // {'key': 'Role verwijderen','codename':'delete_role'},
        // Functie
        // {'key': 'Functie toevoegen','codename':'add_functie'},
        // {'key': 'Functie bekijken','codename':"view_functie"},
        // {'key': 'Functie bewerken','codename':"change_functie"},
        // {'key': 'Functie verwijderen','codename':"delete_functie"},
        // Project
        {'key': 'Project toevoegen','codename':"add_project"},
        {'key': 'Project bekijken','codename':"view_project"},
        {'key': 'Project bewerken','codename':"change_project"},
        {'key': 'Project verwijderen','codename':"delete_project"},
        
        //Productie
        // {'key': 'Project toevoegen','codename':"add_project"},
        // {'key': 'Project bekijken','codename':"view_project"},
        // {'key': 'Project bewerken','codename':"change_project"},
        // {'key': 'Project verwijderen','codename':"delete_project"},

        
    
    ]

    useEffect(() => {
        
        if(props.roles !== undefined)
        {
            setRoles(props.roles)
        }
    },[props.roles])


    const onRolechange = (e) => {
        var functies = []
        if(props.functies !== undefined)
        {
            props.functies.map((item) => {
                if(parseInt(e) === item[2])
                {
                    functies.push({'id': item[0],'functie': item[1]})
                }
            })
            medewerkerForm.role = parseInt(e)
        }
        setRole(e)
        setFuncties(functies)
    }

    const [error,setError] = useState(false)
    const validationsMessages = "colorvalidations"
    const array_permissions_page = useRef([])
    const [array_permission_user,setArray_permission_user] = useState([])
    const [page,setPage] = useState()
    const [deleteitem,setdeleteitem] = useState()
    // const [pagespermissions,setPagesPermissions] = useState([])
    const [display,setDisplay] = useState(false)
    const [status,setStatus] = useState(true)
    const medewerker = {
        voornaam: '',
        voorletter: '',
        tussenvoegsel: '',
        achternaam: '',
        geslacht: '',
        geboortDatum: '',
        functie: '',
        phone_no: '',
        password: '',
        re_password: '',
        email: '',
        is_active: 1,
        }
    const [medewerkerForm,setMedewerkerFzform] = useState(medewerker)
    const onChangefunctie = e => setMedewerkerFzform({...medewerkerForm,[e.target.name]: e.target.value})
    
    function onBlur(){
        if(medewerkerForm.password === medewerkerForm.re_password)
        {
            setError(false)
        }else{
            setError(true)
        }
    }
    const submitMedewerker = (e) => {
        e.preventDefault()
        medewerkerForm.permissions = array_permission_user
        if(medewerkerForm.email.trim() && medewerkerForm.password.trim() && medewerkerForm.re_password.trim()){
            if(medewerkerForm.password !== medewerkerForm.re_password)
        {
            setError(true)
            
        }else{
            // setError(false)
            // medewerkerForm.projectid = props.props.projectid ? props.props.projectid:null
            props.medewerker_aanmaken(medewerkerForm)
            setError(false)
            setMedewerkerFzform(medewerker)
            props.props.setShowModalFzMedewerker(false)
        }

     }else{
        setError(true)
       
     }
       
    }
    const statusCLicked = (e) => {
        if(e ==="is_Actif")
        {
            setStatus(true)
            medewerkerForm.is_active = 1
        }else if(e === "is_Inactif")
        {
            setStatus(false)
            medewerkerForm.is_active = 0
        }
        else{
            status.current = true
            medewerkerForm.is_active = 1
        }
    }
    
    const onChangepage = (e) => {
      
        if(e.target.value === '- kies page -')
        {
            
            setPage(e.target.value)
            setDisplay(false)
            // return null

        }else{
            setPage(e.target.value)
            setDisplay(true)
        }
        
    }
    function updateOrAddToObjectArray(arr, key) {
        if(arr.includes(key))
        {
            return arr
        }else{
            arr.push(key)
        }
        // Check if the key already exists in the array
        // const index = arr.findIndex(obj => obj[key] === value);
        
        // // If the key exists, update the object value
        // if (index !== -1) {
        // //   arr[index][key]['page'] = newObject[key]['key'];
        //   arr[index]['permissions'] = newObject['permissions']
        // //   arr[index][value] = newObject[value];
        // } else {
        //   // If the key does not exist, add the new object to the array
        //   arr.push(newObject);
        // }
        
        return arr;
      }

    const add_page_permissions_to_list = () => {
        setArray_permission_user(updateOrAddToObjectArray(array_permission_user,page))  
    }
    const delete_page_permissions_from_list = () => {
        if(array_permission_user.length !==0)
        {
            setArray_permission_user(array_permission_user.filter(item => item !== deleteitem))
        }
        setError(false)
    }
    const display_key_name = (value) => {
        return pages_array.map((item) => item.codename === value ? item.key:null)
    }

    
  return (
    <div>
        <Modal
            show={props.props.showModalFzMedewerker}
            onHide={() => props.props.setShowModalFzMedewerker(false)}
            animation={true}
            centered={true}
            dialogClassName="modal-dialog modal-fullscreen-lg-up"
            aria-labelledby="example-custom-modal-styling-title"
        >
            <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
                Contact personen Factoryzero aanmaken
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className='card card-animate'>
                    <form action="" novalidate>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="voornaam" className="form-label">Voornaam</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="voornaam" value={medewerkerForm.voornaam}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="voornaam" placeholder="Voorbeeld: John" required/>
                                {error ===true ? medewerkerForm.voornaam.trim() ? null: <span className={validationsMessages}>voornaam moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="voorletter" className="form-label">Voorletter</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="voorletter" value={medewerkerForm.voorletter}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="voorletter" placeholder="Voorbeeld: J" required/>
                                {error ===true ? medewerkerForm.voorletter.trim() ? null: <span className={validationsMessages}>voorletter moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="tussenvoegsel" className="form-label">Tussenvoegsel</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="tussenvoegsel" value={medewerkerForm.tussenvoegsel}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="tussenvoegsel" placeholder="Voorbeeld: van" />
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="achternaam" className="form-label">Achternaam</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="achternaam" value={medewerkerForm.achternaam}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="achternaam" placeholder="Voorbeeld: Doe" required/>
                                {error ===true ? medewerkerForm.achternaam.trim() ? null: <span className={validationsMessages}>achternaam moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="geslacht" className="form-label">Geslacht</label>    
                            </div>
                            <div className="col-lg-9">
                            <select 
                                    id="geslacht" 
                                    name="geslacht"
                                    defaultValue={medewerkerForm.geslacht}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangefunctie(e)}
                                >
                                    <option> -- Kies aanhef -- </option>
                                    <option value="Dhr">Dhr</option>
                                    <option value="Mevr">Mevr</option>
                                    </select>
                                {error ===true ? medewerkerForm.geslacht.trim() ? null: <span className={validationsMessages}>Geslacht moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="geslacht" className="form-label">Roles</label>
                            </div>
                            <div className="col-lg-6">
                            <select 
                                    id="role" 
                                    name="role_id"
                                    defaultValue={role.role_id}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onRolechange(e.target.value)}
                                >
                                    
                                    <option> -- Kies role -- </option>
                                    {roles !==undefined && roles !==null && roles.length !== 0 && roles !== true? roles.map((item) => <option key={item[0]} value={item[0]}>{item[1]}</option> ):'geen gegevens gevonden'}
                                    </select>
                                {error ===true  && role.role_id !== undefined? role.role_id.trim() ? null: <span className={validationsMessages}>role moet worden ingevuld</span> :null}
                            </div>
                            <div className="col-lg-3">
                                <button type="button" onClick={() => setShowModalCreateRole(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                                {<RoleModalAanmaken props={{'ShowModalCreateRole':ShowModalCreateRole,'setShowModalCreateRole':setShowModalCreateRole}}/> }
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="geslacht" className="form-label">Functies</label>
                            </div>
                            <div className="col-lg-6">
                                <select 
                                    id="functie" 
                                    name="functie"
                                    defaultValue={medewerkerForm.functie}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangefunctie(e)}
                                >
                                    <option> -- Kies functie -- </option>
                                    {functies !==undefined && functies !==null && functies.length !== 0 ? functies.map((item) => <option key={item.id} value={item.id}>{item.functie}</option> ):'geen gegevens gevonden'}
                                </select>
                            </div>
                            <div className="col-lg-3">
                                <button type="button" onClick={() => setShowModalCreateFunctie(true)} className="btn btn-light btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                                {<FunctieModalAanmaken props={{'ShowModalCreateFunctie':ShowModalCreateFunctie,'setShowModalCreateFunctie':setShowModalCreateFunctie}}/> }
                            </div>

                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="phone_no" className="form-label">Telefoon</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="phone_no" value={medewerkerForm.phone_no}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="phone_no" placeholder="Voorbeeld: +31687621114" required/>
                                {error ===true ? medewerkerForm.phone_no.trim() ? null: <span className={validationsMessages}>telefoon moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="geboortDatum" className="form-label">Geboortdatum</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="date" name="geboortDatum" value={medewerkerForm.geboortDatum}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="geboortDatum" placeholder="Voorbeeld: 01-01-2000" required/>
                                {error ===true ? medewerkerForm.geboortDatum !== '' ? null: <span className={validationsMessages}>telefoon moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="email" className="form-label">Email</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="email" name="email" value={medewerkerForm.email}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="email" placeholder="Voorbeeld: example@factoryzero.nl" required/>
                                {error ===true ? medewerkerForm.email.trim() ? null: <span className={validationsMessages}>email moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="email" className="form-label">Status</label>    
                            </div>
                            <div className="col-lg-9">
                                <div className='row m-3'>
                                    <div className='col-lg-6'>
                                        <div class="form-check form-check-outline form-check-success mb-3">
                                            <input onClick={(e) => statusCLicked(e.target.attributes[0].value)} name="is_Actif" defaultValue={medewerkerForm.is_active} class="form-check-input" type="checkbox" id="formCheck15" checked={status !== true ? false:true}/>
                                            <label class="form-check-label" for="formCheck15">
                                                Actif
                                            </label>
                                        </div>
                                    </div>
                                    <div className='col-lg-6'>
                                        <div class="form-check form-check-outline form-check-danger mb-3">
                                            <input onClick={(e) => statusCLicked(e.target.attributes[0].value)} name="is_Inactif" class="form-check-input" type="checkbox" id="formCheck17" checked={status !== true ? true:false} />
                                            <label class="form-check-label" for="formCheck17">
                                                Non-actif
                                            </label>
                                        </div>
                                    </div>
                                    
                                </div>
                            </div> 
                        </div>
                        <div className="col-lg-3">
                                <label for="geslacht" className="form-label">Rechten</label>
                            </div>
                        <div className="row m-3">
                            <div className="col-lg-6">
                            <select 
                                value = {page}
                                onChange={(e) => onChangepage(e)}
                                className="form-select form-select-sm"
                                // size="3" 
                                aria-label="size 3 select example">
                                <option> - Kies rechten - </option>
                                { pages_array.map((item) => <option value={item.codename}>{item.key}</option> )}
                            </select>
                            </div>
                            <div className="col-lg-3">
                                <button type="button" onClick={() => add_page_permissions_to_list()} className="btn btn-light btn-icon waves-effect waves-light "><i class="ri-add-fill"></i></button>
                            </div>
                            <div className="col-lg-6">
                                { array_permission_user.length !== 0 ?<>
                                    <select 
                                        value = {deleteitem}
                                        onChange={(e) => setdeleteitem(e.target.value)}
                                        className="form-select form-select-sm"
                                        // size="3" 
                                        aria-label="size 3 select example">
                                       
                                        { array_permission_user.map((item) => <option value={item}>{ display_key_name(item)}</option> )}
                                    </select>
                                    <div className="col-lg-3">
                                        <button type="button" onClick={() => delete_page_permissions_from_list()} className="btn btn-light btn-icon waves-effect waves-light "><i class="bx bx-x "></i></button>
                                    </div>
                                </> :null}
                            </div>

                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="password" className="form-label">Wachtwoord</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="password" onBlur={() => onBlur()} name="password" value={medewerkerForm.password}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="password" autoComplete={medewerkerForm.password} required/>
                                {error ===true ? medewerkerForm.password.trim() ? null: <span className={validationsMessages}>wachtwoord moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label for="re_password" className="form-label">wachtwoord bevestigen</label>    
                            </div>
                            <div className="col-lg-9">
                                <input type="password" onBlur={() => onBlur()} name="re_password" value={medewerkerForm.re_password}   onChange={(e) => onChangefunctie(e)} className="form-control form-control-sm" id="re_password" autoComplete={medewerkerForm.re_password} required/>
                                {error ===true ? medewerkerForm.re_password.trim() ? null: <span className={validationsMessages}>wachtwoord bewestigen moet worden ingevuld</span> :null}
                            </div>
                        </div>

                        <div class="text-end">
                            <div class="row m-3">
                                <div className='col'>
                                    <button type="button" onClick={(e) => submitMedewerker(e)} className="btn btn-danger btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                                </div>
                                <div className='col'>
                                    <button type="button" onClick={() => props.props.setShowModalFzMedewerker(false)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                                </div>
                            </div> 
                        </div>
                    </form>
                </div>
                
            </Modal.Body>
        </Modal>
    </div>
  )
}

const getContext = state => ({
    roles: state.admin.roles,
    functies: state.admin.functies
})
export default connect(getContext,{getRoles,getFuncties,medewerker_aanmaken,load_all_medewerkers_permissions})(MedewerkerModal)