import React , {useRef,useState}from 'react'
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { connect } from 'react-redux'
import {newPassword} from '../actions/auth'
import CSRFToken from './CSRFToken'
import { Toast } from 'primereact/toast';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const PasswordReset = ({newPassword,message,is_success}) => {
    ///////////////////////////////////////////
    const [formData,setFormData] = useState({
        oldPassword: '',
        new_Password: '',
        confirm_Password:''
      })
      const {oldPassword,new_Password,confirm_Password} = formData
      const onChange = e => setFormData({...formData, [e.target.name]: e.target.value})
      const [passwordUpdated,setpasswordUpdated] = useState(false)
      const [status,setStatus] = useState(null)
      
      const onSubmit = e => {
        e.preventDefault();
        newPassword(JSON.stringify(formData))
        setpasswordUpdated(true);
        
      }
       
  return (
    <div>
      <>
        <form className="row g-3 needs-validation" novalidate>
          <div class="row g-2">
            <div class="col-lg-4">
              <div>
                <label for="oldpasswordInput" class="form-label">Old Password*</label>
                <input type="password" name="oldPassword" value={oldPassword} onChange={e => onChange(e)}  class="form-control" id="oldpasswordInput" placeholder="Enter current password" required/>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="has-validation">
                <label for="newpasswordInput" class="form-label">New Password*</label>
                <input type="password" minLength={8} name="new_Password" value={new_Password} onChange={e => onChange(e)} class="form-control" id="newpasswordInput" placeholder="Enter new password" required/>
                {/* <div class="invalid-feedback">
                    Please select a valid state.
                </div> */}
              </div>
            </div>
            <div class="col-lg-4">
              <div>
                  <label for="confirmpasswordInput" class="form-label">Confirm Password*</label>
                  <input type="password" class="form-control" id="confirmpasswordInput" name="confirm_Password" value={confirm_Password} onChange={e => onChange(e)} placeholder="Confirm password" required/>
              </div>
            </div>
            <div class="col-lg-12">
                <div class="mb-3">
                    <a href="javascript:void(0);" class="link-primary text-decoration-underline">Forgot Password ?</a>
                </div>
            </div>
            <div class="col-lg-12">
                <div class="text-end">
                    <button type="submit" onClick={onSubmit} class="btn btn-success">Change Password</button>
                </div>
            </div>
          </div>
        </form>
        </>
      
    </div>
  )
}
const mapStateToProps = state => ({
    is_success: state.auth.is_success,
    message: state.auth.message
  })
export default connect(mapStateToProps,{newPassword})(PasswordReset)
