import React, { useState } from 'react'
import Modal from 'react-bootstrap/Modal';
import {aanmaken_klantMedewerker} from '../../actions/project'
import { connect } from 'react-redux'

const KlantMedewerkerModal = (props) => {
    const klantMedewerker = {
        name_medewerker:'',
        achternaam_medewerker:'',
        phone: '',
        functie_medewerker: '',
        klantID_id: '',
        projectid: ''
    }
    const validationsMessages = "colorvalidations"
    const [error,setError] = useState(false)
    const [klantMedewerkerForm,setklantMedewerkerForm] = useState(klantMedewerker)
    const onChangeAanemer = e => setklantMedewerkerForm({...klantMedewerkerForm,[e.target.name]: e.target.value})

    const klantID = props.props.klantId ? props.props.klantId: null
    
    const submitKlantAanemer = (e) => {
        e.preventDefault()
        klantMedewerkerForm.klantID_id = klantID
        klantMedewerkerForm.projectid = props.props.projectid ? props.props.projectid:null
       
        if(klantMedewerkerForm.name_medewerker.trim() && klantMedewerkerForm.achternaam_medewerker.trim() &&
        klantMedewerkerForm.functie_medewerker.trim() && klantMedewerkerForm.phone.trim())
        {
            console.log(klantMedewerkerForm)
            props.aanmaken_klantMedewerker(klantMedewerkerForm)
            setklantMedewerkerForm(klantMedewerker)
            setError(false)
            props.props.setShowModalAanemer(false)
            
        }else{
            console.log('it doesnt work')
            setError(true)
        }
        
    }
  return (
   <>
     <Modal
            show={props.props.showModalAanemer}
            onHide={() => props.props.setShowModalAanemer(false)}
            animation={true}
            centered={true}
            dialogClassName="modal-90w"
            aria-labelledby="example-custom-modal-styling-title"
        >
            <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
                Contact personen aanemer aanmaken
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className='card card-animate'>
                    <form action="">
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label htmlFor="name_medewerker" className="form-label">Voornaam</label>
                                
                            </div>
                            <div className="col-lg-9">
                                <input type="text" name="name_medewerker" value={klantMedewerkerForm.name_medewerker}   onChange={(e) => onChangeAanemer(e)} className="form-control form-control-sm" id="nameInput" placeholder="Voorbeeld: John" required/>
                                    {error ===true ? klantMedewerkerForm.name_medewerker.trim() ? null: <span className={validationsMessages}>voornaam moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className="row m-3">
                            <div className="col-lg-3">
                                <label htmlFor="achternaam_medewerker" className="form-label">Achternaam</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" name="achternaam_medewerker" value={klantMedewerkerForm.achternaam_medewerker} onChange={(e) => onChangeAanemer(e)} class="form-control form-control-sm" id="achternaam_medewerker" placeholder="Voorbeeld: Doe" required/>
                                {error ===true ? klantMedewerkerForm.achternaam_medewerker.trim() ? null: <span className={validationsMessages}>achternaam moet worden ingevuld</span> :null}
                            </div>
                        </div>
                
                        <div className='row m-3'>
                            <div className="col-lg-3">
                                <label htmlFor="functie_medewerker" class="form-label">Kies Funcite</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="functie_medewerker" 
                                    name="functie_medewerker"
                                    defaultValue={klantMedewerkerForm.functie_medewerker}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeAanemer(e)}
                                >
                                    <option> - </option>
                                    <option value="Projectleider">Projectleider</option>
                                    <option value="Werk-voorbereider">Werk-voorbereider</option> 
                                    <option value="Uitvoerder">Uitvoerder</option>
                                </select>
                                {error ===true ? klantMedewerkerForm.functie_medewerker.trim() ? null: <span className={validationsMessages}>Functie moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        
                        <div class="row m-3">
                            <div class="col-lg-3">
                                <label htmlFor="phone" class="form-label">Phone</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="number" name='phone' value={klantMedewerkerForm.phone} onChange={(e) => onChangeAanemer(e)}  class="form-control form-control-sm" id="phone" placeholder="+31 698478321" />
                                {error ===true ? klantMedewerkerForm.phone.trim() ? null: <span className={validationsMessages}>Phone moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div class="text-end">
                            <div class="row m-3">
                                <div className='col'>
                                    <button type="button" onClick={(e) => submitKlantAanemer(e)} className="btn btn-danger btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                                </div>
                                <div className='col'>
                                    <button type="button" onClick={() => props.props.setShowModalAanemer(false)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                                </div>
                            </div>   
                        </div>
                    </form>
                </div>
                
            </Modal.Body>
        </Modal>
   </>
  )
}

export default connect(null,{aanmaken_klantMedewerker})(KlantMedewerkerModal)
