import axios from  'axios'
import Cookies from 'js-cookie'
import { load_project } from './project'
import {
    LOAD_ROLES_SUCCESS,
    LOAD_ROLES_FAIL,
    AANMAKEN_ROLES_SUCCESS,
    AANMAKEN_ROLES_FAIL,
    LOAD_FUNCTIES_SUCCESS,
    LOAD_FUNCTIES_FAIL,
    AANMAKEN_FUNCTIES_SUCCESS,
    AANMAKEN_FUNCTIES_FAIL,

} from './types'
axios.defaults.withCredentials = true

// ROLES
export const getRoles = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
            // 'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/users/role`,config);
        
        if (res.data.roles){
            // console.log(res.data.roles)
            dispatch({
                type: LOAD_ROLES_SUCCESS,
                payload : res.data
            })
        }
        else{
            dispatch({
                type: LOAD_ROLES_FAIL,
                // payload : res.data
            })

        }

    }catch(err){
        dispatch({
            type: LOAD_ROLES_FAIL,
        });
    }
}

export const create_role = (formData) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }

    };
    const body = formData

    try{
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/users/create-role`,body,config)
        if(res.data.success){
            dispatch({
                type: AANMAKEN_ROLES_SUCCESS,
                payload: res.data.success
            })
            // dispatch(load_medewerkers())
            dispatch(load_project())
            // dispatch(getRoles())
        }else{
            dispatch({
                type: AANMAKEN_ROLES_FAIL,
                payload: res.data.error
            })
            // dispatch(load_medewerkers)
        }
    }
    catch (err) {
        dispatch({
            type: AANMAKEN_ROLES_FAIL
        });
        // dispatch(load_medewerkers)
    }
}
